import React, { Component } from "react";
// material imports
import { Button, Dialog, DialogContent, Grid } from "@material-ui/core";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { actionGetMinAmount } from "../../actions/ProductAction";
import {
  RevenueData,
  ClickedButton,
  PurchaseCartItems,
} from "../../config/amplitude/SnacksAmplitude";
import {
  PAGENAME,
  BUTTONNAME,
} from "../../config/amplitude/Taxonomy.constants";

class OrderSummary extends Component {
  constructor(props) {
    super(props);
    this.state = {
      cartItems: this.props.isThankYou
        ? sessionStorage.getItem("_sc_re")
          ? JSON.parse(sessionStorage.getItem("_sc_re")).items
          : []
        : JSON.parse(sessionStorage.getItem("cartItems"))
        ? JSON.parse(sessionStorage.getItem("cartItems"))
        : [],
      total: 0,
      tax: 0,
      shippingCurrency: "$",
      DialougeBoxOpen: false,
      taxFlag: "",
      multipackDetail: "",
      coupon: sessionStorage.getItem("coupon") || "",
      discount:
        !!sessionStorage.getItem("coupon") &&
        !!sessionStorage.getItem("totalDiscount")
          ? sessionStorage.getItem("totalDiscount")
          : sessionStorage.getItem("discount") || 0,

      shippingCost: sessionStorage.getItem("shippingSkuCost")
        ? JSON.parse(sessionStorage.getItem("shippingSkuCost"))
        : 0.0,
    };
  }

  componentDidMount() {
    this.props.actionGetMinAmount("", "", "", "").then(() => {
      if (
        !!this.props.minPrice &&
        Object.keys(this.props.minPrice).length > 0
      ) {
        this.setState({ taxFlag: this.props.minPrice.taxFlag });
      }
    });
    if (!this.props.isThankYou) {
      this.totalCalculation(null);
    } else {
      let payData = JSON.parse(sessionStorage.getItem("_sc_re"));
      if (payData) {
        this.setState(
          {
            shippingCost: payData.shipping ? parseFloat(payData.shipping) : 0,
            shippingCurrency: payData.currencySymbol,
          },
          () => this.totalCalculation(null)
        );
      }
    }
    this.sendAmplitudeRequest();
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.someValue !== this.props.summuryTaxData) {
      const summuryTaxData = !!this.props.summuryTaxData
        ? !!this.props.summuryTaxData.data
          ? this.props.summuryTaxData.data.totalTaxAmount
          : null
        : null;
      this.totalCalculation(summuryTaxData);
    }
  }

  sendAmplitudeRequest = () => {
    const { cartItems } = this.state;

    cartItems.map((cartItem) => {
      if (!this.props.isThankYou) {
        RevenueData(
          cartItem.productId,
          parseFloat(cartItem.price),
          cartItem.quantity
        );
      } else {
        RevenueData(
          cartItem.productId,
          parseFloat(cartItem.price),
          cartItem.qty
        );
        PurchaseCartItems(this.props.OrderID, cartItem);
      }
    });
  };

  totalCalculation(summuryTaxData) {
    let total = 0;
    this.state.cartItems.map((cartItem, index) => {
      if (this.props.isThankYou) {
        total = total + cartItem.price * cartItem.qty;
      } else {
        total = total + cartItem.price * cartItem.quantity;
      }
      return true;
    });

    let tax = !!summuryTaxData ? summuryTaxData : 0;

    this.setState({ tax, total });
    if (!this.props.isThankYou) {
      //do nothing
    } else {
      if (this.props.totalUpdate)
        if (sessionStorage.getItem("tax") !== null)
          this.props.totalUpdate(
            total + parseFloat(sessionStorage.getItem("tax"))
          );
        else this.props.totalUpdate(total);
    }
  }

  renderOrderItem = (cartItem, index) => {
    let totalPrice = 0;
    /** total price calculation based on product count group */

    if (!this.props.isThankYou) {
      totalPrice = cartItem.price * cartItem.quantity;
    } else {
      totalPrice = cartItem.price * cartItem.qty;
    }
    let productDesc = "";

    if (cartItem.multipack) {
      productDesc = `${cartItem.productTitle || cartItem.shortDescription}`;
    } else {
      productDesc =
        cartItem.productDescription ||
        cartItem.shortDescription ||
        cartItem.title;
    }

    return (
      <Grid key={index}>
        <Grid
          container
          spacing={1}
          align="start"
          className="subtotal-row"
          key={"cartItem" + index}
        >
          <Grid item sm={3} xs={2} className="summary-img-order">
            <div className="summary-img-checkout">
              <img
                alt={`Product ${
                  !this.props.isThankYou ? productDesc : cartItem.name
                }`}
                src={cartItem.image}
              />
            </div>
          </Grid>
          <Grid item sm={9} xs={10}>
            <div className="item-desc">
              {!this.props.isThankYou && productDesc !== undefined
                ? productDesc
                : cartItem.name}
            </div>
            <div className="item-qty">
              Quantity:{" "}
              {!this.props.isThankYou ? cartItem.quantity : cartItem.qty}
            </div>
          </Grid>
        </Grid>
        <div>
          <div className="divider" />
        </div>
      </Grid>
    );
  };

  showTotal = () => {
    const { discount, taxFlag, total } = this.state;
    const shippingCost = sessionStorage.getItem("shippingSkuCost")
      ? JSON.parse(sessionStorage.getItem("shippingSkuCost"))
      : 0.0;
    let tax =
      taxFlag === "true" && sessionStorage.getItem("tax")
        ? parseFloat(sessionStorage.getItem("tax"))
        : 0.0;

    let finalTotal = total + shippingCost;

    if (discount && discount > 0) {
      finalTotal = total - discount + shippingCost;
    }

    if (taxFlag && tax > 0) {
      finalTotal = total + tax + shippingCost;
    }

    if (discount && taxFlag) {
      finalTotal = total - discount + tax + shippingCost;
    }

    sessionStorage.setItem("totalAmount", total);
    sessionStorage.setItem("total", total);
    sessionStorage.setItem("finalTotal", parseFloat(finalTotal).toFixed(2));

    return (
      <div className="products-total">
        <div className="products-total-inner">
          <Grid
            container
            spacing={0}
            justify="space-between"
            alignItems="center"
          >
            <Grid item className="total">
              <div className="h3">Total Amount</div>
            </Grid>
            <Grid item className="total-amount">
              <div className="h3">${parseFloat(finalTotal).toFixed(2)}</div>
            </Grid>
          </Grid>
        </div>
      </div>
    );
  };

  renderMultipackDetail = (cartItem) => {
    return (
      <Grid item xs={12} sm={12} md={12} className="multipack-cart-detail">
        <div className="multipack-cart-detail-inner">
          <Button
            id={`closeMultipackDetail`}
            aria-label={`Close Multipack Detail`}
            className="multipack-cart-detail-close"
            onClick={() =>
              this.setState({
                multipackDetail: "",
              })
            }
          >
            <i className="fa fa-times"></i>
          </Button>
          {cartItem.map((multipackItem, index) => {
            return (
              <Grid container key={index}>
                <Grid item className="multipack-cart-detail-img">
                  <img
                    alt={`multipack-${multipackItem.name}-${multipackItem.productId}`}
                    src={multipackItem.image}
                    role="presentation"
                  />
                </Grid>
                <Grid item xs>
                  <div className="title-product">{multipackItem.name}</div>
                  <div className="count">Quantity: {multipackItem.qty}</div>
                </Grid>
              </Grid>
            );
          })}
        </div>
      </Grid>
    );
  };

  render() {
    const {
      isThankYou,
      onlyTotal,
      checkoutText,
      disableCheckout,
      onCheckoutTap,
      multiPackOrder,
      guestCheckout,
      isShippingFeeApiLoading,
    } = this.props;

    const {
      discount,
      taxFlag,
      coupon,
      cartItems,
      DialougeBoxOpen,
      total,
    } = this.state;
    let dis =
      !!sessionStorage.getItem("coupon") &&
      !!sessionStorage.getItem("totalDiscount")
        ? sessionStorage.getItem("totalDiscount")
        : sessionStorage.getItem("discount") || 0;
    if (coupon.length > 0 && discount !== dis) {
      this.setState({
        discount: dis,
      });
    }

    return (
      <div className="summary-right" id="summary-sec">
        <div className={!isThankYou ? "card-summary-container" : ""}>
          {!isThankYou && (
            <div>
              <div className="card-summary-header">
                <h3>{onlyTotal ? "YOUR ORDER" : "ORDER SUMMARY"}</h3>
              </div>
              <div className="divider" />
            </div>
          )}
          {!onlyTotal && (
            <div className={!isThankYou ? "summary-list" : "summary-list-grid"}>
              {!isThankYou ? (
                cartItems.map((cartItem, index) =>
                  this.renderOrderItem(cartItem, index)
                )
              ) : (
                <Grid container spacing={1} xs={12}>
                  {!!multiPackOrder &&
                    multiPackOrder.length > 0 &&
                    multiPackOrder.map((multiPackItem, index) => (
                      <Grid item xs={4}>
                        <Grid key={index}>
                          <Grid
                            container
                            spacing={1}
                            align="start"
                            className="subtotal-row"
                            key={"cartItem" + index}
                          >
                            <Grid
                              item
                              sm={3}
                              xs={2}
                              className="summary-img-order"
                            >
                              <div className="summary-img-checkout">
                                <img
                                  src={multiPackItem.image}
                                  alt={`${multiPackItem.productTitle}- ${index}`}
                                />
                              </div>
                            </Grid>
                            <Grid item sm={9} xs={10}>
                              <div className="item-desc">
                                {multiPackItem.productTitle}
                              </div>
                              <div className="item-qty">
                                <span>Quantity: {multiPackItem.quantity}</span>
                                <Button
                                  id="multipackdetails"
                                  aria-label="multipackdetails"
                                  className="multipack-view-detail"
                                  onClick={() =>
                                    this.setState({
                                      multipackDetail:
                                        multiPackItem.multipackId,
                                    })
                                  }
                                >
                                  View Detail
                                </Button>
                              </div>
                            </Grid>
                          </Grid>
                          <div>
                            <div className="divider" />
                          </div>
                        </Grid>
                        {multiPackItem.multipackList.length > 0 &&
                          this.state.multipackDetail ===
                            multiPackItem.multipackId &&
                          this.renderMultipackDetail(
                            multiPackItem.multipackList
                          )}
                      </Grid>
                    ))}

                  {cartItems.map((cartItem, index) => (
                    <Grid item xs={4} key={index}>
                      {this.renderOrderItem(cartItem, index)}
                    </Grid>
                  ))}
                </Grid>
              )}
            </div>
          )}
          {onlyTotal
            ? !isShippingFeeApiLoading && this.showTotal()
            : !isThankYou && (
                <div>
                  <div className="card-summary-bottom-row">
                    <Grid container spacing={0} alignItems="center">
                      <Grid item sm={10} xs={10} className="summary-subtotal">
                        <div className="h3">Subtotal</div>
                      </Grid>
                      <Grid item sm={2} xs={2}>
                        <div className="summary-subtotal-value h3">
                          ${parseFloat(total).toFixed(2)}
                        </div>
                      </Grid>
                    </Grid>
                  </div>

                  {coupon.length > 0 && (
                    <div className="card-summary-bottom-row">
                      <Grid container spacing={0} alignItems="center">
                        <Grid item sm={9} xs={9} className="summary-shipping">
                          Promo Discount Applied
                        </Grid>
                        <Grid item sm={3} xs={3}>
                          <div className="summary-shipping-value">
                            (-${parseFloat(discount).toFixed(2)})
                          </div>
                        </Grid>
                      </Grid>
                    </div>
                  )}

                  <div className="card-summary-bottom-row">
                    <Grid container spacing={0} alignItems="center">
                      <Grid item sm={10} xs={10} className="summary-tax">
                        Tax{" "}
                        <span className="summary-tax">
                          (
                          {sessionStorage.getItem("taxZipCode")
                            ? sessionStorage.getItem("taxZipCode")
                            : sessionStorage.getItem("Zipcode")}
                          )
                        </span>
                      </Grid>
                      <Grid item sm={2} xs={2}>
                        <div className="summary-shipping-value">
                          {taxFlag === "true"
                            ? "$" +
                              (sessionStorage.getItem("tax") == undefined
                                ? "0.00"
                                : parseFloat(
                                    sessionStorage.getItem("tax")
                                  ).toFixed(2))
                            : "FREE"}
                        </div>
                      </Grid>
                    </Grid>
                  </div>
                  {/* shipping charges */}
                  {!isShippingFeeApiLoading && (
                    <div className="card-summary-bottom-row">
                      <Grid container spacing={0} alignItems="center">
                        <Grid item sm={10} xs={10} className="summary-shipping">
                          {sessionStorage.getItem("shippingSkuName")}
                        </Grid>
                        <Grid item sm={2} xs={2}>
                          <div className="summary-shipping-value">
                            {(sessionStorage.getItem("shippingSkuCost") ||
                              0) === 0
                              ? "FREE"
                              : `$${
                                  sessionStorage.getItem("shippingSkuCost") || 0
                                }`}
                          </div>
                        </Grid>
                      </Grid>
                    </div>
                  )}
                  <div>
                    <div className="divider" />
                  </div>

                  {!isShippingFeeApiLoading && this.showTotal()}
                  {checkoutText && (
                    <div className="button-group">
                      <Button
                        disabled={disableCheckout}
                        onClick={() => {
                          ClickedButton(
                            BUTTONNAME.CONTINUETOPAYMENT,
                            guestCheckout
                              ? PAGENAME.GUESTCHECKOUT
                              : PAGENAME.CHECKOUT
                          );
                          onCheckoutTap();
                        }}
                        className="btn-primary show-web"
                        id="ContinueToPayment"
                        aria-label={checkoutText}
                      >
                        {checkoutText}
                      </Button>
                    </div>
                  )}
                </div>
              )}
        </div>
        <Dialog
          open={DialougeBoxOpen}
          onClose={() => {
            this.setState({ DialougeBoxOpen: false });
          }}
          aria-labelledby="responsive-dialog-title"
          maxWidth={false}
          classes={{
            paper: "modal-container",
            root: "modal-stack-top",
          }}
        >
          <DialogContent>
            <div style={{ textAlign: "center" }}>
              <h2>Something went Wrong</h2>
            </div>
          </DialogContent>
          <Button
            id="errorCatch"
            aria-label="error Catch"
            onClick={() => {
              this.setState({ DialougeBoxOpen: false });
            }}
          >
            Close
          </Button>
        </Dialog>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    minPrice: state.reducer.minAmount,
  };
};

export default connect(mapStateToProps, { actionGetMinAmount })(
  withRouter(OrderSummary)
);
