import OktaApiUtil from "../config/OktaApiUtil";
import qs from "query-string";
/* 
import { useOktaAuth } from '@okta/okta-react';
import React, { useEffect } from 'react'; */

const LoginCallback = () => {
  let params = qs.parse(window.location.search);
  let code = params ? params.code : "";
  if (code) {
    // localStorage.setItem("code", code);
    OktaApiUtil.getToken(code).then((res) => {
      if (
        sessionStorage.getItem("prevUrl") &&
        (sessionStorage.getItem("prevUrl") == "/checkout" ||
          sessionStorage.getItem("prevUrl") == "/cart-preview")
      ) {
        sessionStorage.removeItem("isPayment");
        window.location.assign("/checkout");
      } else if (
        sessionStorage.getItem("prevUrl") &&
        sessionStorage.getItem("prevUrl") == "/cart"
      ) {
        window.location.assign("/cart");
      } else {
        window.location.assign("/home");
      }
    });
  } else {
    window.location.href = "/home";
  }
  return "";
};
export default LoginCallback;
