import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import { Grid, TextField, InputAdornment } from "@material-ui/core";
import NavigateBeforeIcon from "@material-ui/icons/NavigateBefore";
import SearchIcon from "@material-ui/icons/Search";
import "./multipack-header.scss";

export default class MultipackHeader extends Component {
  state = {
    globalSearch: "",
    searchFocused: false,
  };

  componentDidMount() {
    window.addEventListener(
      "scroll",
      document.getElementById("multipackHeader")
        ? this.resizeHeaderOnScroll
        : ""
    );
  }

  resizeHeaderOnScroll = () => {
    let distanceY = document.documentElement.scrollTop;
    let shrinkSmall = 80;
    let headerEl = document.getElementById("multipackHeader");

    if (headerEl) {
      if (distanceY > shrinkSmall) {
        headerEl.classList.add("smaller");
        document.getElementById("multipackCart").classList.add("fixed");
      } else {
        headerEl.classList.remove("smaller");
        document.getElementById("multipackCart").classList.remove("fixed");
      }
    }
  };

  multipackSearch = (e) => {
    const term = e.target.value.trim();
    this.setState({ globalSearch: term });
  };

  searchTriggered = () => {
    const { globalSearch } = this.state;
    this.props.searchAPI(globalSearch);
  };

  searchFocused = () => {
    this.setState({
      searchFocused: true,
    });
  };

  render() {
    const {
      config: {
        image,
        mixCountName,
        price,
        longDescription,
        heading,
        currency,
      },
    } = this.props;

    return (
      <Grid
        container
        justify="center"
        className="multipack-header sticky-head"
        id="multipackHeader"
      >
        <Grid item sm={12}>
          <NavLink
            className="multipack-back"
            to="/productlist"
            aria-label="Back to All Products"
            onClick={() => {
              sessionStorage.removeItem("multipackCartItems");
              sessionStorage.removeItem("multipackEditId");
            }}
          >
            <NavigateBeforeIcon />
            <span>Back to All Products</span>
          </NavLink>
        </Grid>
        <Grid item className="multipack-header-img">
          <div className="price-wrapper">
            <span className="price">{`${currency}${price}`}</span>
            <span className="count">{mixCountName}</span>
          </div>
          <div className="thumbnail">
            <img alt="Multipack" src={image} />
          </div>
        </Grid>
        <Grid item xs className="multipack-header-content">
          {heading && <h1>{heading}</h1>}
          {longDescription && <p>{longDescription}</p>}
          <TextField
            className={`search-bar ${this.state.searchFocused && "focused"}`}
            id="multipackSearch"
            fullWidth
            label="Search"
            aria-label="search"
            placeholder="Search"
            type="text"
            onChange={(e) => this.multipackSearch(e)}
            onBlur={this.searchTriggered}
            onKeyDown={(e) => e.key === "Enter" && this.searchTriggered()}
            onFocus={this.searchFocused}
            value={this.state.globalSearch}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start" className="search-icon">
                  <SearchIcon />
                </InputAdornment>
              ),
            }}
          />
        </Grid>
      </Grid>
    );
  }
}
