import React, { Component } from "react";
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  CircularProgress,
  Grid,
} from "@material-ui/core";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ProductDetailDialog from "../../components/pages/productDetail/ProductDetailDialog";
import MultipackTout from "../multipackTout/MultipackTout";
import "./multipack-list.scss";

export default class MultipackList extends Component {
  state = {
    productInfo: [],
    productDetailIndex: "",
    selectedItems: [],
    showProductDetail: false,
    isExpanded: this.props.tabName,
  };

  productDetail = (productInfo, index) => {
    this.setState({
      showProductDetail: true,
      productInfo: productInfo,
      productDetailIndex: index,
    });
  };

  selectedCartItems = (selectedItems) => {
    this.setState({ selectedItems });
  };

  updateProductDetailStatus = (status) => {
    this.setState({ showProductDetail: status });
  };

  renderProductList = (contentList, multipackLimit) => {
    const { warningMsgFunc, mainHeading } = this.props;
    return contentList.map((productList, index) => (
      <MultipackTout
        productList={productList}
        index={index}
        key={index}
        detailedInfo={this.productDetail}
        multipackLimit={multipackLimit}
        warningMsgFunc={warningMsgFunc}
        mainHeading={mainHeading}
      />
    ));
  };

  handleChange = (panel) => (event, isExpanded) => {
    this.setState({ isExpanded: isExpanded ? panel : false });
  };

  render() {
    const {
      showProductDetail,
      selectedItems,
      productInfo,
      isExpanded,
    } = this.state;
    const {
      mainHeading,
      contentList,
      multipackLimit,
      isContentLoaded,
      warningMsgFunc,
      tabName,
    } = this.props;
    const keyId = mainHeading.replace(/\s+/g, "-").toLowerCase();

    return (
      <Accordion
        className="multipack-accordion-wrapper"
        expanded={isExpanded === tabName}
        onChange={this.handleChange(tabName)}
      >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls={`${keyId}-content`}
          id={keyId}
          classes={{
            root: "multipack-accordion-header",
            content: "multipack-accordion-content",
            expandIcon: "expandIcon",
          }}
        >
          {mainHeading}
        </AccordionSummary>
        <AccordionDetails className="multipack-accordion-body">
          {!isContentLoaded && (
            <Grid
              container
              justify="center"
              style={{ minHeight: "100px", textAlign: "center" }}
            >
              <Grid item>
                <CircularProgress />
              </Grid>
            </Grid>
          )}
          {isContentLoaded && contentList && contentList.length === 0 && (
            <div className="no-products">No Products</div>
          )}
          {isContentLoaded &&
            contentList &&
            contentList.length > 0 &&
            this.renderProductList(contentList, multipackLimit)}

          {showProductDetail && (
            <ProductDetailDialog
              status={showProductDetail}
              statusUpdate={this.updateProductDetailStatus}
              productData={productInfo}
              selectedItems={selectedItems}
              warningMsgFunc={warningMsgFunc}
              selectedCartItems={this.selectedCartItems}
              isMultipack
              multipackLimit={multipackLimit}
            />
          )}
        </AccordionDetails>
      </Accordion>
    );
  }
}
