import React, { Component } from "react";
import {
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  RadioGroup,
  Radio,
  FormControlLabel,
} from "@material-ui/core";

class AddressSuggestionDialog extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedAddress: 0,
    };
  }

  getAdressLine(e, index) {
    var city = "";
    var state = "";
    var country = "";
    var zipcode = "";
    var addressLine = !!e.AddressLine ? e.AddressLine : "";
    if (Array.isArray(e.AddressLine)) {
      addressLine =
        (!!e.AddressLine[0] ? e.AddressLine[0] : "") +
        (!!e.AddressLine[1] ? ", " + e.AddressLine[1] : "");
    }
    if (!!e.CountryCode) {
      country = e.CountryCode;
    }
    if (!!e.PoliticalDivision1) {
      state = e.PoliticalDivision1;
    }
    if (!!e.PoliticalDivision2) {
      city = e.PoliticalDivision2;
    }
    if (!!e.PostcodePrimaryLow) {
      zipcode = e.PostcodePrimaryLow;
    }
    const addressLabel =
      (!!addressLine ? addressLine : "") +
      (!!city ? " " + city : "") +
      (!!state ? " " + state : "") +
      (!!country ? ", " + country : "") +
      (!!zipcode ? " " + zipcode : "");
    return (
      <FormControlLabel
        onChange={(e) => this.setState({ selectedAddress: index })}
        value={index}
        key={`${"address1" + index}`}
        control={<Radio />}
        label={addressLabel}
      />
    );
  }

  manageSuggestedAddress() {
    const addressData = !!this.props.addressVerifyData[
      this.state.selectedAddress
    ]
      ? this.props.addressVerifyData[this.state.selectedAddress]
          .AddressKeyFormat
      : "";
    var intialAddress = this.props.initialValues;
    if (this.props.isAddress == "billing") {
      if (Array.isArray(addressData.AddressLine)) {
        intialAddress.billingAddressline1 = addressData.AddressLine[0];
        intialAddress.billingAddressline2 = addressData.AddressLine[1];
      } else {
        intialAddress.billingAddressline1 = addressData.AddressLine;
        intialAddress.billingAddressline2 = "";
      }
      intialAddress.billingCountry = !!addressData.CountryCode
        ? addressData.CountryCode
        : "";
      intialAddress.billingState = !!addressData.PoliticalDivision1
        ? addressData.PoliticalDivision1
        : "";
      intialAddress.billingCity = !!addressData.PoliticalDivision2
        ? addressData.PoliticalDivision2
        : "";
      intialAddress.billingZip = !!addressData.PostcodePrimaryLow
        ? addressData.PostcodePrimaryLow
        : "";
    } else {
      if (Array.isArray(addressData.AddressLine)) {
        intialAddress.shippingAddressline1 = addressData.AddressLine[0];
        intialAddress.shippingAddressline2 = addressData.AddressLine[1];
      } else {
        intialAddress.shippingAddressline1 = addressData.AddressLine;
        intialAddress.shippingAddressline2 = "";
      }

      intialAddress.shippingCountry = !!addressData.CountryCode
        ? addressData.CountryCode
        : "";
      intialAddress.shippingState = !!addressData.PoliticalDivision1
        ? addressData.PoliticalDivision1
        : "";
      intialAddress.shippingCity = !!addressData.PoliticalDivision2
        ? addressData.PoliticalDivision2
        : "";
      intialAddress.shippingZip = !!addressData.PostcodePrimaryLow
        ? addressData.PostcodePrimaryLow
        : "";
    }
    this.props.onSubmit(intialAddress);
  }

  render() {
    return (
      <div>
        {/* relevant address modal */}
        <Dialog
          open={true}
          onClose={(event) => this.props.hideDialog(event.target.innerHTML)}
          aria-labelledby="address-modal"
          disableBackdropClick
          disableEscapeKeyDown
          maxWidth={false}
          classes={{
            paper: "modal-address",
            root: "modal-stack-top",
          }}
        >
          {/* Dialog Content */}
          <DialogTitle id="address-modal" className="modal-address-title">
            Relevant Address
          </DialogTitle>
          <DialogContent dividers className="modal-address-content">
            <RadioGroup
              aria-label="relevant address"
              name="relevantaddress"
              value={this.state.selectedAddress}
            >
              {!!this.props.addressVerifyData &&
              this.props.addressVerifyData.length > 0 ? (
                this.props.addressVerifyData.map((e, index) => {
                  return this.getAdressLine(e.AddressKeyFormat, index);
                })
              ) : (
                <h3>Address is not available!!</h3>
              )}
            </RadioGroup>
          </DialogContent>
          <DialogActions className="modal-address-action">
            <Button
              onClick={() => this.props.cancelDialog()}
              variant="contained"
              color="primary"
              className={" btn-secondary"}
              aria-label="Cancel"
            >
              Cancel
            </Button>
            {!!this.props.addressVerifyData &&
            this.props.addressVerifyData.length > 0 ? (
              <Button
                onClick={() => this.manageSuggestedAddress()}
                color="primary"
                className={" btn-primary"}
                id="applyAddress"
                aria-label="Apply Address"
              >
                {" "}
                Apply{" "}
              </Button>
            ) : (
              ""
            )}
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}
export default AddressSuggestionDialog;
