import React, { Component } from "react";
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Grid,
  Typography,
  CircularProgress,
} from "@material-ui/core";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import QuantityCount from "../../commonComponents/quantityCount/QuantityCount";
import Color from "color-thief-react";
import { hexToRgbA } from "../../Utils";
import { EventNames, trackEvent } from "../../../appinsights/EventTrack";
import { DisplayHeading } from "./../../../config/constants/content.constant";
import APIUtil from "../../../config/APIUtil";
import UrlConstants from "../../../config/UrlConstants";
import { ViewedPage } from "../../../config/amplitude/SnacksAmplitude";
import {
  PAGENAME,
  PRODUCTTYPE,
} from "../../../config/amplitude/Taxonomy.constants";
import "./ProductDetail.scss";

export default class MultipackDetail extends Component {
  state = {
    productDetail: [],
    isDetailLoading: true,
  };

  componentDidMount() {
    ViewedPage(PAGENAME.VARIETYDETAIL);
    this.getProductDetailAPI();
  }

  getProductDetailAPI = () => {
    const { productData } = this.props;
    if (productData) {
      trackEvent(
        EventNames.Action.ACTION_MULTIPACK_PRODUCT_DETAIL,
        EventNames.Event.EVENT_PAGE_VIEW,
        window.location.origin,
        window.location.pathname,
        { product: JSON.stringify(productData) }
      );
      const payload = {
        prodId: productData.id,
        locationNumber: sessionStorage.getItem("_lo_No") || null,
      };
      APIUtil.postMethod(UrlConstants.multipackDetailUrl, payload, true)
        .then((response) => {
          this.setState({
            productDetail: response.data.data || [],
            isDetailLoading: false,
          });
        })
        .catch((error) => console.error("Multipack detail API", error));
    }
  };

  showAccordionContent = (heading, body, panel, show) => {
    const { productDetail } = this.state;
    return (
      <Accordion className="accordion-wrapper">
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-more-icon" />}
          aria-controls={`${panel}-content`}
          id={`${panel}-header`}
          className="accordion-header"
        >
          <Typography variant="h2">{heading}</Typography>
        </AccordionSummary>
        <AccordionDetails className="accordion-body">
          {show === "desc" ? (
            <div className="product-desc">{body}</div>
          ) : (
            <img src={body} alt={`${heading} of ${productDetail.brand}`} />
          )}
        </AccordionDetails>
      </Accordion>
    );
  };

  render() {
    const { productDetail, isDetailLoading } = this.state;
    const { selectedItems, warningMsgFunc, multipackLimit } = this.props;
    let rgbaColor = "";

    if (isDetailLoading) {
      return (
        <Grid
          container
          justify="center"
          style={{
            minHeight: "100px",
            textAlign: "center",
            paddingTop: "25px",
          }}
        >
          <Grid item>
            <CircularProgress />
          </Grid>
        </Grid>
      );
    }

    if (productDetail && productDetail.length === 0) {
      return (
        <Grid container justify="center">
          <Grid item>
            <h1 className="noproducts-text">No Product details found</h1>
          </Grid>
        </Grid>
      );
    }

    const {
      image,
      backgroundColorRgb,
      shortDescription,
      availableQty,
      currency,
      size,
      // price,
      brand,
    } = productDetail[0];

    return (
      <React.Fragment>
        <Color src={image.Product_Image} crossOrigin="Anonymous" format="hex">
          {({ data, loading }) => {
            if (backgroundColorRgb) {
              rgbaColor = hexToRgbA("#" + backgroundColorRgb);
              data = hexToRgbA("#" + backgroundColorRgb);
            } else {
              rgbaColor = data ? hexToRgbA(data) : "#000000";
            }
            // const rgbaColor = data ? hexToRgbA(data) : "#000000";
            return (
              <div
                className="product-detail-wrapper"
                style={{
                  backgroundImage: `linear-gradient(to bottom, ${rgbaColor} ,${data})`,
                }}
              >
                <Grid container className="product-info-container">
                  <Grid item sm={6}>
                    <div className="product-detail-image-outer">
                      <div className="product-detail-image">
                        <img src={image.Product_Image} alt={shortDescription} />
                        <div className="shadow"></div>
                      </div>
                    </div>
                  </Grid>
                  <Grid item sm={6} className="product-info-div">
                    <div className="product-brand">{brand}</div>
                    <h1 className="product-name">{shortDescription}</h1>
                    <div className="product-detail-actions">
                      {availableQty <= 0 && (
                        <div className="out-of-stock">
                          {DisplayHeading.OUT_OF_STOCK}
                        </div>
                      )}
                      {availableQty > 0 && (
                        <QuantityCount
                          primaryProduct={productDetail[0]}
                          productInfo={productDetail[0]}
                          selectedItems={selectedItems}
                          sizeName={size}
                          // price={price}
                          warningMsgFunc={warningMsgFunc}
                          availableQty={availableQty}
                          currency={currency}
                          multipack
                          multipackLimit={multipackLimit}
                          PageTitle={PAGENAME.VARIETYDETAIL}
                          ProductType={PRODUCTTYPE.VARIETY}
                        />
                      )}
                    </div>
                    {image.Nutrition_Image &&
                      this.showAccordionContent(
                        DisplayHeading.NUTRITION_FACTS,
                        image.Nutrition_Image,
                        "panel3a"
                      )}
                    {image.Ingredients_Image &&
                      this.showAccordionContent(
                        DisplayHeading.INGREDIENTS,
                        image.Ingredients_Image,
                        "panel1a"
                      )}
                  </Grid>
                </Grid>
              </div>
            );
          }}
        </Color>
      </React.Fragment>
    );
  }
}
