import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { Card, Button, Tooltip } from "@material-ui/core";
import Truncate from "react-truncate";
import "./multipack-product-tout.scss";

class MultipackProductTout extends Component {
  render() {
    const { productInfo } = this.props;
    const isHoliday = productInfo[0].hasOwnProperty("isHolidayShop");
    return (
      <Card
        className={`product-box-bundle-card bundle-card multipack-product-tout ${
          isHoliday && "isHoliday"
        }`}
        onClick={() => {
          if (isHoliday) {
            this.props.history.push(`/holidayshop`);
          } else {
            this.props.history.push(`/varietypack`);
          }
        }}
      >
        <div className="product-content-wrapper">
          <Button className="media-card-wrapper">
            <img alt="Multipack" src={productInfo[0].image} />
          </Button>
          <div className="product-information">
            <div className="product-heading">{productInfo[0].heading}</div>
            <Tooltip placement="top" title={productInfo[0].shortDescription}>
              <div className="product-brand">
                <Truncate lines={3} ellipsis="...">
                  {productInfo[0].shortDescription}
                </Truncate>
              </div>
            </Tooltip>
            <div className="product-action">
              <Button
                className="selected-product"
                aria-label={
                  isHoliday
                    ? `${productInfo[0].buttonText} in holiday shop`
                    : "build Variety Pack"
                }
              >
                {isHoliday ? productInfo[0].buttonText : "build"}
              </Button>
            </div>
          </div>
        </div>
      </Card>
    );
  }
}

export default withRouter(MultipackProductTout);
