import React, { Component } from "react";
import { Grid, Button, Dialog, DialogContent } from "@material-ui/core";
import { withRouter } from "react-router-dom";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
import CustomEvent from "custom-event";
import MultipackTout from "../multipackTout/MultipackTout";
import MultipackConfirmation from "../multipackConfirmation/MultipackConfirmation";
import "./multipack-cart.scss";

class MultipackCart extends Component {
  state = {
    packCount: 0,
    isExpanded: true,
    showConfirmation: false,
  };

  componentDidMount() {
    this.multipackCount();
    let that = this;
    document.addEventListener("multipackCartItems", function (e) {
      that.multipackCount();
    });
    this.expandedState();
  }

  expandedState = () => {
    window.innerWidth < 991 &&
      this.setState({
        isExpanded: false,
      });
  };

  multipackCount = () => {
    let packCount = 0;
    const multipackCartItem = JSON.parse(
      sessionStorage.getItem("multipackCartItems") || "[]"
    );
    multipackCartItem &&
      multipackCartItem.map((item) => {
        packCount = packCount + parseInt(item.quantity);
        return packCount;
      });
    this.setState({ packCount });
  };

  generateMultipackId = (cartItems, multipackId) => {
    const mCart = cartItems.filter((item) => item.multipack);
    if (mCart.length > 0) {
      mCart.sort((a, b) => a.id.localeCompare(b.id));
      const mId =
        parseInt(mCart[mCart.length - 1].id.split(`${multipackId}-`)[1]) + 1;
      return `${multipackId}-${mId}`;
    }
    return `${multipackId}-1`;
  };

  packExistInCart = (cartItems, multipackCartItem) => {
    let itemExist = false;
    const multipackEditId = sessionStorage.getItem("multipackEditId");
    if (cartItems.length > 0) {
      cartItems.map((item) => {
        if (item.multipack) {
          if (multipackEditId && item.productId === multipackEditId) {
            itemExist = {
              multipackId: item.productId,
              multipackQty: item.quantity,
            };
          } else {
            const sortedListA = item.multipackList.sort((a, b) =>
              a.id.localeCompare(b.id)
            );
            const sortedListB = multipackCartItem.sort((a, b) =>
              a.id.localeCompare(b.id)
            );
            if (JSON.stringify(sortedListA) === JSON.stringify(sortedListB)) {
              itemExist = {
                multipackId: item.productId,
                multipackQty: item.quantity,
              };
            }
          }
        }
      });
    }

    return itemExist;
  };

  addToMainCart = (multipackCartItem) => {
    const multipackEditId = sessionStorage.getItem("multipackEditId");
    const cartItems = JSON.parse(sessionStorage.getItem("cartItems") || "[]");
    const alreadyExist = this.packExistInCart(cartItems, multipackCartItem);

    let updatedCartItem = [],
      multipackItem = {};

    const {
      config: {
        mixCount,
        image,
        price,
        shortDescription,
        currency,
        multipackId,
        zNonSku,
        varietyPackAvailableQty,
        productTitle,
      },
    } = this.props;

    const newQty =
      alreadyExist && !multipackEditId
        ? alreadyExist.multipackQty + 1
        : multipackEditId
        ? alreadyExist.multipackQty
        : 1;

    const newMultipackId = alreadyExist
      ? `${alreadyExist.multipackId}`
      : this.generateMultipackId(cartItems, multipackId);

    const index = cartItems.findIndex(
      (obj) => obj.productId === newMultipackId
    );

    multipackItem = {
      currency,
      id: newMultipackId,
      productId: newMultipackId,
      zNonSku,
      image,
      productTitle,
      price,
      availableQty: varietyPackAvailableQty,
      quantity: newQty,
      size: mixCount,
      multipack: true,
      shortDescription,
      multipackList: multipackCartItem,
    };

    if (index === -1) {
      updatedCartItem = [...new Set([multipackItem, ...cartItems])];
    } else {
      cartItems[index].quantity = newQty;
      if (multipackEditId) {
        cartItems[index].multipackList = multipackCartItem;
      }
      updatedCartItem = [...cartItems];
    }
    if (multipackEditId) {
      this.setState({
        packCount: 0,
      });
      this.props.history.push("/cart");
    } else {
      this.setState({
        showConfirmation: !this.state.showConfirmation,
        packCount: 0,
      });
    }
    sessionStorage.setItem("cartItems", JSON.stringify(updatedCartItem));
    sessionStorage.setItem("multipackCartItems", "[]");
    sessionStorage.removeItem("multipackEditId");
    document.dispatchEvent(new CustomEvent("cartitem"));
    document.dispatchEvent(new CustomEvent("multipackCartItems"));
  };

  renderMuiltipackCartList = (contentList, multipackLimit) => {
    const { warningMsgFunc } = this.props;
    return contentList.map((productList, index) => (
      <MultipackTout
        horizontal
        productList={productList}
        index={index}
        key={index}
        multipackLimit={multipackLimit}
        warningMsgFunc={warningMsgFunc}
      />
    ));
  };

  addToCartButton = (packCount, mixCount, multipackCartItem) => {
    const multipackEditId = sessionStorage.getItem("multipackEditId");

    return (
      <Button
        disabled={packCount !== mixCount}
        className="cart-button"
        onClick={() => this.addToMainCart(multipackCartItem)}
        aria-label={multipackEditId ? "Update Cart" : "add to cart"}
      >
        {multipackEditId ? "Update Cart" : "add to cart"}
      </Button>
    );
  };

  render() {
    const multipackCartItem = JSON.parse(
      sessionStorage.getItem("multipackCartItems") || "[]"
    );
    const { packCount, isExpanded } = this.state;
    const { config } = this.props;
    const { mixCount, multipackCartItemsOut, multipackCartHeading } = config;

    return (
      <Grid
        container
        justify="center"
        className={`multipack-cart ${this.state.isExpanded && "open"}`}
        id="multipackCart"
      >
        <Grid item sm={12} className="multipack-cart-header">
          {this.addToCartButton(packCount, mixCount, multipackCartItem)}
          <div className="title">
            <span>{packCount}</span> {multipackCartItemsOut}
          </div>
          <Button
            className="multipack-cart-arrow"
            aria-label={`click here to ${
              this.state.isExpanded ? "collapse" : "expand"
            } cart`}
            onClick={() => {
              this.setState({
                isExpanded: !this.state.isExpanded,
              });
            }}
          >
            {isExpanded ? <ExpandLessIcon /> : <ExpandMoreIcon />}
          </Button>
        </Grid>
        <Grid item sm={12} className="multipack-cart-body">
          {isExpanded && (
            <div className="multipack-cart-content">
              {packCount === 0 && (
                <div className="note">{multipackCartHeading}</div>
              )}
              {packCount > 0 &&
                this.renderMuiltipackCartList(multipackCartItem, mixCount)}
            </div>
          )}
          <div className="multipack-cart-action">
            {this.addToCartButton(packCount, mixCount, multipackCartItem)}
          </div>
          <Dialog
            open={this.state.showConfirmation}
            maxWidth={false}
            scroll={"body"}
            classes={{
              paper: "multipack-modal modal-paper",
              root: "modal-stack-top",
            }}
          >
            <DialogContent>
              {/* <Button
                className="icn-close"
                id="productDetailClose"
                aria-label="productDetail Close"
                onClick={() =>
                  this.setState({
                    showConfirmation: !this.state.showConfirmation,
                  })
                }
              >
                <i className="fa fa-times"></i>
              </Button> */}
              <MultipackConfirmation config={config} />
            </DialogContent>
          </Dialog>
        </Grid>
      </Grid>
    );
  }
}

export default withRouter(MultipackCart);
