import React, { Component } from "react";
import Truncate from "react-truncate";
import { NavLink } from "react-router-dom";
import { IconButton, Tooltip } from "@material-ui/core";
import QuantityCount from "../quantityCount/QuantityCount";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
import { DisplayHeading } from "../../../config/constants/content.constant";
import { ClickedButton } from "../../../config/amplitude/SnacksAmplitude";
import {
  METHOD,
  BUTTONNAME,
  PRODUCTTYPE,
} from "../../../config/amplitude/Taxonomy.constants";
import "./MiniCart.scss";

class MiniCart extends Component {
  state = {
    cartItems: JSON.parse(sessionStorage.getItem("cartItems")) || [],
    open: false,
    message: "The cart is empty",
    itemIndex: undefined,
    cartData: [],
    totalPrice: 0,
    minPrice: "",
    snackOpen: true,
    msg: "",
    selectedItems: [],
    cartExpand: false,
  };

  render() {
    const { warningMsgFunc, selectedItems, minPrice } = this.props;
    const { cartExpand } = this.state;
    let cartItems = JSON.parse(sessionStorage.getItem("cartItems"));
    let cartInfo = [],
      price = 0,
      totalQuantity = 0;
    cartItems !== null &&
      cartItems.map(function (item) {
        price = price + parseFloat(item.price) * item.quantity;
        totalQuantity = totalQuantity + parseInt(item.quantity);
        cartInfo["price"] = price;
        cartInfo["quantity"] = totalQuantity;
        return cartInfo;
      });

    return (
      <div className={cartExpand ? "mini-cart open" : "mini-cart"}>
        {cartExpand && (
          <div className="mini-cart-body">
            {cartItems !== null && cartItems.length > 0 ? (
              cartItems.map((itemName, index) => {
                let productTitle = itemName.multipack
                  ? `${itemName.productTitle}`
                  : itemName.title;
                let ProductType = PRODUCTTYPE.SNACKS;
                itemName.multipack && (ProductType = PRODUCTTYPE.VARIETY);
                itemName.isHolidayShopProduct &&
                  (ProductType = PRODUCTTYPE.HOLIDAY);

                return (
                  <div key={index} className="mini-cart-single">
                    <div className="mini-cart-info">
                      <div className="product-heading">
                        <Truncate lines={2} ellipsis="...">
                          {productTitle}
                        </Truncate>
                      </div>
                      {!itemName.multipack && (
                        <div className="product-brand">{itemName.size}</div>
                      )}
                    </div>
                    <div className="mini-cart-qty">
                      <QuantityCount
                        primaryProduct={itemName}
                        productInfo={itemName}
                        warningMsgFunc={warningMsgFunc}
                        selectedItems={selectedItems}
                        availableQty={itemName.availableQty}
                        currency={itemName.currency}
                        PageTitle={METHOD.MINICART}
                        ProductType={ProductType}
                      />
                    </div>
                  </div>
                );
              })
            ) : (
              <div className="no-items" aria-label="No Items in the cart">
                No Items in cart
              </div>
            )}
          </div>
        )}
        <div className="mini-cart-footer">
          {(cartInfo["price"] > minPrice ||
            (cartItems && cartItems.length > 0 && cartItems[0].multipack)) && (
            <NavLink
              to="/cart"
              onClick={() =>
                ClickedButton(BUTTONNAME.CHECKOUT, METHOD.MINICART)
              }
              aria-label={DisplayHeading.CHECKOUT}
              className="mini-cart-checkout"
            >
              {DisplayHeading.CHECKOUT}
            </NavLink>
          )}
          <div className="mini-cart-action">
            <div className="mini-cart-text">
              <span
                className="mini-cart-item"
                aria-label={`${
                  cartInfo.quantity > 0 ? cartInfo.quantity : "No"
                } item(s)`}
              >
                {cartInfo.quantity > 0 ? cartInfo.quantity : "No "} item(s)
              </span>
              <span className="mini-cart-price">
                <sup>$</sup>
                {cartInfo.price > 0 ? cartInfo.price.toFixed(2) : "0"}
              </span>
            </div>

            <Tooltip
              placeholder="top"
              title={`${
                cartExpand ? "hide mini cart items" : "show mini cart items"
              }`}
            >
              <IconButton
                aria-label={`${
                  cartExpand ? "hide mini cart" : "show mini cart"
                }`}
                id="miniCartToggle"
                aria-expanded={cartExpand}
                aria-controls="expandable"
                onClick={() =>
                  this.setState({
                    cartExpand: !cartExpand,
                  })
                }
              >
                {cartExpand ? <ExpandLessIcon /> : <ExpandMoreIcon />}
              </IconButton>
            </Tooltip>
          </div>
        </div>
      </div>
    );
  }
}
export default MiniCart;
