import React, { Component } from "react";
import BottomScrollListener from "react-bottom-scroll-listener";
import { Container, Grid, CircularProgress } from "@material-ui/core";
import { connect } from "react-redux";
import {
  actionCheckProductAvailability,
  actionGetMinAmount,
  actiongetProduct,
} from "../../../actions/ProductAction";
import APIUtil from "../../../config/APIUtil";
import UrlConstants from "../../../config/UrlConstants";
import SearchFilter from "../../commonComponents/searchFilter/SearchFilter";
import ProductDetailDialog from "../productDetail/ProductDetailDialog";
import ProductTout from "../../ProductTout/ProductTout";
import SnacksAlertMessage from "../../commonComponents/snacksAlertMessage/SnacksAlertMessage";
import SomethingWrongAlert from "../../commonComponents/somethingWrongAlert/SomethingWrongAlert";
import { DisplayHeading } from "../../../config/constants/content.constant";
import MiniCart from "../../commonComponents/miniCart/MiniCart";
import { deepLinkValidation } from "../../Utils";
import {
  ViewedPage,
  ClickedButton,
  SearchEvent,
  ProductListFilters,
} from "../../../config/amplitude/SnacksAmplitude";
import {
  PAGENAME,
  BUTTONNAME,
} from "../../../config/amplitude/Taxonomy.constants";
import "./productList.scss";

class ProductList extends Component {
  state = {
    dialougeBoxOpen: false,
    selectedItems: [],
    showProductDetail: false,
    minOrder: "",
    currency: "",
    limit: UrlConstants.productsLimit,
    locationNumber:
      localStorage.getItem("_lo_No") || sessionStorage.getItem("_lo_No"),
    offset: 0,
    msg: null,
    totalProducts: null,
    productDetailIndex: "",
    filterData: {},
    productInfo: [],
    productsList: [],
    isProductLoading: true,
    isProductScrollable: false,
    totalProductsVisible: 0,
    lazyLoading: false,
    storeProductId: "",
    isProductScrolling: false,
  };

  componentDidMount() {
    ViewedPage(PAGENAME.SNACKS);
    this.getFilteredData();
    window.scrollTo(0, 0);
  }

  UNSAFE_componentWillMount() {
    if (this.props.location && this.props.location.search) {
      deepLinkValidation(this.props.location);
    }
    this.actionGetMinAmount();
  }

  productListAPICall = (callFrom) => {
    let payload = this.state.filterData;
    const { limit, offset, locationNumber, productsList } = this.state;

    if (!payload.hasOwnProperty("limit")) {
      payload = { limit, offset, locationNumber, varietyPackCookie: true };
      this.setState({ filterData: payload });
    }
    if (callFrom === "lazy") {
      payload = {
        ...payload,
        offset: this.state.offset,
      };
    }
    var productsData =
      !!productsList && productsList.length > 0 ? productsList : [];
    APIUtil.postMethod(UrlConstants.ProductsUrl, payload, true)
      .then((response) => {
        var productListData = response.data.Groups || [];
        if (
          productsData.length > 0 &&
          !!productListData &&
          productListData.length > 0 &&
          callFrom !== "appliedFilter"
        ) {
          productListData.map((prods) => productsData.push(prods));
        } else {
          productsData = productListData;
        }
        this.setState({
          productsList: productsData,
          isProductLoading: false,
          totalProducts: response.data && response.data.totalProducts,
          lazyLoading: callFrom === "lazy",
          isProductScrolling: false,
        });
      })
      .catch((error) => {
        console.error("Products Group API", error);
      });
  };

  handleOnDocumentBottom = () => {
    const { offset, totalProducts, limit } = this.state;
    const totalCount = offset + limit;

    if (totalProducts > totalCount) {
      this.setState(
        {
          lazyLoading: true,
          offset: offset + UrlConstants.productsLimit,
          isProductScrolling: true,
        },
        () => {
          setTimeout(() => {
            this.productListAPICall("lazy");
          }, 500);
        }
      );
    } else {
      this.setState({ lazyLoading: false });
    }
  };

  getFilteredData = () => {
    this.setState({ isProductLoading: true });
    const selectedCategory = sessionStorage.getItem("selectedCategory")
      ? JSON.parse(sessionStorage.getItem("selectedCategory"))
      : [];
    let payload = {
      offset: this.state.offset,
      limit: this.state.limit,
      locationNumber:
        localStorage.getItem("_lo_No") || sessionStorage.getItem("_lo_No"),
    };
    selectedCategory.map((item) => {
      const { categoryId, subCategoryIdList } = item;
      return (payload = {
        ...payload,
        varietyPackCookie: true,
        [categoryId]: subCategoryIdList,
      });
    });
    if (selectedCategory && selectedCategory.length > 0) {
      this.setState(
        {
          filterData: payload,
        },
        () => {
          ProductListFilters(this.state.filterData);
          this.productListAPICall("appliedFilter");
        }
      );
    } else {
      this.productListAPICall();
    }
  };

  actionGetMinAmount() {
    const { minPrice } = this.props;
    if (
      sessionStorage.getItem("shippingMinimum") === undefined ||
      sessionStorage.getItem("shippingMinimum") === null
    ) {
      this.props.actionGetMinAmount("", "", "", "").then(() => {
        this.setState({
          minOrder: minPrice ? minPrice.shippingMinimum : "",
        });
        if (minPrice && Object.keys(minPrice).length > 0) {
          sessionStorage.setItem(
            "shippingMinimum",
            minPrice.shippingMinimum || null
          );
          sessionStorage.setItem(
            "shippingCharges",
            minPrice.shippingCharges || null
          );
        }
      });
    } else {
      this.setState({
        minOrder: sessionStorage.getItem("shippingMinimum") || "",
      });
    }
  }

  updateProductDetailStatus = (status) => {
    this.setState({ showProductDetail: status });
  };

  /**
   * @description To show product detail popup
   * @memberof ProductLibrary
   */
  productDetail = (productInfo, index) => {
    this.setState({
      showProductDetail: true,
      productInfo: productInfo,
      productDetailIndex: index,
    });
  };

  warningMsgFunc = (msg, availableQty) => {
    this.setState(
      {
        msg: msg.replace(
          /##QTY##/g,
          function () {
            return parseInt(availableQty);
          },
          document.getElementById("body").classList.add("has-toast")
        ),
      },
      () =>
        setTimeout(() => {
          this.setState({ msg: null });
          document.getElementById("body").classList.remove("has-toast");
        }, 3000)
    );
  };

  selectedCartItems = (selectedItems) => {
    this.setState({ selectedItems });
  };

  selectedFilter = (filters, search) => {
    ClickedButton(BUTTONNAME.APPLYFILTERS, PAGENAME.SNACKS);
    this.setState(
      {
        filterData: {
          ...this.state.filterData,
          [UrlConstants.searchKey]: search.trim() || "",
          ...filters,
        },
        isProductLoading: true,
        offset: 0,
      },
      () => {
        ProductListFilters(this.state.filterData);
        SearchEvent(search, PAGENAME.SNACKS);
        this.productListAPICall("appliedFilter");
      }
    );
  };

  clearAllfilters = () => {
    sessionStorage.removeItem("selectedCategory");
    ClickedButton(BUTTONNAME.CLEARALLFILTERS, PAGENAME.SNACKS);
    this.setState(
      {
        isProductLoading: true,
        limit: UrlConstants.productsLimit,
        offset: 0,
        locationNumber: sessionStorage.getItem("_lo_No"),
        filterData: {},
      },
      () => this.productListAPICall("appliedFilter")
    );
    window.location && (window.location.search = "");
  };

  updateSelectedProduct = (storeProductId) => {
    this.setState({ storeProductId });
  };

  renderProductsList = (productsList) => {
    if (this.state.isProductLoading) {
      return this.loadSpinner();
    }
    return productsList.map((prod, index) => {
      if (prod && prod.length > 0) {
        return (
          <Grid item key={`product-${index}`} xs={6} lg={3} sm={3}>
            <ProductTout
              productKey={`${prod.productId}-${index}`}
              productInfo={prod}
              index={index}
              selectedItems={this.selectedCartItems}
              detailedInfo={this.productDetail}
              warningMsgFunc={this.warningMsgFunc}
              updateSelectedProduct={this.updateSelectedProduct}
              storeProductId={this.state.storeProductId}
              PageTitle={PAGENAME.SNACKS}
            />
          </Grid>
        );
      }
      return (
        <Grid container justify="center" className="noproducts-grid">
          <Grid item>
            <h2 className="noproducts-text">No Products</h2>
          </Grid>
        </Grid>
      );
    });
  };

  updateDialogState = () => {
    this.setState({ dialougeBoxOpen: false });
  };

  loadSpinner = () => {
    return (
      <Grid
        container
        spacing={0}
        style={{ textAlign: "center" }}
        justify="center"
        className="noproducts-grid"
      >
        <Grid item xs={12} sm={12} lg={12} className="loader-inline">
          <CircularProgress disableShrink />
        </Grid>
      </Grid>
    );
  };

  render() {
    const {
      productsList,
      showProductDetail,
      productInfo,
      lazyLoading,
      msg,
      isProductLoading,
      selectedItems,
      minOrder,
      isProductScrolling,
    } = this.state;
    const { minPrice } = this.props;
    return (
      <div className="page-product-listing">
        <SearchFilter
          filterMainHeading={DisplayHeading.PRODUCT_LIST_TITLE}
          selectedFilter={this.selectedFilter}
          removedFilter={this.removedFilter}
          clearAllfilters={this.clearAllfilters}
          locationNumber={localStorage.getItem("_lo_No")}
        />
        <MiniCart
          warningMsgFunc={this.warningMsgFunc}
          selectedItems={this.selectedCartItems}
          minPrice={minPrice ? minPrice.shippingMinimum : minOrder}
        />

        <Container className="container-root">
          {!isProductScrolling ? (
            <BottomScrollListener
              offset="950"
              debounce="200"
              onBottom={() => this.handleOnDocumentBottom()}
            />
          ) : (
            ""
          )}

          {msg && <SnacksAlertMessage fullWidth msg={msg} />}

          <Grid
            container
            spacing={3}
            id="productGridContainer"
            className="product-grid-container lists"
          >
            {productsList && productsList.length > 0 ? (
              this.renderProductsList(productsList)
            ) : isProductLoading ? (
              this.loadSpinner()
            ) : (
              <Grid container justify="center" className="noproducts-grid">
                <Grid item>
                  <h2 className="noproducts-text">No Products</h2>
                </Grid>
              </Grid>
            )}
          </Grid>

          {lazyLoading && this.loadSpinner()}

          {this.state.dialougeBoxOpen && (
            <SomethingWrongAlert
              dialougeBoxOpen={this.state.dialougeBoxOpen}
              updateDialogState={this.updateDialogState}
            />
          )}

          {showProductDetail && (
            <ProductDetailDialog
              status={showProductDetail}
              statusUpdate={this.updateProductDetailStatus}
              productData={productInfo}
              selectedItems={selectedItems}
              warningMsgFunc={this.warningMsgFunc}
              selectedCartItems={this.selectedCartItems}
            />
          )}
        </Container>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    products: state.reducer.Products || [],
    totalProduct: state.reducer.totalProduct,
    filterProducts: state.reducer.filterProducts,
    minPrice: state.reducer.minAmount,
    productInventory: state.reducer.productInventory,
  };
};

export default connect(mapStateToProps, {
  actiongetProduct,
  actionCheckProductAvailability,
  actionGetMinAmount,
})(ProductList);
