/**
 * @description Class with functions with post, put, get, delete method api call
 */
import Axios from "axios";
import UrlConstants from "./UrlConstants";
var accessToken = !!localStorage.getItem("accessToken")
  ? JSON.parse(localStorage.getItem("accessToken")).accessToken
  : "";
class APIUtil {
  /**
   *
   * @description function to call external api with url
   * @param {*} url - API URL
   * @returns error.response || "status":"404".
   * @memberof APIUtil
   */
  checAPIData() {
    if (
      !!sessionStorage.getItem("_ss_i") &&
      !!sessionStorage.getItem("_rr_n_dts") &&
      !!sessionStorage.getItem("_en_cs")
    ) {
      return true;
    } else {
      window.location.hash = "#404";
      // window.location.reload();
    }
  }

  externalAPI(url, auth) {
    const apicheck = this.checAPIData();
    if (apicheck) {
      var headersSet = {
        Accept: "application/json",
        "Content-Type": "application/json",
      };
      if (auth) {
        headersSet["sessionId"] = sessionStorage.getItem("_ss_i");
        headersSet["rnduts"] = sessionStorage.getItem("_rr_n_dts");
        headersSet["encstr"] = sessionStorage.getItem("_en_cs");
      }
      return Axios({
        method: "get",
        url: url,
        headers: headersSet,
      })
        .then((response) => response)
        .catch((error) => {
          if (error.response.statusText === "Forbidden") {
            this.locationReload();
          } else if (error.response) {
            return error.response;
          } else {
            return { status: "404" };
          }
        });
    }
  }
  /**
   *
   * @description action to call get api with/without auth token
   * @param {*} url - API URL
   * @param {*} auth - true/false for auth token pass or not
   * @memberof APIUtil
   */
  getMethod(url, auth) {
    const apicheck = this.checAPIData();
    if (apicheck) {
      var headersSet = {
        Accept: "application/json",
        "Content-Type": "application/json",
      };

      if (auth) {
        //headersSet['Authorization']  = accessToken;
        headersSet["sessionId"] = sessionStorage.getItem("_ss_i");
        headersSet["rnduts"] = sessionStorage.getItem("_rr_n_dts");
        headersSet["encstr"] = sessionStorage.getItem("_en_cs");
        headersSet["X-Auth-Token"] = accessToken;
      }

      return Axios({
        method: "get",
        url: url,
        headers: headersSet,
      })
        .then((response) => response)
        .catch((error) => {
          if (error.response && error.response.statusText === "Forbidden") {
            this.locationReload();
          } else if (error.response) {
            if (error.response.data === "Invalid token.") {
              this.inValidToken();
            } else {
              return error.response;
            }
          } else {
            return { status: "404" };
          }
        });
    }
  }
  /**
   *
   * @description action to remove token from sessionStorage
   * @memberof APIUtil
   */
  inValidToken() {
    sessionStorage.removeItem("accessToken");
    sessionStorage.removeItem("usertype");
    window.location.href = UrlConstants.SiteUrl + "login";
    window.location.reload();
  }

  /**
   *
   * @description action to call post api with/without auth token and post data
   * @param {*} url - API URL
   * @param {*} data - Post data object
   * @param {*} auth - true/false for auth token pass or not
   * @memberof APIUtil
   */

  postAPIKeyMethod(url, data, auth) {
    var headersSet = {
      Accept: "application/json",
      "Content-Type": "application/json",
    };
    return Axios({
      method: "post",
      url: url,
      headers: headersSet,
      data: data,
    })
      .then((response) => response)
      .catch((error) => {
        if (error.response) {
          if (error.response.data === "Invalid token.") {
            this.inValidToken();
          } else {
            return error.response;
          }
        } else {
          return { status: "404" };
        }
      });
  }

  postMethod(url, data, auth) {
    const apicheck = this.checAPIData();
    if (apicheck) {
      var headersSet = {
        Accept: "application/json",
        "Content-Type": "application/json",
      };
      if (auth) {
        headersSet["sessionId"] = sessionStorage.getItem("_ss_i");
        headersSet["rnduts"] = sessionStorage.getItem("_rr_n_dts");
        headersSet["encstr"] = sessionStorage.getItem("_en_cs");
        headersSet["X-Auth-Token"] = accessToken;
      }

      return Axios({
        method: "post",
        url: url,
        headers: headersSet,
        data: data,
      })
        .then((response) => response)
        .catch((error) => {
          if (error.response.statusText === "Forbidden") {
            this.locationReload();
          } else if (error.response) {
            if (error.response.data === "Invalid token.") {
              this.inValidToken();
            } else {
              return error.response;
            }
          } else {
            return { status: "404" };
          }
        });
    }
  }
  locationReload() {
    sessionStorage.removeItem("_ss_i");
    sessionStorage.removeItem("_rr_n_dts");
    sessionStorage.removeItem("_en_cs");
    window.location.reload();
  }
  /**
   *
   * @description action to call put api with/without auth token and post data
   * @param {*} url - API URL
   * @param {*} data - Post data object
   * @param {*} auth - true/false for auth token pass or not
   * @memberof APIUtil
   */
  putMethod(url, data, auth) {
    const apicheck = this.checAPIData();
    if (apicheck) {
      var headersSet = {
        Accept: "application/json",
        "Content-Type": "application/json",
        "x-api-key": UrlConstants.PimAPIKey,
      };
      if (auth) {
        headersSet["sessionId"] = sessionStorage.getItem("_ss_i");
        headersSet["rnduts"] = sessionStorage.getItem("_rr_n_dts");
        headersSet["encstr"] = sessionStorage.getItem("_en_cs");
        headersSet["X-Auth-Token"] = accessToken;
      }

      return Axios({
        method: "PUT",
        url: url,
        headers: headersSet,
        data: data,
      })
        .then((response) => response)
        .catch((error) => {
          if (error.response.statusText === "Forbidden") {
            this.locationReload();
          } else if (error.response) {
            if (error.response.data === "Invalid token.") {
              this.inValidToken();
            } else {
              return error.response;
            }
          } else {
            return { status: "404" };
          }
        });
    }
  }
  /**
   *
   * @description action to call delete api with/without auth token and post data
   * @param {*} url - API URL
   * @param {*} auth - true/false for auth token pass or not
   * @memberof APIUtil
   */
  deleteMethod(url, auth, data) {
    const apicheck = this.checAPIData();
    if (apicheck) {
      var headersSet = {
        Accept: "application/json",
        "Content-Type": "application/json",
      };
      if (auth) {
        headersSet["sessionId"] = sessionStorage.getItem("_ss_i");
        headersSet["rnduts"] = sessionStorage.getItem("_rr_n_dts");
        headersSet["encstr"] = sessionStorage.getItem("_en_cs");
        headersSet["X-Auth-Token"] = accessToken;
      }

      return Axios({
        method: "DELETE",
        url: url,
        data: data ? data : null,
        headers: headersSet,
      })
        .then((response) => response)
        .catch((error) => {
          if (error.response.statusText === "Forbidden") {
            this.locationReload();
          } else if (error.response) {
            if (error.response.data === "Invalid token.") {
              this.inValidToken();
            } else {
              return error.response;
            }
          } else {
            return { status: "404" };
          }
        });
    }
  }

  /**
   *
   * @description action to call post api with/without auth token and post data with form data
   * @param {*} url - API URL
   * @param {*} data - Form data object
   * @param {*} auth - true/false for auth token pass or not
   * @memberof APIUtil
   */
  postFormDataMethod(url, data, auth) {
    const apicheck = this.checAPIData();
    if (apicheck) {
      var headersSet = {
        Accept: "application/json",
        "Content-Type": "multipart/form-data",
      };
      if (auth) {
        var accessToken = sessionStorage.getItem("token");
        //headersSet['Authorization']  = accessToken;
        headersSet["sessionId"] = sessionStorage.getItem("_ss_i");
        headersSet["rnduts"] = sessionStorage.getItem("_rr_n_dts");
        headersSet["encstr"] = sessionStorage.getItem("_en_cs");
      }

      return Axios({
        method: "post",
        url: url,
        headers: headersSet,
        data: data,
      })
        .then((response) => response)
        .catch((error) => {
          if (error.response.statusText === "Forbidden") {
            this.locationReload();
          } else if (error.response) {
            if (error.response.data === "Invalid token.") {
              this.inValidToken();
            } else {
              return error.response;
            }
          } else {
            return { status: "404" };
          }
        });
    }
  }
}
export default new APIUtil();
