import React, { Component } from "react";
import { ThemeProvider } from "@material-ui/styles";
import { createMuiTheme } from "@material-ui/core/styles";
import { blue, indigo } from "@material-ui/core/colors";
import { getAppInsights } from "./TelemetryService";
import TelemetryProvider from "./telemetry-provider";
import UrlConstants from "./config/UrlConstants";
import Routes from "./routes";

const theme = createMuiTheme({
  palette: {
    secondary: {
      main: blue[900],
    },
    primary: {
      main: indigo[700],
    },
  },
});

class App extends Component {
  state = {
    spinner: false,
    //instrumentationKey: !!this.props.minPrice ? this.props.minPrice.instrumentationKey : '',
  };

  actionGetMinAmount = () => {
    try {
      this.props.actionGetMinAmount().then(() => {
        const { minPrice } = this.props;
        if (!!minPrice && Object.keys(minPrice).length !== 0) {
          sessionStorage.setItem(
            "minimumOrderAmount",
            minPrice.minimumOrderAmount || null
          );
          sessionStorage.setItem(
            "shippingMinimum",
            minPrice.shippingMinimum || null
          );
          sessionStorage.setItem(
            "shippingCharges",
            minPrice.shippingCharges || null
          );
          sessionStorage.setItem(
            "holidayShopStockText",
            minPrice.holidayShopStockText || null
          );

          sessionStorage.setItem("msgTime", minPrice.mesgDisplayTimeout || "");
          if (
            minPrice.instrumentationKey !== null ||
            minPrice.instrumentationKey !== undefined
          ) {
            this.setState({
              instrumentationKey: minPrice.instrumentationKey,
            });
          }
        }
      });
    } catch (error) {
      console.log("someThing Went wrong", error);
    }
  };

  render() {
    let appInsights = null;
    let instrumentKey = "5d55d161-4411-4f69-b440-acc12095fd76";

    if (window.location.hostname != "www.snacks.com") {
      instrumentKey = "1e31d811-6fad-484c-879e-0d8ac97278f7";
    }
    return (
      <TelemetryProvider
        instrumentationKey={instrumentKey}
        after={() => {
          appInsights = getAppInsights();
        }}
      >
        <ThemeProvider theme={theme}>
          <div
            className="main-container"
            data-version={UrlConstants.BuildVersion}
          >
            <ThemeProvider theme={theme}>
              <Routes />
            </ThemeProvider>
          </div>
        </ThemeProvider>
      </TelemetryProvider>
    );
  }
}
export default App;
