import React, { Component } from "react";
import { Grid, Container, CircularProgress } from "@material-ui/core";
import MultipackHeader from "../../multipackHeader/MultipackHeader";
import MultipackList from "../../multipackList/MultipackList";
import MultipackCart from "../../multipackCart/MultipackCart";
import UrlConstants from "../../../config/UrlConstants";
import APIUtil from "../../../config/APIUtil";
import SnacksAlertMessage from "../../commonComponents/snacksAlertMessage/SnacksAlertMessage";
import { ViewedPage } from "../../../config/amplitude/SnacksAmplitude";
import { PAGENAME } from "../../../config/amplitude/Taxonomy.constants";
import "./multipack.scss";
import { EventNames, trackEvent } from "../../../appinsights/EventTrack";

export default class Multipack extends Component {
  state = {
    popularList: [],
    isPopularListLoaded: false,
    snacksList: [],
    isSnacksListLoaded: false,
    isSearch: true,
    config: {},
    isConfigLoading: true,
    locationNumber:
      sessionStorage.getItem("_lo_No") || localStorage.getItem("_lo_No"),
    offset: 0,
    limit: UrlConstants.multipackProductsLimit,
    packCount: 0,
    msg: "",
  };

  componentDidMount() {
    ViewedPage(PAGENAME.VARIETY);
    this.getConfig();
    this.getPopularProducts();
    this.getViewAllSnacks();
    this.multipackCount();
    let that = this;
    document.addEventListener("multipackCartItems", function (e) {
      that.multipackCount();
    });
  }

  multipackCount = () => {
    const multipackCartItem = JSON.parse(
      sessionStorage.getItem("multipackCartItems") || "[]"
    );
    let packCount = 0;
    multipackCartItem &&
      multipackCartItem.map((item) => {
        packCount = packCount + parseInt(item.quantity);
        return packCount;
      });
    this.setState({ packCount });
  };

  getConfig = () => {
    const { offset, limit, locationNumber } = this.state;
    const payload = { offset, limit, locationNumber };

    APIUtil.postMethod(UrlConstants.multipackConfig, payload, true)
      .then((response) => {
        this.setState({
          config: (response.data.data && response.data.data[0]) || {},
          isConfigLoading: false,
        });
      })
      .catch((error) => console.error("Multipack Config API", error));
  };

  getViewAllSnacks = (searchKey) => {
    const { offset, limit, locationNumber } = this.state;
    let payload = { offset, limit, locationNumber };

    if (searchKey) {
      payload = { ...payload, globalSearch: searchKey };
    }

    APIUtil.postMethod(UrlConstants.multipackViewAllSnacks, payload, true)
      .then((response) => {
        this.setState({
          snacksList: response.data.data || [],
          isSnacksListLoaded: true,
        });
      })
      .catch((error) => console.error("Multipack View All Snacks API", error));
  };

  searchAPI = (searchKey) => {
    this.setState({
      isSnacksListLoaded: false,
      isSearch: false,
    });
    this.getViewAllSnacks(searchKey);
    trackEvent(
      EventNames.Action.ACTION_MULTIPACK_PRODUCT_SEARCH,
      EventNames.Event.EVENT_PAGE_VIEW,
      window.location.origin,
      window.location.pathname,
      { searchBy: searchKey }
    );
  };

  getPopularProducts = () => {
    const { offset, limit, locationNumber } = this.state;
    let payload = { offset, limit, locationNumber };

    APIUtil.postMethod(UrlConstants.multipackPopularSnacks, payload, true)
      .then((response) => {
        this.setState({
          popularList: response.data.data || [],
          isPopularListLoaded: true,
        });
      })
      .catch((error) => console.error("Multipack Popular Products API", error));
  };

  warningMsgFunc = (msg, availableQty) => {
    this.setState(
      {
        msg: msg.replace(
          /##QTY##/g,
          function () {
            return parseInt(availableQty);
          },
          document.getElementById("body").classList.add("has-toast")
        ),
      },
      () =>
        setTimeout(() => {
          this.setState({ msg: null });
          document.getElementById("body").classList.remove("has-toast");
        }, 3000)
    );
  };

  render() {
    const {
      popularList,
      snacksList,
      config,
      isConfigLoading,
      isSnacksListLoaded,
      isPopularListLoaded,
      msg,
      isSearch,
    } = this.state;

    if (isConfigLoading) {
      return (
        <Grid
          container
          justify="center"
          style={{ minHeight: "100px", textAlign: "center" }}
        >
          <Grid item>
            <CircularProgress />
          </Grid>
        </Grid>
      );
    }

    const multipackLimit = config.mixCount || 0;

    return (
      <div className="multipack multipack-view">
        <SnacksAlertMessage fullWidth msg={msg} />
        <MultipackHeader searchAPI={this.searchAPI} config={config} />
        <Container className="container-root">
          <Grid
            container
            justify="center"
            className="multipack-body"
            spacing={2}
          >
            <Grid item xs={9} className="multipack-body-left">
              <MultipackList
                mainHeading="Popular Products"
                contentList={popularList}
                multipackLimit={multipackLimit}
                warningMsgFunc={this.warningMsgFunc}
                isContentLoaded={isPopularListLoaded}
                tabName={isSearch ? "popular" : false}
              />

              <MultipackList
                mainHeading="View All Snacks"
                contentList={snacksList}
                multipackLimit={multipackLimit}
                warningMsgFunc={this.warningMsgFunc}
                isContentLoaded={isSnacksListLoaded}
                tabName="snacks"
              />
            </Grid>
            <Grid item xs={3} className="multipack-body-right">
              <MultipackCart
                config={config}
                warningMsgFunc={this.warningMsgFunc}
              />
            </Grid>
          </Grid>
        </Container>
      </div>
    );
  }
}
