import React, { Component } from "react";
import {
  Grid,
  Container,
  CircularProgress,
  Dialog,
  DialogContent,
  Button,
} from "@material-ui/core";
import MultipackHeader from "./Header/holidayShopHeader";
import UrlConstants from "../../../config/UrlConstants";
import APIUtil from "../../../config/APIUtil";
import SnacksAlertMessage from "../../commonComponents/snacksAlertMessage/SnacksAlertMessage";
import "./holidayShop.scss";
import BottomScrollListener from "react-bottom-scroll-listener";
import HolidayShopProductTout from "../../ProductTout/HolidayShopProductTout";
import HolidayShopDetail from "./HolidayShopDetail";
import { EventNames, trackEvent } from "../../../appinsights/EventTrack";
import SomethingWrongAlert from "../../commonComponents/somethingWrongAlert/SomethingWrongAlert";
import {
  ViewedPage,
  SearchEvent,
} from "../../../config/amplitude/SnacksAmplitude";
import { PAGENAME } from "../../../config/amplitude/Taxonomy.constants";

export default class HolidayShopProductList extends Component {
  state = {
    dialougeBoxOpen: false,
    selectedItems: [],
    showProductDetail: false,
    currency: "",
    limit: UrlConstants.productsLimit,
    locationNumber:
      localStorage.getItem("_lo_No") || sessionStorage.getItem("_lo_No"),
    offset: 0,
    msg: null,
    totalProducts: null,
    productDetailIndex: "",
    productInfo: [],
    productsList: [],
    isProductLoading: true,
    lazyLoading: false,
    storeProductId: "",
    isProductScrolling: false,
    isConfigLoading: true,
    headerData: "",
    openHolidayShop: false,
    productSearchKey: "",
  };

  componentDidMount() {
    ViewedPage(PAGENAME.HOLIDAY);
  }

  UNSAFE_componentWillMount() {
    /* if(sessionStorage.getItem("holidayShopEnable")){
      this.getViewAllSnacks();
    } else{
      this.props.history.push('/home');
    } */
    this.getViewAllSnacks();
  }

  getViewAllSnacks = (searchKey, fromSearch) => {
    const { offset, limit, locationNumber, productsList } = this.state;
    let payload = { offset, limit, locationNumber };

    if (searchKey) {
      payload = { ...payload, globalSearch: searchKey };
    }
    var productsData =
      !!productsList && productsList.length > 0 ? productsList : [];
    APIUtil.postMethod(UrlConstants.holidayShop, payload, true)
      .then((response) => {
        if (response.data.success) {
          var productListData = response.data.Groups || [];
          if (
            productsData.length > 0 &&
            !!productListData &&
            productListData.length > 0 &&
            !fromSearch
          ) {
            productListData.map((prods) => {
              productsData.push(prods);
            });
          } else {
            productsData = productListData;
          }
          this.setState({
            productsList: productsData,
            isProductLoading: false,
            totalProducts: response.data && response.data.totalProducts,
            isProductScrolling: false,
            headerData:
              response.data && response.data.Header && response.data.Header[0],
          });
        } else {
          this.setState({
            msg: response.data.message
              ? response.data.message
              : UrlConstants.errorMsg,
            isProductLoading: false,
          });
        }
      })
      .catch((error) => console.error("Merchandise Product API", error));
  };

  updateSelectedProduct = (storeProductId) => {
    this.setState({ storeProductId });
  };

  handleOnDocumentBottom = () => {
    const { offset, totalProducts, limit } = this.state;
    const totalCount = offset + limit;

    if (totalProducts > totalCount) {
      this.setState(
        {
          lazyLoading: true,
          offset: offset + UrlConstants.productsLimit,
          isProductScrolling: true,
        },
        () => {
          setTimeout(() => {
            this.getViewAllSnacks(this.state.productSearchKey, false);
          }, 500);
        }
      );
    } else {
      this.setState({ lazyLoading: false });
    }
  };

  searchAPI = (searchKey) => {
    this.setState({
      isSnacksListLoaded: false,
      isProductLoading: true,
      productSearchKey: searchKey,
    });
    this.getViewAllSnacks(searchKey, true);
    trackEvent(
      EventNames.Action.ACTION_HOLIDAYSHOP_PRODUCT_SEARCH,
      EventNames.Event.EVENT_PAGE_VIEW,
      window.location.origin,
      window.location.pathname,
      { searchBy: searchKey }
    );
    SearchEvent(searchKey, PAGENAME.HOLIDAY);
  };

  productDetail = (productInfo, index) => {
    this.setState({
      showProductDetail: true,
      productInfo: productInfo,
      productDetailIndex: index,
    });
  };

  selectedCartItems = (selectedItems) => {
    this.setState({ selectedItems });
  };

  warningMsgFunc = (msg, availableQty) => {
    this.setState(
      {
        msg: msg.replace(
          /##QTY##/g,
          function () {
            return parseInt(availableQty);
          },
          document.getElementById("body").classList.add("has-toast")
        ),
      },
      () =>
        setTimeout(() => {
          this.setState({ msg: null });
          document.getElementById("body").classList.remove("has-toast");
        }, 3000)
    );
  };

  loadSpinner = () => {
    return (
      <Grid
        container
        spacing={0}
        style={{ textAlign: "center" }}
        justify="center"
        className={`noproducts-grid ${
          !this.state.lazyLoading && "loader-full"
        }`}
      >
        <Grid item xs={12} sm={12} lg={12} className="loader-inline">
          <CircularProgress disableShrink />
        </Grid>
      </Grid>
    );
  };

  renderProductsList = (productsList) => {
    if (this.state.isProductLoading) {
      return this.loadSpinner();
    }
    return productsList.map((prod, index) => {
      if (prod && prod.length > 0) {
        return (
          <Grid item key={`product-${index}`} xs={6} lg={3} sm={3}>
            <HolidayShopProductTout
              productKey={`${prod.productId}-${index}`}
              productInfo={prod}
              index={index}
              selectedItems={this.selectedCartItems}
              detailedInfo={this.productDetail}
              warningMsgFunc={this.warningMsgFunc}
              updateSelectedProduct={this.updateSelectedProduct}
              storeProductId={this.state.storeProductId}
              isHolidayShopProduct={true}
            />
          </Grid>
        );
      }
      return (
        <Grid container justify="center" className="noproducts-grid">
          <Grid item>
            <h2 className="noproducts-text">No Products</h2>
          </Grid>
        </Grid>
      );
    });
  };

  render() {
    const {
      productsList,
      showProductDetail,
      productInfo,
      lazyLoading,
      msg,
      isProductLoading,
      selectedItems,
      isProductScrolling,
      headerData,
    } = this.state;
    return (
      <div className="multipack multipack-view holiday-shop">
        <SnacksAlertMessage fullWidth msg={msg} />
        {headerData && (
          <MultipackHeader searchAPI={this.searchAPI} headerData={headerData} />
        )}
        <Container className="container-root">
          {!isProductScrolling ? (
            <BottomScrollListener
              offset="950"
              debounce="200"
              onBottom={() => this.handleOnDocumentBottom()}
            />
          ) : (
            ""
          )}

          {msg && <SnacksAlertMessage fullWidth msg={msg} />}

          <Grid
            container
            spacing={3}
            id="productGridContainer"
            className="product-grid-container lists"
          >
            {productsList && productsList.length > 0 ? (
              this.renderProductsList(productsList)
            ) : isProductLoading ? (
              this.loadSpinner()
            ) : (
              <Grid container justify="center" className="noproducts-grid">
                <Grid item>
                  <h2 className="noproducts-text">No Products</h2>
                </Grid>
              </Grid>
            )}
          </Grid>

          {lazyLoading && this.loadSpinner()}

          {this.state.dialougeBoxOpen && (
            <SomethingWrongAlert
              dialougeBoxOpen={this.state.dialougeBoxOpen}
              updateDialogState={this.updateDialogState}
            />
          )}

          <Dialog
            open={showProductDetail}
            onClose={() => this.setState({ showProductDetail: false })}
            maxWidth={false}
            scroll={"body"}
            classes={{
              paper: "modal-big holiday-shop-modal modal-paper",
              root: "modal-stack-top",
            }}
          >
            <DialogContent>
              <Button
                className="icn-close"
                id="productDetailClose"
                aria-label="productDetail Close"
                onClick={() => this.setState({ showProductDetail: false })}
              >
                <i className="fa fa-times"></i>
              </Button>
              <HolidayShopDetail
                productData={productInfo}
                selectedCartItems={selectedItems}
                warningMsgFunc={this.warningMsgFunc}
              />
            </DialogContent>
          </Dialog>
        </Container>
      </div>
    );
  }
}
