import { Container, Grid, Snackbar, Button } from "@material-ui/core";
import MuiAlert from "@material-ui/lab/Alert";
import React from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import {
  actionCreateCustomer,
  actionGetCustomer,
} from "../../../actions/ProductAction";
import OrderSummary from "../OrderSummary";
import Payment from "../Payment";
import Registration from "./Registration";
import ThankYouMessage from "../ThankYou";
import Spinner from "../../Spinner";
import CheckoutDetails from "./CheckoutDetails";
import UrlConstants from "../../../config/UrlConstants";
import APIUtil from "../../../config/APIUtil";
import PropTypes from "prop-types";
import { trackEvent, EventNames } from "../../../appinsights/EventTrack";
import { DisplayHeading } from "../../../config/constants/content.constant";
import {
  ViewedPage,
  ClickedButton,
} from "../../../config/amplitude/SnacksAmplitude";
import {
  PAGENAME,
  BUTTONNAME,
} from "../../../config/amplitude/Taxonomy.constants";
import { shippingCostCalculator } from "../../Utils";
import "./checkout.scss";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`checkout-tabpanel-${index}`}
      aria-labelledby={`checkout-tabpanel-${index}`}
      {...other}
    >
      {value === index && <div>{children}</div>}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

class check extends React.Component {
  constructor(props) {
    super(props);
    this.checkoutMode = sessionStorage.getItem("checkoutMode");
    if (localStorage.getItem("token") !== null) {
      this.checkoutMode = null;
    }
    this.header = <div />;
    this.state = {
      fromForgot: false,
      spinner: false,
      isPayment:
        sessionStorage.getItem("isPayment") === null
          ? 0
          : parseInt(sessionStorage.getItem("isPayment")),
      keyData: 0,
      showLogin:
        this.checkoutMode === null
          ? localStorage.getItem("token") === null
            ? true
            : false
          : this.checkoutMode === "Login"
          ? true
          : false,
      showRegister:
        this.checkoutMode !== null && this.checkoutMode === "Register"
          ? true
          : localStorage.getItem("token") === null
          ? false
          : true,
      showGuest:
        this.checkoutMode !== null && this.checkoutMode === "Guest"
          ? true
          : localStorage.getItem("token") === null
          ? true
          : false,
      //  loggedinReg: false,
      errorMessage: false,
      summuryTaxData: {},
      isSummuryLoader: false,
      userInfo: this.props.customerDetails, //this.props.user
      billingSameAsShipping: false,
      error: "",
      emailValid: true,
      tabIndex: localStorage.getItem("token") !== null ? 1 : 0,
      hasMinTotal: true,
      cartItems: JSON.parse(sessionStorage.getItem("cartItems"))
        ? JSON.parse(sessionStorage.getItem("cartItems"))
        : [],
      disableCheckout: false,
      header: () => this.getHeader(),
      callSubmit: false,

      shippingFeeApiResponse: {},
      isShippingFeeApiLoading: true,
    };
    sessionStorage.setItem("newUserCheckout", false);
    this.shippingFeeAPI();
  }

  componentDidMount() {
    if (sessionStorage.getItem("cartItems") !== null)
      trackEvent(
        EventNames.Action.ACTION_CART_ON_CHECKOUT,
        EventNames.Event.EVENT_BUTTON_PRESS,
        window.location.origin,
        window.location.pathname,
        {
          cart: JSON.parse(sessionStorage.getItem("cartItems")),
          userLoggedIn: localStorage.getItem("token") !== null ? true : false,
        }
      );
    // var that = this;
    setTimeout(function () {
      window.scrollTo(0, 0);
    }, 10);
    this.getUserDetails();

    if (this.checkoutMode !== null) {
      if (localStorage.getItem("token") === null) this.handleChange(null, 2);
      else
        this.handleChange(
          null,
          this.checkoutMode === "Login"
            ? 0
            : this.checkoutMode === "Register"
            ? 1
            : 2
        );
    } else {
      if (
        localStorage.getItem("token") !== null &&
        this.props.customerDetails &&
        this.props.customerDetails !== null &&
        (this.props.customerDetails.accountAddresses === null ||
          this.props.customerDetails.accountAddresses.length === 0)
      ) {
        this.handleChange(null, 1);
      }
    }
    if (sessionStorage.getItem("errorPaymentMsg")) {
      this.setState(
        {
          errorMessage: true,
          error: sessionStorage.getItem("errorPaymentMsg"),
        },
        () => document.getElementById("body").classList.add("has-toast")
      );
    }
    sessionStorage.removeItem("errorPaymentMsg");
    var totalPrice = 0;
    if (!!this.props.minPrice && Object.keys(this.props.minPrice).length > 0) {
      if (this.state.cartItems.length > 0) {
        this.state.cartItems.map((items) => {
          var total = items.price * items.quantity;
          totalPrice = totalPrice + total;
          return true;
        });
      }
      if (totalPrice < this.props.minPrice.shippingMinimum) {
        this.setState(
          {
            hasMinTotal: false,
            errorMessage: true,
            error:
              "$" +
              parseFloat(this.props.minPrice.shippingMinimum).toFixed(2) +
              " " +
              "Minimum Order Requirement.",
          },
          () => {
            document.getElementById("body").classList.add("has-toast");
          }
        );
      } else {
        this.setState({
          hasMinTotal: true,
        });
      }
    }
    if (localStorage.getItem("token") !== null) {
      ViewedPage(PAGENAME.CHECKOUT);
    } else {
      ViewedPage(PAGENAME.GUESTCHECKOUT);
    }
  }

  componentWillUnmount() {
    sessionStorage.setItem(
      "checkoutMode",
      this.state.showLogin
        ? "Login"
        : this.state.showRegister
        ? "Register"
        : "Guest"
    );
    // if (this.state.isPayment === 0) {
    sessionStorage.removeItem("isPayment");
  }

  shippingFeeAPI = () => {
    APIUtil.getMethod(UrlConstants.shippingFee, true)
      .then((response) => {
        this.setState(
          {
            shippingFeeApiResponse: response.data.data || {},
            isShippingFeeApiLoading: false,
          },
          () => this.calculateShippingFee(this.state.shippingFeeApiResponse)
        );
      })
      .catch((error) => {
        console.error("Shipping Fee API failed", error);
        this.setState({
          isShippingFeeApiLoading: false,
        });
      });
  };

  calculateShippingFee = (shippingResponse) => {
    const { shippingName, shippingTax } = shippingResponse;
    const discount = sessionStorage.getItem("discount");
    const cartItems = sessionStorage.getItem("cartItems")
      ? JSON.parse(sessionStorage.getItem("cartItems"))
      : [];
    let totalPrice = 0;
    if (cartItems.length > 0) {
      cartItems.map((items) => {
        let total = items.price * items.quantity;
        return (totalPrice = totalPrice + total);
      });
    }

    let totalAmount =
      discount && discount > 0 ? totalPrice - discount : totalPrice;
    const isMultipackExist = cartItems.filter((item) => item.multipack);
    sessionStorage.removeItem("shippingSku");
    sessionStorage.removeItem("shippingSkuCost");
    sessionStorage.setItem("shippingSkuName", shippingName);

    shippingCostCalculator({
      totalAmount,
      isMultipackExist,
      shippingTax: shippingTax || [],
    });
  };

  getUserDetails = () => {
    if (
      localStorage.getItem("token") !== null &&
      localStorage.getItem("accessToken")
    ) {
      this.props.actionGetCustomer();
    }
  };

  getHeader() {
    let pageTitle = DisplayHeading.GUEST_CHECKOUT;
    let pageName = PAGENAME.GUESTCHECKOUT;
    if (this.state.isPayment === 1) {
      pageTitle = DisplayHeading.PAYMENT_PAGE;
      pageName = PAGENAME.PAYMENT;
    } else if (localStorage.getItem("token") !== null) {
      pageTitle = DisplayHeading.CHECKOUT;
      pageName = PAGENAME.CHECKOUT;
    }

    return (
      <div className="wizard-step sticky-head-wrapper">
        <div className="card-summary-page-heading sticky-head has-button has-button-mobile">
          <div className="sticky-head-inner">
            <h1 className="sticky-title" aria-label={"checkoutHeading"}>
              {pageTitle}
            </h1>

            {this.state.isPayment !== 1 && (
              <div className="button-group">
                <Button
                  disabled={this.state.disableCheckout}
                  onClick={() => {
                    this.setState({
                      callSubmit: true,
                    });
                    ClickedButton(BUTTONNAME.CONTINUETOPAYMENT, pageName);
                  }}
                  className="btn-primary show-web"
                  id="ContinueToPaymentSigned"
                  aria-label={DisplayHeading.CONTINUE_TO_PAYMENT}
                >
                  {DisplayHeading.CONTINUE_TO_PAYMENT}
                </Button>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }

  emailValidate = (fields, callContact) => {
    let emailPostData = {
      email: fields.email,
    };
    let url = UrlConstants.EmailValidate;
    this.setState({ spinner: false, error: "", errorMessage: false });
    APIUtil.postMethod(url, emailPostData, true)
      .then((response) => {
        if (response.status === 200 && response.data.success) {
          if (callContact) this.createCustomer(fields, false);
          this.setState(
            {
              error: "",
              spinner: false,
              errorMessage: false,
              emailValid: true,
            },
            () => document.getElementById("body").classList.remove("has-toast")
          );
        } else {
          this.setState(
            {
              error:
                response.data.message !== null ? response.data.message : "",
              spinner: false,
              errorMessage: true,
              emailValid: false,
            },
            () => document.getElementById("body").classList.add("has-toast")
          );
        }
      })
      .catch((err) => {
        this.setState({
          error: err.message,
          spinner: false,
          errorMessage: true,
          emailValid: false,
        });
      });
  };

  createCustomer(fileds, isForFormSubmit, fromUpdateContact) {
    let isForCustomer =
      localStorage.getItem("accountId") === null || isForFormSubmit === false;
    this.setState({
      spinner: isForCustomer && !fromUpdateContact ? false : true,
      error: "",
      errorMessage: false,
    });
    this.props
      .actionCreateCustomer(fileds, this.state.showGuest, isForFormSubmit)
      .then((res) => {
        if (this.props.createCustomerDetail.success === true) {
          this.setState(
            { spinner: false, errorMessage: false, error: "" },
            () => {
              if (!isForCustomer) {
                this.showPayment();
              } else if (
                this.props.createCustomerDetail.data.id &&
                this.props.createCustomerDetail.data.accountContacts
              ) {
                localStorage.setItem(
                  "accountId",
                  this.props.createCustomerDetail.data.id
                );
                if (
                  this.props.createCustomerDetail.data.accountContacts.length >
                  0
                ) {
                  localStorage.setItem(
                    "contactId",
                    this.props.createCustomerDetail.data.accountContacts[0].id
                  );
                }
                if (
                  this.props.createCustomerDetail.data.accountAddresses !== null
                ) {
                  this.props.createCustomerDetail.data.accountAddresses.map(
                    (item) => {
                      if (item.addressType === "SHIPPING_ADDRESS")
                        localStorage.setItem("shippingId", item.id);
                      else if (item.addressType === "BILLING_ADDRESS")
                        localStorage.setItem("billingId", item.id);
                    }
                  );
                }
                if (this.state.showGuest) {
                  this.showPayment();
                }
              } else if (fromUpdateContact) {
                trackEvent(
                  EventNames.Action.ACTION_UPDATE_CUSTOMER,
                  EventNames.Event.EVENT_BUTTON_PRESS,
                  window.location.origin,
                  window.location.pathname,
                  fileds
                );
                localStorage.removeItem("accountId");
                localStorage.removeItem("contactId");
                this.getUserDetails();
              }
            }
          );
          this.setState({
            spinner: false,
          });
        } else {
          this.setState({
            errorMessage: true,
            spinner: false,
            error: this.props.createCustomerDetail.message,
          });
        }
      })
      .catch((productData) => {
        this.setState({ spinner: false });
      });
  }

  taxCalculation = (price) => {
    let taxPercent = 8.5; // static tax(in %)
    return Number((price * taxPercent) / 100);
  };

  showLoginAccordion() {
    sessionStorage.setItem("newUserCheckout", false);
    if (!this.state.showLogin) {
      localStorage.removeItem("locationId");
      localStorage.removeItem("shippingId");
      localStorage.removeItem("billingId");
    }
    this.setState(
      {
        showLogin: true,
        showRegister: false,
        showGuest: false,
        errorMessage: false,
      },
      () => document.getElementById("body").classList.remove("has-toast")
    );
  }

  showRegisterAccordion() {
    sessionStorage.setItem("newUserCheckout", false);
    if (!this.state.showRegister) {
      localStorage.removeItem("locationId");
      localStorage.removeItem("shippingId");
      localStorage.removeItem("billingId");
    }
    sessionStorage.setItem("billingInvalid", "no");
    sessionStorage.setItem("shippingInvalid", "no");
    this.setState({
      showRegister: true,
      showLogin: false,
      showGuest: false,
    });
  }

  showGuestAccordion() {
    sessionStorage.setItem("newUserCheckout", false);
    if (!this.state.showGuest) {
      localStorage.removeItem("locationId");
      localStorage.removeItem("shippingId");
      localStorage.removeItem("billingId");
    }
    this.setState({
      showRegister: false,
      showLogin: false,
      showGuest: true,
    });
  }

  showPayment() {
    window.scrollTo(0, 0);
    this.setState(
      {
        isPayment: 1,
        header: null,
        callSubmit: false,
      },
      () => sessionStorage.setItem("isPayment", 1)
    );
  }

  goToLogin() {
    this.setState(
      {
        isPayment: 0,
      },
      () => sessionStorage.removeItem("isPayment")
    );
  }

  OrderPlaced() {
    this.setState(
      {
        isPayment: 2,
      },
      () => {
        sessionStorage.setItem("isPayment", 2);
        this.props.isThanku();
      }
    );
  }

  getFields(fields) {
    this.setState({ formData: fields });
  }

  handleClose = () => {
    this.setState({ errorMessage: false, error: "" });
    document.getElementById("body").classList.remove("has-toast");
  };

  onLoginSuccess = (response) => {
    this.getUserDetails();
  };

  handleChange = (event, newValue) => {
    if (newValue === 0) this.showLoginAccordion();
    else if (newValue === 1) {
      this.showRegisterAccordion();
      if (this.state.fromForgot) {
        this.setState({
          fromForgot: false,
        });
      }
    } else if (newValue === 2) {
      this.showGuestAccordion();
      if (this.state.fromForgot) {
        this.setState({
          fromForgot: false,
        });
      }
    }

    this.setState({
      tabIndex: newValue,
      errorMessage: false,
      error: "",
    });
    sessionStorage.setItem("chk_tabIndex", parseInt(newValue));
  };

  makeHref() {
    this.setState({
      fromForgot: true,
    });
    this.handleChange(null, 0);
  }

  render() {
    var link = (
      <a href={this.makeHref} onClick={() => this.makeHref()}>
        <u>click here</u>
      </a>
    );
    return (
      <div className="content-main">
        {this.state.header === null ? (
          <div className="wizard-step sticky-head-wrapper">
            <div className="thankyou-page-heading sticky-head">
              <div className="sticky-head-inner">
                <h1 className="sticky-title">PAYMENT</h1>
              </div>
            </div>
          </div>
        ) : (
          <this.state.header />
        )}
        {this.state.spinner ? <Spinner /> : ""}
        <Container className="container-root checkout-container">
          {this.state.isPayment === 0 ? (
            <div>
              <Grid container spacing={1}>
                {localStorage.getItem("token") === null ||
                localStorage.getItem("token").length === 0 ||
                (localStorage.getItem("token") !== null &&
                  this.props.customerDetails &&
                  this.props.customerDetails !== null &&
                  (this.props.customerDetails.accountAddresses === null ||
                    this.props.customerDetails.accountAddresses.length ===
                      0)) ? (
                  <Grid
                    item
                    xs={12}
                    sm={8}
                    className="checkout-form-wrapper registration-col tab-col"
                  >
                    {/* <TabPanel value={this.state.tabIndex} index={0}>
                      <div className="checkout-content-body">
                        <div className="checkout-content-body-inner">
                          <Login
                            changeForgotToLogin={() =>
                              this.setState({
                                fromForgot: false,
                              })
                            }
                            closeLoginDialog={() => {}}
                            fromForgot={this.state.fromForgot}
                            loginClicked={(response) =>
                              this.onLoginSuccess(response)
                            }
                          />
                        </div>
                      </div>
                    </TabPanel> */}
                    <TabPanel value={this.state.tabIndex} index={1}>
                      <div className="checkout-content-body">
                        <Registration
                          shouldDisable={(result) => {
                            if (result !== this.state.disableCheckout)
                              this.setState({ disableCheckout: result });
                          }}
                          updateSpinner={(val) =>
                            this.setState({
                              spinner: val,
                            })
                          }
                          callSubmit={this.state.callSubmit}
                          hasMinTotal={this.state.hasMinTotal}
                          emailValid={this.state.emailValid}
                          guestCheckout={this.state.showGuest}
                          userInfo={this.props.customerDetails}
                          emailValidate={(email, callContact, fields) =>
                            this.emailValidate(email, callContact, fields)
                          }
                          zipcodeValidData={this.props.zipcodeValidData}
                          moveToPrevStepCheckout={() =>
                            this.props.moveToPrevStep()
                          }
                          getTaxtLoader={(data, summuryData) =>
                            this.setState({
                              isSummuryLoader: data,
                              summuryTaxData: summuryData,
                            })
                          }
                          createCustomer={(fileds, isForFormSubmit) => {
                            if (
                              this.props.customerDetails &&
                              this.props.customerDetails.accountContacts
                            ) {
                              localStorage.setItem(
                                "accountId",
                                this.props.customerDetails.id
                              );
                              if (
                                this.props.customerDetails.accountContacts
                                  .length > 0
                              ) {
                                localStorage.setItem(
                                  "contactId",
                                  this.props.customerDetails.accountContacts[0]
                                    .id
                                );
                              }
                            }
                            this.createCustomer(fileds, isForFormSubmit, false);
                          }}
                          spinner={this.state.spinner}
                          createAccount={(fileds) => {
                            localStorage.setItem(
                              "accountId",
                              this.props.customerDetails.id
                            );
                            if (
                              this.props.customerDetails.accountContacts
                                .length > 0
                            ) {
                              localStorage.setItem(
                                "contactId",
                                this.props.customerDetails.accountContacts[0].id
                              );
                            }
                            this.createCustomer(fileds, false, true);
                          }}
                        />
                      </div>
                    </TabPanel>
                    <TabPanel value={this.state.tabIndex} index={2}>
                      <div className="checkout-content-body">
                        <Registration
                          shouldDisable={(result) => {
                            if (result !== this.state.disableCheckout)
                              this.setState({ disableCheckout: result });
                          }}
                          updateSpinner={(val) =>
                            this.setState({
                              spinner: val,
                            })
                          }
                          callSubmit={this.state.callSubmit}
                          hasMinTotal={this.state.hasMinTotal}
                          emailValid={this.state.emailValid}
                          userInfo={this.props.customerDetails}
                          guestCheckout={true}
                          emailValidate={(email) => this.emailValidate(email)}
                          zipcodeValidData={this.props.zipcodeValidData}
                          moveToPrevStepCheckout={() =>
                            this.props.moveToPrevStep()
                          }
                          getTaxtLoader={(data, summuryData) =>
                            this.setState({
                              isSummuryLoader: data,
                              summuryTaxData: summuryData,
                            })
                          }
                          createAccount={(fileds) => {
                            localStorage.setItem(
                              "accountId",
                              this.props.customerDetails.id
                            );
                            if (
                              this.props.customerDetails.accountContacts
                                .length > 0
                            ) {
                              localStorage.setItem(
                                "contactId",
                                this.props.customerDetails.accountContacts[0].id
                              );
                            }
                            this.createCustomer(fileds, false, true);
                          }}
                          createCustomer={(fileds, isForFormSubmit) => {
                            if (
                              this.props.customerDetails &&
                              this.props.customerDetails.accountContacts
                            ) {
                              localStorage.setItem(
                                "accountId",
                                this.props.customerDetails.id
                              );
                              if (
                                this.props.customerDetails.accountContacts
                                  .length > 0
                              ) {
                                localStorage.setItem(
                                  "contactId",
                                  this.props.customerDetails.accountContacts[0]
                                    .id
                                );
                              }
                            }
                            this.createCustomer(fileds, isForFormSubmit, false);
                          }}
                          spinner={this.state.spinner}
                        />
                      </div>
                    </TabPanel>
                    <Snackbar
                      open={this.state.errorMessage}
                      anchorOrigin={{
                        vertical: "top",
                        horizontal: "center",
                      }}
                      className="snackbar snackbar-full"
                    >
                      {this.state.error != null &&
                      this.state.error.length > 0 &&
                      this.state.error.includes(
                        "is already a registered user"
                      ) ? (
                        <MuiAlert
                          variant="filled"
                          onClose={this.handleClose}
                          severity="error"
                        >
                          {this.state.error}
                          {" Please"} {link} {" for the assistance"}
                        </MuiAlert>
                      ) : (
                        <MuiAlert
                          variant="filled"
                          onClose={this.handleClose}
                          severity="error"
                        >
                          {this.state.error != null &&
                          this.state.error.length > 0
                            ? this.state.error
                            : "Some Technical Issue"}
                        </MuiAlert>
                      )}
                    </Snackbar>

                    {this.state.isPayment === 2 ? (
                      <ThankYouMessage
                        summuryTaxData={this.state.summuryTaxData}
                        isSummuryLoader={this.state.isSummuryLoader}
                        shippingCharges={this.props.shippingCharges}
                        taxCalculation={this.taxCalculation}
                      />
                    ) : (
                      ""
                    )}
                  </Grid>
                ) : (
                  <Grid
                    item
                    xs={12}
                    sm={8}
                    className="checkout-form-wrapper registration-col tab-col"
                  >
                    <div className={"checkout-content-wrapper"}>
                      {!this.state.isShippingFeeApiLoading &&
                        this.props.customerDetails && (
                          <CheckoutDetails
                            shouldDisable={(result) => {
                              if (result !== this.state.disableCheckout)
                                this.setState({ disableCheckout: result });
                            }}
                            callSubmit={this.state.callSubmit}
                            hasMinTotal={this.state.hasMinTotal}
                            getCustomer={() => this.props.actionGetCustomer()}
                            userInfo={this.props.customerDetails}
                            guestCheckout={this.state.showGuest}
                            getTaxtLoader={(data, summuryData) =>
                              this.setState({
                                isSummuryLoader: data,
                                summuryTaxData: summuryData,
                              })
                            }
                            spinner={this.state.spinner}
                            showPayment={() => this.showPayment()}
                            createCustomer={(fileds) =>
                              this.createCustomer(fileds, false, true)
                            }
                          />
                        )}
                    </div>
                    <Snackbar
                      open={this.state.errorMessage}
                      anchorOrigin={{
                        vertical: "top",
                        horizontal: "center",
                      }}
                      className="snackbar snackbar-full"
                    >
                      <MuiAlert
                        variant="filled"
                        onClose={this.handleClose}
                        severity="error"
                      >
                        {this.state.error !== null &&
                        this.state.error.length > 0
                          ? this.state.error
                          : "Some Technical Issue"}
                      </MuiAlert>
                    </Snackbar>
                  </Grid>
                )}

                {/* order summary */}
                <Grid item xs={12} sm={4} className="summary-col">
                  <OrderSummary
                    isThankYou={false}
                    onlyTotal={false}
                    summuryTaxData={this.state.summuryTaxData}
                    isSummuryLoader={this.state.isSummuryLoader}
                    shippingCharges={this.props.shippingCharges}
                    onCheckoutTap={() =>
                      this.setState({
                        callSubmit: true,
                      })
                    }
                    disableCheckout={this.state.disableCheckout}
                    checkoutText={DisplayHeading.CONTINUE_TO_PAYMENT}
                    guestCheckout={this.state.showGuest}
                    isShippingFeeApiLoading={this.state.isShippingFeeApiLoading}
                  />
                </Grid>
              </Grid>
            </div>
          ) : (
            ""
          )}

          <Grid container spacing={2}>
            {this.state.isPayment === 1 ? (
              <Grid item xs={12} className="payment-grid">
                {/* Payment */}
                <Payment
                  onRef={(ref) => (this.payment = ref)}
                  payload={(e) => {
                    this.setState({ payload: e });
                  }}
                  fields={this.state.formData}
                  showPayment={() => this.showPayment()}
                  goToLogin={() => this.goToLogin()}
                  OrderPlaced={() => this.OrderPlaced()}
                  summuryTaxData={this.state.summuryTaxData}
                  isSummuryLoader={this.state.isSummuryLoader}
                  shippingCharges={this.props.shippingCharges}
                  taxCalculation={this.taxCalculation}
                  moveToPrevStep={() => this.props.moveToPrevStep()}
                />
              </Grid>
            ) : (
              ""
            )}
          </Grid>
        </Container>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    submitResponse: state.reducer.submit,
    createCustomerDetail: state.reducer.createCustomer,
    isAuthenticated: state.reducer.isAuthenticated,
    user: state.reducer.loginResponse.user,
    customerDetails: state.reducer.customerDetails,
    minPrice: state.reducer.minAmount,
  };
};

export default connect(mapStateToProps, {
  actionCreateCustomer,
  actionGetCustomer,
})(withRouter(check));
