import React, { Component } from "react";
import { Grid, Button, Card, Tooltip, IconButton } from "@material-ui/core";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import Truncate from "react-truncate";
import QuantityCount from "../commonComponents/quantityCount/QuantityCount";
import { PAGENAME } from "../../config/amplitude/Taxonomy.constants";
import "./multipack-tout.scss";

export default class MultipackTout extends Component {
  openDetailView = () => {
    const { detailedInfo, productList, index } = this.props;
    detailedInfo(productList, index);
  };

  render() {
    const {
      index,
      productList,
      multipackLimit,
      horizontal,
      warningMsgFunc,
      mainHeading,
    } = this.props;
    const {
      image,
      productDescription,
      shortDescription,
      productId,
      // brand,
      availableQty,
    } = productList;
    const keyValue = `${productId}-${index}`;

    const description = shortDescription || productDescription;

    const productImage =
      image && typeof image === "string" ? image : image.Product_Image;

    return (
      <Grid
        item
        xs={horizontal ? 12 : 6}
        lg={horizontal ? 12 : 3}
        sm={horizontal ? 12 : 3}
        key={keyValue}
      >
        <Card
          className={`product-box-bundle-card bundle-card multipack-tout ${
            horizontal ? "horizontal-view" : ""
          }`}
        >
          {!horizontal && (
            <IconButton
              className="more-vert"
              aria-label={`click here to open variety pack product details`}
              onClick={this.openDetailView}
            >
              <MoreVertIcon />
            </IconButton>
          )}
          <div className={`product-content-wrapper`}>
            <Button
              className="media-card-wrapper"
              onClick={this.openDetailView}
            >
              <img alt={`Multipack-${productId}`} src={productImage} />
            </Button>
            <div className="product-information">
              {/* {!horizontal && <div className="brand">{brand}</div>} */}
              <Tooltip placement="top" title={description}>
                <div className="product-heading">
                  <Truncate lines={2} ellipsis="...">
                    {description}
                  </Truncate>
                </div>
              </Tooltip>
              <div className="product-action">
                <QuantityCount
                  productInfo={productList}
                  availableQty={availableQty}
                  warningMsgFunc={warningMsgFunc}
                  multipack
                  multipackLimit={multipackLimit}
                  PageTitle={PAGENAME.VARIETY}
                  mainHeading={mainHeading}
                />
              </div>
            </div>
          </div>
        </Card>
      </Grid>
    );
  }
}
