import React, { Component } from "react";
import {
  Grid,
  FormControl,
  InputLabel,
  Select,
  CircularProgress,
  Button,
  Dialog,
  DialogContent,
} from "@material-ui/core";
import QuantityCount from "../../commonComponents/quantityCount/QuantityCount";
import ImageGallery from "react-image-gallery";
import "react-image-gallery/styles/scss/image-gallery.scss";
import "./holiday-shop-detail.scss";
import APIUtil from "../../../config/APIUtil";
import UrlConstants from "../../../config/UrlConstants";
import { EventNames, trackEvent } from "../../../appinsights/EventTrack";
import { DisplayHeading } from "./../../../config/constants/content.constant";
import {
  ViewedPage,
  ClickedButton,
} from "../../../config/amplitude/SnacksAmplitude";
import {
  PAGENAME,
  PRODUCTTYPE,
  BUTTONNAME,
} from "../../../config/amplitude/Taxonomy.constants";

class HolidayShopDetail extends Component {
  state = {
    productDetail: [],
    isDetailLoading: true,
    productGallery: [],
    productGroupDetail: "",
    defaultGroupProduct: "",
    isProductGroupUpdated: true,
    showSizeChart: false,
  };

  componentDidMount() {
    ViewedPage(PAGENAME.HOLIDAYDETAIL);
    const { productData } = this.props;
    const primaryIndex = productData.findIndex((obj) => obj.primaryProduct);
    if (productData && productData[primaryIndex]) {
      trackEvent(
        EventNames.Action.ACTION_HOLIDAYSHOP_PRODUCT_DETAIL,
        EventNames.Event.EVENT_PAGE_VIEW,
        window.location.origin,
        window.location.pathname,
        { product: productData[primaryIndex] }
      );
      const payload = {
        prodId: productData[primaryIndex].id,
        locationNumber: sessionStorage.getItem("_lo_No") || null,
      };
      APIUtil.postMethod(UrlConstants.holidayShopProductDetail, payload, true)
        .then((response) => {
          let responseData = response.data.data;
          let images = [];
          if (responseData[0].image.length > 0) {
            responseData[0].image.forEach((img) => {
              images.push({
                original: img,
                thumbnail: img,
              });
            });
          }
          this.filterOutofStockProducts(responseData[0]);
          this.setState({
            productDetail: responseData[0] || [],
            isDetailLoading: false,
            productGallery: images,
          });
        })
        .catch((error) => console.error("Multipack detail API", error));
    }
  }

  filterOutofStockProducts(productGroupData) {
    if (!!productGroupData.Groups && productGroupData.Groups.length > 0) {
      const proInfo = productGroupData.Groups.filter(
        (item) => item.availableQty && item.availableQty > 0
      );
      this.setState({
        productGroupDetail: proInfo,
        defaultGroupProduct: proInfo[0],
      });
    }
  }

  handleChange(value) {
    /* trackEvent(
      EventNames.Action.ACTION_HOLIDAYSHOP_PRODUCT_SEARCH,
      EventNames.Event.EVENT_DROPDOWN_CHANGE,
      window.location.origin,
      window.location.pathname,
      { filterBy: value }
    ); */
    this.setState({ isProductGroupUpdated: false }, () => {
      const proInfo = this.state.productDetail.Groups.filter(
        (item) => item.productId == value
      );
      this.setState({
        defaultGroupProduct: proInfo[0],
        isProductGroupUpdated: true,
      });
    });
  }

  getSizeList = (productData) => {
    /* const { warningMsgFunc, productData, selectedItems } = this.props;
    const primaryIndex = productData.findIndex((obj) => obj.primaryProduct); */

    const proInfo = productData.filter(
      (item) => item.availableQty && item.availableQty > 0
    );
    return (
      <Select
        native
        inputProps={{
          name: "size",
          id: "size",
        }}
        onChange={(e) => this.handleChange(e.target.value)}
      >
        {proInfo.map((product, index) => (
          <option key={index} value={product.productId}>
            {product.size}
          </option>
        ))}
      </Select>
    );
  };

  showSizeChartPopup = (sizeChartImage) => {
    return (
      <Dialog
        open={this.state.showSizeChart}
        onClose={() => this.setState({ showSizeChart: false })}
        maxWidth={false}
        scroll={"body"}
        classes={{
          paper: "modal-big modal-size-chart modal-paper",
          root: "modal-stack-top",
        }}
      >
        <DialogContent>
          <img src={sizeChartImage} alt="size chart" />
          <Button
            className="icn-close"
            id="closeModal"
            aria-label="close Size Chart"
            onClick={() => this.setState({ showSizeChart: false })}
          >
            <i className="fa fa-times"></i>
          </Button>
        </DialogContent>
      </Dialog>
    );
  };

  render() {
    const {
      productDetail,
      isDetailLoading,
      productGallery,
      productGroupDetail,
      defaultGroupProduct,
    } = this.state;
    const { productData, selectedItems, warningMsgFunc } = this.props;

    const productDetailInfo = {
      ...productData[0],
      isHolidayShopProduct: true,
    };
    if (isDetailLoading) {
      return (
        <Grid
          container
          justify="center"
          style={{
            minHeight: "100px",
            textAlign: "center",
            paddingTop: "25px",
          }}
        >
          <Grid item>
            <CircularProgress />
          </Grid>
        </Grid>
      );
    }
    if (productDetail && productDetail.length === 0) {
      return (
        <Grid container justify="center">
          <Grid item>
            <h1 className="noproducts-text">No Product details found</h1>
          </Grid>
        </Grid>
      );
    }

    return (
      <>
        <Grid container className="">
          <Grid item className="holiday-shop-slider">
            <ImageGallery
              items={productGallery}
              showPlayButton={false}
              showFullscreenButton={false}
              showBullets={false}
              showNav={false}
            />

            {!!productDetail.chartImage && productDetail.chartImage.length > 0 && (
              <>
                <Button
                  className="btn-link"
                  id="sizeChart"
                  aria-label="Size Chart"
                  onClick={() => {
                    this.setState({ showSizeChart: true });
                    ClickedButton(BUTTONNAME.SIZECHART, PAGENAME.HOLIDAYDETAIL);
                  }}
                >
                  Size Chart
                </Button>
                {this.showSizeChartPopup(productDetail.chartImage[0])}
              </>
            )}
          </Grid>
          <Grid item xs className="holiday-shop-content">
            <h1>{productDetail.title}</h1>
            <p>{productDetail.description}</p>

            {defaultGroupProduct.price && (
              <div className="price">
                <sup>{productDetail.currency}</sup>
                {defaultGroupProduct.price}
              </div>
            )}
            {productGroupDetail.length > 0 &&
            productDetailInfo.flavour === "" ? (
              <Grid container className="holiday-shop-size">
                <Grid item>
                  <FormControl variant="filled" className="size-dropdown-root">
                    <InputLabel htmlFor="size">size</InputLabel>
                    {this.getSizeList(productGroupDetail)}
                  </FormControl>
                </Grid>
                <Grid item xs>
                  {this.state.isProductGroupUpdated && (
                    <QuantityCount
                      productInfo={defaultGroupProduct}
                      availableQty={defaultGroupProduct.availableQty}
                      primaryProduct={productDetailInfo}
                      warningMsgFunc={warningMsgFunc}
                      selectedItems={selectedItems}
                      currency={productDetail.currency}
                      PageTitle={PAGENAME.HOLIDAYDETAIL}
                      ProductType={PRODUCTTYPE.HOLIDAY}
                    />
                  )}
                </Grid>
              </Grid>
            ) : (
              <div className="product-detail-actions">
                <div className="out-of-stock">
                  {!productDetailInfo.flavour === ""
                    ? DisplayHeading.OUT_OF_STOCK
                    : sessionStorage.getItem("holidayShopStockText")}
                </div>
              </div>
            )}

            {/* {productDetail.description ? (
              <Grid container>
                <Grid item xs={12}>
                  <Accordion className="holiday-accordion-wrapper">
                    <AccordionSummary
                      expandIcon={
                        <ExpandMoreIcon className="expand-more-icon" />
                      }
                      aria-controls={`holiday-shop-content`}
                      id={`holiday-shop-header`}
                      className="holiday-accordion-header"
                    >
                      <Typography variant="h2">Details</Typography>
                    </AccordionSummary>
                    <AccordionDetails className="holiday-accordion-body">
                      {productDetail.description}
                    </AccordionDetails>
                  </Accordion>
                </Grid>
              </Grid>
            ) : (
              ""
            )} */}
          </Grid>
        </Grid>
      </>
    );
  }
}
export default HolidayShopDetail;
