import React from "react";
import {
  Container,
  Snackbar,
  Tabs,
  Tab,
  RadioGroup,
  Button,
  ClickAwayListener,
  MenuList,
  MenuItem,
} from "@material-ui/core";
import MuiAlert from "@material-ui/lab/Alert";
import ViewContact from "./ProfileComponents/ViewContact";
import EditContact from "./ProfileComponents/EditContact";
import ViewBilling from "./ProfileComponents/ViewBilling";
import EditBilling from "./ProfileComponents/EditBilling";
import ViewShipping from "./ProfileComponents/ViewShipping";
import EditShipping from "./ProfileComponents/EditShipping";
import ChangePassword from "./ProfileComponents/ChangePassword";
import OrderHistory from "./ProfileComponents/OrderHistory";
import { withRouter } from "react-router-dom";
import Spinner from "../Spinner";
import PropTypes from "prop-types";
import MasterDataCommon from "./MasterDataCommon";
import { connect } from "react-redux";
import * as types from "../../actions/actions";
import ErrorBoundry from "./../ErrorBoundary";
import { trackEvent, EventNames } from "../../appinsights/EventTrack";
import {
  ViewedPage,
  ClickedButton,
} from "../../config/amplitude/SnacksAmplitude";
import {
  PAGENAME,
  BUTTONNAME,
} from "../../config/amplitude/Taxonomy.constants";
import "./myProfile.scss";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`profile-tabpanel-${index}`}
      aria-labelledby={`profile-tabpanel-${index}`}
      {...other}
    >
      {value === index && <div>{children}</div>}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index, tabName) {
  trackEvent(
    index === 0
      ? EventNames.Action.ACTION_TAB_PROFILE
      : index === 2
      ? EventNames.Action.ACTION_TAB_ORDER_HISTORY
      : EventNames.Action.ACTION_TAB_CHANGE_PASSWORD,
    EventNames.Event.EVENT_PAGE_VIEW,
    window.location.origin,
    window.location.pathname
  );

  return {
    id: `profile-tab-${index}`,
    "aria-controls": `profile-tabpanel-${index}`,
    "aria-label": `${tabName}`,
  };
}

class MyProfile extends MasterDataCommon {
  UNSAFE_componentWillMount() {
    if (!localStorage.getItem("accessToken")) {
      this.props.history.push("/home");
    }
  }

  componentDidMount() {
    this.setState({
      origin: window.location.origin,
      pathname: window.location.pathname,
      menuToggle: false,
    });
    this.getCustomerDetails();
    document.addEventListener("keydown", this.hideUserDropdown);
    ViewedPage(PAGENAME.MYPROFILE);
  }

  hideUserDropdown = (event) => {
    // ESC
    if (event.keyCode === 27) this.setState({ menuToggle: false });
  };

  handleClickAway = () => this.setState({ menuToggle: false });

  handleClick = () => this.setState({ menuToggle: !this.state.menuToggle });

  addMultipleShipping() {
    let customerShipping = this.state.customerShipping;

    if (customerShipping && customerShipping.length >= 5) {
      this.getShippingMessage();
      this.setMsg(this.props.shippingMessage.shippingLimitMessage, "error");
      return false;
    }
    let addObj = {
      shippingFirstname: "",
      shippingLastname: "",
      shippingAddressline1: "",
      shippingAddressline2: "",
      shippingState: "",
      shippingCity: "",
      shippingZip: "",
      shippingCountry: this.state.defaultCountry,
      isPrimary: false,
      isViewShipping: false,
    };
    customerShipping.push(addObj);
    this.setState({
      customerShipping: customerShipping,
      isMultiShipDisabled: true,
    });
  }

  handleChange = (event, newValue) => {
    this.setState({ tabIndex: newValue }, () => {
      newValue === 0 && ViewedPage(PAGENAME.MYPROFILE);
      newValue === 1 && ViewedPage(PAGENAME.ORDERHISTORY);
      newValue === 2 && ViewedPage(PAGENAME.CHANGEPASSWORD);
    });
  };

  deleteAddress(index) {
    this.deleteShippingAddress(index);
  }
  addAnotherShipping() {
    ClickedButton(BUTTONNAME.CREATESHIPPINGADD, PAGENAME.MYPROFILE);
    this.addMultipleShipping();
  }
  setPrimaryAddress(values, id) {
    values.isPrimary = true;
    ClickedButton(BUTTONNAME.PRIMARYSHIPPINGADD, PAGENAME.MYPROFILE);
    this.handleShippingSubmit(values, id);
  }

  componentDidUpdate(prevProps, prevState) {
    if (
      prevState.customerDetails !== null &&
      (prevProps.customerDetails === undefined ||
        prevProps.customerDetails === null ||
        (prevState.customerDetails &&
          JSON.stringify(prevState.customerDetails) !==
            JSON.stringify(prevProps.customerDetails)))
    ) {
      this.props.updateCustomerDetails(this.state.customerDetails);
    }
  }

  render() {
    let shippingAddressTxt =
      this.state.customerShipping.length > 0
        ? "ADD ANOTHER SHIPPING ADDRESS"
        : "Add SHIPPING ADDRESS";
    return (
      <div className="wizard-step profile-page">
        {this.state.msg ? (
          <Snackbar
            open={true}
            anchorOrigin={{ vertical: "top", horizontal: "center" }}
            className="snackbar snackbar-full"
          >
            <MuiAlert
              variant="filled"
              onClose={this.handleClose}
              severity={this.state.msgType}
            >
              {this.state.msg}
            </MuiAlert>
          </Snackbar>
        ) : (
          ""
        )}
        {this.state.isSpinner ? <Spinner /> : ""}
        <div className="sticky-head has-button">
          <div className="sticky-head-inner">
            <h1 className="sticky-title">{this.state.title}</h1>
            <ClickAwayListener onClickAway={this.handleClickAway}>
              <div className="profile-dropdown">
                <div className="button-group">
                  <Button
                    onClick={this.handleClick}
                    className={`${
                      this.state.menuToggle ? "open" : ""
                    } btn btn-primary`}
                    id="ProfileMenu"
                    aria-label="Continue Menu"
                  >
                    {this.state.title}
                    <i className="fa fa-angle-down"></i>
                  </Button>
                </div>
                {this.state.menuToggle ? (
                  <MenuList>
                    <MenuItem
                      aria-label="profile"
                      onClick={(event) => {
                        a11yProps(0, "my profile");
                        this.setState(
                          { menuToggle: false, title: "my profile" },
                          () => this.handleChange(event, 0)
                        );
                      }}
                    >
                      my profile
                    </MenuItem>
                    <MenuItem
                      onClick={(event) => {
                        a11yProps(1, "order history");
                        this.setState(
                          { menuToggle: false, title: "order history" },
                          () => this.handleChange(event, 1)
                        );
                      }}
                    >
                      order history
                    </MenuItem>
                    <MenuItem
                      onClick={(event) => {
                        a11yProps(2, "change password");

                        this.setState(
                          { menuToggle: false, title: "change password" },
                          () => this.handleChange(event, 2)
                        );
                      }}
                    >
                      change password
                    </MenuItem>
                  </MenuList>
                ) : null}
              </div>
            </ClickAwayListener>
          </div>
        </div>
        <div>
          <Tabs
            value={this.state.tabIndex}
            onChange={this.handleChange}
            aria-label="My Profile Tabs List"
            variant="scrollable"
            scrollButtons="auto"
            classes={{
              root: "profile-root tabMenu",
              flexContainer: "profile-flexContainer",
              indicator: "profile-indicator",
            }}
          >
            <Tab
              label="my profile"
              {...a11yProps(0, "my profile")}
              onClick={() => this.setState({ title: "my profile" })}
            />
            <Tab
              label="order history"
              {...a11yProps(1, "order history")}
              onClick={() => this.setState({ title: "order history" })}
            />
            <Tab
              label="change password"
              {...a11yProps(2, "change password")}
              onClick={() => this.setState({ title: "change password" })}
            />
          </Tabs>

          <TabPanel value={this.state.tabIndex} index={0}>
            <Container className="container-root">
              {/* fddf */}
              <div>
                {this.state.isViewContact ? (
                  <ViewContact
                    customerDetails={this.state.customerContact}
                    setMessages={(msg, msgtype) => this.setMsg(msg, msgtype)}
                    history={this.props.history}
                    setProfile={(data) => {
                      ClickedButton(
                        BUTTONNAME.EDITCONTACTINFO,
                        PAGENAME.MYPROFILE
                      );
                      this.setState({ isViewContact: data });
                    }}
                  />
                ) : (
                  <EditContact
                    customerDetails={this.state.customerContact}
                    setMessages={(msg, msgtype) => this.setMsg(msg, msgtype)}
                    history={this.props.history}
                    handleSubmit={(values) => this.handleContactSubmit(values)}
                    setProfile={(data) => {
                      ClickedButton(
                        BUTTONNAME.CANCELCONTACTINFO,
                        PAGENAME.MYPROFILE
                      );
                      this.setState({ isViewContact: data });
                    }}
                  />
                )}
              </div>
              {/* //View Billing */}
              <div>
                {this.state.isViewBilling ? (
                  <ViewBilling
                    customerDetails={this.state.customerBilling}
                    setMessages={(msg, msgtype) => this.setMsg(msg, msgtype)}
                    history={this.props.history}
                    setBilling={(data) => {
                      ClickedButton(
                        BUTTONNAME.EDITBILLINGADD,
                        PAGENAME.MYPROFILE
                      );
                      this.setState({ isViewBilling: data });
                    }}
                  />
                ) : (
                  <EditBilling
                    isLoading={(data) => this.setState({ isSpinner: data })}
                    customerDetails={this.state.customerBilling}
                    setMessages={(msg, msgtype) => this.setMsg(msg, msgtype)}
                    history={this.props.history}
                    handleSubmit={(values) =>
                      this.handleBillingSubmit(
                        values,
                        this.state.customerBilling
                      )
                    }
                    setBilling={(data) => {
                      ClickedButton(
                        BUTTONNAME.CANCELBILLINGADD,
                        PAGENAME.MYPROFILE
                      );
                      this.setState({ isViewBilling: data });
                    }}
                  />
                )}
              </div>
              <div>
                {this.state.addShipping ? (
                  <ViewShipping
                    baseData={this.state.customerShipping}
                    customerDetails={this.state.customerShipping}
                    index="0"
                    shippingAddressTxt={shippingAddressTxt}
                    setMessages={(msg, msgtype) => this.setMsg(msg, msgtype)}
                    history={this.props.history}
                    removeShippingAddress={(index) => this.deleteAddress(index)}
                    setPrimaryAddress={(values, id) =>
                      this.setPrimaryAddress(values, id)
                    }
                    addShippingAddress={() => this.addAnotherShipping()}
                    addShipping={this.state.addShipping}
                    setShipping={(data, index) => {
                      ClickedButton(
                        BUTTONNAME.EDITSHIPPINGADD,
                        PAGENAME.MYPROFILE
                      );
                      this.state.customerShipping[index].isViewShipping = data;
                      this.setState({
                        customerShipping: this.state.customerShipping,
                      });
                    }}
                  />
                ) : (
                  ""
                )}
                {this.state.customerShipping &&
                  this.state.customerShipping.map((res, index) => {
                    return (
                      <div key={index}>
                        {!!res.isViewShipping ? (
                          <RadioGroup
                            aria-label={
                              "Shipping Address" +
                              (index === 0 ? "" : index + 1)
                            }
                            name={
                              "ShippingAddress" + (index === 0 ? "" : index + 1)
                            }
                            value={!!res.isPrimary ? res.id : ""}
                          >
                            <ViewShipping
                              baseData={this.state.customerShipping}
                              customerDetails={res}
                              index={index}
                              shippingAddressTxt={shippingAddressTxt}
                              addShipping={this.state.addShipping}
                              setMessages={(msg, msgtype) =>
                                this.setMsg(msg, msgtype)
                              }
                              history={this.props.history}
                              removeShippingAddress={(index) =>
                                this.deleteAddress(index)
                              }
                              setPrimaryAddress={(values, id) =>
                                this.setPrimaryAddress(values, id)
                              }
                              addShippingAddress={() =>
                                this.addAnotherShipping()
                              }
                              setShipping={(data, index) => {
                                ClickedButton(
                                  BUTTONNAME.EDITSHIPPINGADD,
                                  PAGENAME.MYPROFILE
                                );
                                this.state.customerShipping[
                                  index
                                ].isViewShipping = data;
                                this.setState({
                                  customerShipping: this.state.customerShipping,
                                });
                              }}
                            />{" "}
                          </RadioGroup>
                        ) : (
                          <EditShipping
                            isLoading={(data) =>
                              this.setState({ isSpinner: data })
                            }
                            baseData={this.state.customerShipping}
                            customerDetails={res}
                            index={index}
                            handleSubmit={(values, index) =>
                              this.handleShippingSubmit(values, index)
                            }
                            removeShippingAddress={(index) => {
                              this.setState(
                                { isMultiShipDisabled: false },
                                () => {
                                  let addressData = this.state.customerShipping;
                                  addressData = addressData.filter((e, ind) => {
                                    return ind !== index;
                                  });
                                  this.setState({
                                    customerShipping: addressData,
                                  });
                                }
                              );
                            }}
                            setMessages={(msg, msgtype) =>
                              this.setMsg(msg, msgtype)
                            }
                            history={this.props.history}
                            setShipping={(data, index) => {
                              ClickedButton(
                                BUTTONNAME.CANCELSHIPPINGADD,
                                PAGENAME.MYPROFILE
                              );
                              this.state.customerShipping[
                                index
                              ].isViewShipping = data;
                              this.setState({
                                customerShipping: this.state.customerShipping,
                              });
                            }}
                          />
                        )}
                      </div>
                    );
                  })}
              </div>
              <div className="profile-info profile-info-inline">
                {/* <Button
									variant="contained"
									color="primary"
									className={'btn-link'}
									id="addAnotherShippingAddress"
									disabled={this.state.isMultiShipDisabled ? true : false}
									aria-label={shippingAddressTxt}
									onClick={() => this.addMultipleShipping()}
								>
									{shippingAddressTxt}
								</Button> */}
              </div>
            </Container>
          </TabPanel>
          <TabPanel value={this.state.tabIndex} index={1}>
            <Container className="container-root">
              <ErrorBoundry>
                <OrderHistory />
              </ErrorBoundry>
            </Container>
          </TabPanel>
          <TabPanel value={this.state.tabIndex} index={2}>
            <Container className="container-root">
              <ChangePassword
                handleSubmit={(values) => this.handleChangePassword(values)}
                cancel={(data) => this.setState({ tabIndex: data })}
              />
            </Container>
          </TabPanel>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    customerDetails: state.reducer.customerDetails,
    shippingMessage: state.reducer.minAmount,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    updateCustomerDetails: (response) =>
      dispatch({
        type: types.CUSTOMERDETAILS,
        customerDetails: response,
      }),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(MyProfile));
