import React, { Component } from "react";
import { Button, Grid, Typography, Card } from "@material-ui/core";
import { DisplayHeading } from "./../../../config/constants/content.constant";

class ViewBilling extends Component {
  getAddress(billingAddressData) {
    var billingAddress = "";
    billingAddress +=
      !!billingAddressData && !!billingAddressData.billingAddressline1
        ? billingAddressData.billingAddressline1
        : "";
    billingAddress +=
      !!billingAddressData && !!billingAddressData.billingAddressline2
        ? ", " + billingAddressData.billingAddressline2
        : "";
    billingAddress +=
      !!billingAddressData && !!billingAddressData.billingCity
        ? ", " + billingAddressData.billingCity
        : "";
    billingAddress +=
      !!billingAddressData && !!billingAddressData.billingState
        ? ", " + billingAddressData.billingState
        : "";
    billingAddress +=
      !!billingAddressData && billingAddressData.billingZip
        ? ", " + billingAddressData.billingZip
        : "";
    return billingAddress;
  }
  render() {
    const customerDetails = this.props.customerDetails;
    let billingTxt = customerDetails.billingFirstname
      ? "EDIT BILLING ADDRESS"
      : "Add BILLING ADDRESS";
    let billingHeading = customerDetails.billingFirstname
      ? DisplayHeading.BILLING_ADDRESS
      : "";

    return (
      <div className="profile-info">
        <>
          <Grid container spacing={6} justify="space-between">
            {/* Biling Address */}
            <Grid item xs={12} sm={12}>
              {customerDetails.billingFirstname ? (
                <Card className="profile-card">
                  <Grid
                    container
                    spacing={2}
                    alignItems="center"
                    className="title-mini"
                  >
                    <Grid item xs container>
                      <Typography variant="h2" component="h2">
                        {billingHeading}
                      </Typography>
                    </Grid>
                  </Grid>
                  <div className="profile-card-bill">
                    <div className="profile-card-single br-0">
                      <div className="key">
                        {!!customerDetails
                          ? (!!customerDetails.billingFirstname
                              ? customerDetails.billingFirstname
                              : "") +
                            " " +
                            (!!customerDetails.billingLastname
                              ? customerDetails.billingLastname
                              : "")
                          : "-"}
                      </div>
                      <div className="val">
                        {this.getAddress(customerDetails)}
                      </div>
                    </div>
                    <div className="btn-bottom-row">
                      <Button
                        variant="contained"
                        color="primary"
                        className="btn-link"
                        id="editBillingAddress"
                        aria-label="edit Billing Address"
                        onClick={() => this.props.setBilling(false)}
                      >
                        {billingTxt}
                      </Button>
                    </div>
                  </div>
                </Card>
              ) : (
                <Button
                  variant="contained"
                  color="primary"
                  className="btn-link"
                  id="editBillingAddress"
                  aria-label="edit Billing Address"
                  onClick={() => this.props.setBilling(false)}
                >
                  {billingTxt}
                </Button>
              )}
            </Grid>
          </Grid>
        </>
      </div>
    );
  }
}
export default ViewBilling;
