import React, { Component } from "react";
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Grid,
  Typography,
  CircularProgress,
} from "@material-ui/core";
import { connect } from "react-redux";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import {
  actiongetProductDetail,
  actionCheckProductAvailability,
} from "../../../actions/ProductAction";
import Spinner from "../../Spinner";
import QuantityCount from "../../commonComponents/quantityCount/QuantityCount";
import SnacksAlertMessage from "../../commonComponents/snacksAlertMessage/SnacksAlertMessage";
import Color from "color-thief-react";
import { hexToRgbA } from "../../Utils";
import { EventNames, trackEvent } from "../../../appinsights/EventTrack";
import { DisplayHeading } from "./../../../config/constants/content.constant";
import { ViewedPage } from "../../../config/amplitude/SnacksAmplitude";
import {
  PAGENAME,
  PRODUCTTYPE,
} from "../../../config/amplitude/Taxonomy.constants";
import "./ProductDetail.scss";

class ProductDetail extends Component {
  state = {
    id: this.props,
    isLoaded: false,
    products: "",
    productItems: {},
    productItemsCopy: {},
    selectedItems: {},
    msg: null,
  };

  componentDidMount() {
    ViewedPage(PAGENAME.PRODUCTDETAIL);
    const { productData, items } = this.props;
    const primaryIndex = productData.findIndex((obj) => obj.primaryProduct);
    let productItems = items;
    const productItemsCopy = { ...items };
    this.setState({ productItems, productItemsCopy });
    if (productData && productData[primaryIndex]) {
      trackEvent(
        EventNames.Action.ACTION_PRODUCT_DETAILS,
        EventNames.Event.EVENT_PAGE_VIEW,
        window.location.origin,
        window.location.pathname,
        { product: productData[primaryIndex] }
      );
      const payload = {
        prodId: productData[primaryIndex].id,
        locationNumber: sessionStorage.getItem("_lo_No") || null,
      };
      this.props.actiongetProductDetail(payload).then((response) => {
        if (response.length > 0) {
          response[0].availableQty = productData[primaryIndex].availableQty;
          response[0].quantity = productData[primaryIndex].quantity;
          if (parseInt(productData[primaryIndex].availableQty) <= 0) {
            response[0].isOutofstock = 0;
          } else {
            response[0].isOutofstock = 1;
          }
          this.setState({ products: response[0] });
        }
      });
    }
  }

  UNSAFE_componentWillReceiveProps({ someProp }) {
    this.setState({ ...this.state, someProp });
  }

  getQuantitySection = () => {
    const { warningMsgFunc, productData, selectedItems } = this.props;
    const primaryIndex = productData.findIndex((obj) => obj.primaryProduct);

    return productData.map((product, index) => (
      <QuantityCount
        primaryProduct={productData[primaryIndex]}
        productInfo={product}
        key={index}
        warningMsgFunc={warningMsgFunc}
        selectedItems={selectedItems}
        sizeName={product.size}
        price={product.price}
        availableQty={product.availableQty}
        currency={productData[primaryIndex].currency}
        PageTitle={PAGENAME.PRODUCTDETAIL}
        ProductType={PRODUCTTYPE.SNACKS}
      />
    ));
  };

  showAccordionContent = (heading, body, panel, show) => {
    const { products } = this.state;
    return (
      <Accordion className="accordion-wrapper">
        <AccordionSummary
          expandIcon={<ExpandMoreIcon className="expand-more-icon" />}
          aria-controls={`${panel}-content`}
          id={`${panel}-header`}
          className="accordion-header"
        >
          <Typography variant="h2">{heading}</Typography>
        </AccordionSummary>
        <AccordionDetails className="accordion-body">
          {show === "desc" ? (
            <div className="product-desc">{body}</div>
          ) : (
            <img src={body} alt={`${heading} of ${products.brand}`} />
          )}
        </AccordionDetails>
      </Accordion>
    );
  };

  render() {
    const { products, msg } = this.state;
    let rgbaColor = "";

    if (!products) {
      return (
        <Grid
          container
          justify="center"
          style={{
            minHeight: "100px",
            textAlign: "center",
            paddingTop: "25px",
          }}
        >
          <Grid item>
            <CircularProgress />
          </Grid>
        </Grid>
      );
    }

    return (
      <React.Fragment>
        {msg && <SnacksAlertMessage msg={msg} />}
        {products ? (
          <Color
            src={products.image.Product_Image}
            crossOrigin="Anonymous"
            format="hex"
          >
            {({ data, loading }) => {
              if (products.backgroundColorRgb) {
                rgbaColor = hexToRgbA("#" + products.backgroundColorRgb);
                data = hexToRgbA("#" + products.backgroundColorRgb);
              } else {
                rgbaColor = data ? hexToRgbA(data) : "#000000";
              }
              // const rgbaColor = data ? hexToRgbA(data) : "#000000";
              return (
                <div
                  className="product-detail-wrapper"
                  style={{
                    backgroundImage: `linear-gradient(to bottom, ${rgbaColor} ,${data})`,
                  }}
                >
                  {/* <Typography
                    variant="h1"
                    className="heading-hidden"
                    aria-hidden="true"
                  >
                    Product Detail
                  </Typography> */}
                  <Grid container className="product-info-container">
                    <Grid item sm={6}>
                      <div className="product-detail-image-outer">
                        <div className="product-detail-image">
                          <img
                            src={products.image.Product_Image}
                            alt={products.shortDescription}
                          />
                          <div className="shadow"></div>
                        </div>
                      </div>

                      {/* {products.description &&
                        this.showAccordionContent(
                          DisplayHeading.DESCRIPTION,
                          products.description,
                          "panel2a",
                          "desc"
                        )} */}
                    </Grid>
                    <Grid item sm={6} className="product-info-div">
                      <div className="product-brand">{products.brand}</div>
                      <h1 className="product-name">
                        {products.shortDescription}
                      </h1>
                      <div className="product-detail-actions">
                        {products.availableQty <= 0 && (
                          <div className="out-of-stock">
                            {DisplayHeading.OUT_OF_STOCK}
                          </div>
                        )}
                        {products.availableQty > 0 && this.getQuantitySection()}
                      </div>
                      {products.image.Nutrition_Image &&
                        this.showAccordionContent(
                          DisplayHeading.NUTRITION_FACTS,
                          products.image.Nutrition_Image,
                          "panel3a"
                        )}
                      {products.image.Ingredients_Image &&
                        this.showAccordionContent(
                          DisplayHeading.INGREDIENTS,
                          products.image.Ingredients_Image,
                          "panel1a"
                        )}
                    </Grid>
                  </Grid>
                </div>
              );
            }}
          </Color>
        ) : (
          <Grid container justify="center">
            <Grid item>
              {products === undefined ? (
                <h1 className="noproducts-text">Technical Issue</h1>
              ) : (
                <Spinner />
              )}
            </Grid>
          </Grid>
        )}
      </React.Fragment>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    products: state.reducer.products,
    productInventory: state.reducer.productInventory,
  };
};
export default connect(mapStateToProps, {
  actiongetProductDetail,
  actionCheckProductAvailability,
})(ProductDetail);
