import React, { Component } from "react";
import { Button, Typography, Grid } from "@material-ui/core";
import CustomEvent from "custom-event";
import { isMobile } from "react-device-detect";
import { trackEvent, EventNames } from "../../appinsights/EventTrack";
import OrderSummary from "./OrderSummary";
import { DisplayHeading } from "../../config/constants/content.constant";
import UrlConstants from "../../config/UrlConstants";
import { connect } from "react-redux";
import PopularProducts from "../popularProducts/PopularProducts";
import { actionLabelList } from "./../../actions/ProductAction";
import {
  ViewedPage,
  ClickedButton,
} from "../../config/amplitude/SnacksAmplitude";
import {
  PAGENAME,
  BUTTONNAME,
} from "../../config/amplitude/Taxonomy.constants";
import { removeSessionWhichStarts } from "../Utils";

class ThankYou extends Component {
  state = {
    orderId: "",
    orderNumber: "",
    OrderData: [],
    total: "",
    discountedAmount: sessionStorage.getItem("discountedAmount") || 0,
    discount: sessionStorage.getItem("discount") || 0,
  };

  componentDidMount() {
    this.props.actionLabelList();
    if (sessionStorage.getItem("_sc_re")) {
      var event = new CustomEvent("cartitem");
      document.dispatchEvent(event);
      this.setState(
        {
          OrderData: sessionStorage.getItem("_sc_re")
            ? JSON.parse(sessionStorage.getItem("_sc_re"))
            : [],
        },
        () => {
          ViewedPage(PAGENAME.THANKYOU, {
            orderId: this.state.OrderData && this.state.OrderData.orderId,
            totalPrice: sessionStorage.getItem("finalTotal"),
            orderType: isMobile ? "Mobile" : "Web",
          });
        }
      );
    }
  }

  componentWillUnmount() {
    this.removeGenericSessionValues();
    //window.top.location.reload();
  }

  removeGenericSessionValues = () => {
    sessionStorage.removeItem("isPayment");
    sessionStorage.removeItem("tax");
    sessionStorage.removeItem("cardNumber");
    sessionStorage.removeItem("coupon");
    sessionStorage.removeItem("discountedAmount");
    sessionStorage.removeItem("discount");
    sessionStorage.removeItem("totalDiscount");
    sessionStorage.removeItem("totalAmount");
    sessionStorage.removeItem("cartId");
    sessionStorage.removeItem("sessionCartId");
    sessionStorage.removeItem("finalTotal");
    sessionStorage.removeItem("orderNumber");
    removeSessionWhichStarts("shippingSku");
  };

  goToProductList = () => {
    this.removeGenericSessionValues();

    window.top.location.href = UrlConstants.SiteUrl + "/productlist";
  };

  renderCommonButtons = (placement) => (
    <div className="button-group">
      <Button
        disabled={this.state.disableCheckout}
        onClick={() => {
          trackEvent(
            EventNames.Action.ACTION_THANK_YOU,
            EventNames.Event.EVENT_BUTTON_PRESS,
            window.location.origin,
            window.location.pathname
          );
          ClickedButton(BUTTONNAME.CONTINUESHOPPING, PAGENAME.THANKYOU);
          this.goToProductList();
        }}
        className="btn-primary"
        id={"continue-shopping"}
        aria-label={DisplayHeading.CONTINUE_SHOPPING}
        tabIndex={0}
      >
        {DisplayHeading.CONTINUE_SHOPPING}
      </Button>
    </div>
  );

  render() {
    sessionStorage.removeItem("taxZipCode");
    const OrderData = this.state.OrderData;
    const { customerDetails, labelListItem } = this.props;
    let orderConfirmationMessage = labelListItem
      ? labelListItem.Messages[0].sectionDescription
      : "";
    let thankYouMessage = labelListItem
      ? labelListItem.Messages[1].sectionDescription
      : "";
    let covidMessage = labelListItem
      ? labelListItem.Messages[2].sectionDescription
      : "";
    let contactUsEmail = labelListItem
      ? labelListItem.Messages[3].sectionDescription
      : "customersupport@fritolay.com";
    const profileUserName =
      customerDetails &&
      customerDetails.accountContacts &&
      customerDetails.accountContacts.length > 0
        ? customerDetails.accountContacts[0].firstName
        : "";
    return (
      <div className={"thankyou"}>
        <div className="thankyou-page-heading sticky-head has-button">
          <div className="sticky-head-inner">
            <h1 className="sticky-title" aria-hidden="true">
              {DisplayHeading.THANK_YOU.replace(/##NAME##/g, function () {
                return profileUserName.length > 0 ? `, ${profileUserName}` : "";
              })}
            </h1>
            {this.renderCommonButtons("header")}
          </div>
        </div>
        {JSON.parse(sessionStorage.getItem("newUserCheckout")) === true ? (
          <div className="activation-info">
            <div className="activation-info-inner">
              <Typography variant="h2">
                Thank you for registering! An email with a link and directions
                for how to setup your password and become a registered member
                has been sent.
              </Typography>
              {/* <Button
              className="icn-close"
              id="closeActivation"
              aria-label="close password activation" >
              <i className="fa fa-times"></i>
            </Button> */}
            </div>
          </div>
        ) : (
          ""
        )}

        <div className="thankyou-content">
          <div className="thankyou-content-container">
            <Grid container className="top-container">
              <Grid item xs={12} sm={7} className="thankyou-header">
                <Typography variant="h2">
                  YOUR ORDER NUMBER IS #{OrderData.orderId}
                </Typography>
                <div className="thankyou-line1">{orderConfirmationMessage}</div>
                <div className="thankyou-line2">
                  If you have any questions or need to get a hold of customer
                  support, please{" "}
                  <a href="/contactus" aria-label={"Contact Us"}>
                    contact us
                  </a>{" "}
                  or email us at {contactUsEmail}
                </div>
                <div>
                  <div className="divider" />
                </div>
                <div className="thankyou-line1">
                  Total Amount charge on Visa Ending in *
                  {!!OrderData &&
                    !!OrderData.cardNumber &&
                    OrderData.cardNumber}
                  {/*  {!!sessionStorage.getItem("cardNumber")
                    ? sessionStorage.getItem("cardNumber")
                    : ""} */}
                  : ${!!OrderData && !!OrderData.total ? OrderData.total : ""}
                  {/* {parseFloat(
                    parseFloat(this.state.total).toFixed(2) -
                      this.state.discount
                  ).toFixed(2)} */}
                  {/* {this.state.total} */}
                </div>
              </Grid>
              <Grid item xs={12} sm={5} className="thankyou-content-inside">
                <div className="thankyou-image"></div>
              </Grid>
            </Grid>
            <Grid item xs={12} sm={12} className="order-bottom-container">
              <OrderSummary
                orderSummary={
                  sessionStorage.getItem("_sc_re")
                    ? JSON.parse(sessionStorage.getItem("_sc_re")).items
                    : []
                }
                multiPackOrder={
                  sessionStorage.getItem("_sc_re")
                    ? JSON.parse(sessionStorage.getItem("_sc_re")).miltipack
                    : []
                }
                isThankYou={true}
                onlyTotal={false}
                summuryTaxData={this.props.summuryTaxData}
                isSummuryLoader={this.props.isSummuryLoader}
                shippingCharges={this.props.shippingCharges}
                totalUpdate={(totalValue) =>
                  this.setState({
                    total: totalValue.toFixed(2).toString(),
                  })
                }
                OrderID={OrderData.orderId}
                taxCalculation={this.props.taxCalculation}
              />
            </Grid>
            <Grid container className="thankyou-gray">
              <Grid item xs={12} className="thankyou-content">
                <div className="thankyou-line-brown">{thankYouMessage}</div>
              </Grid>
            </Grid>
          </div>

          {/* </div> */}
          <div className="thankyou-note">{covidMessage}.</div>
          <PopularProducts
            customheading="SIMILAR PRODUCTS YOU MAY LIKE"
            customerDetails={customerDetails}
            showSeeAllAtBottom={true}
            isThankyouPage={true}
            PageTitle={PAGENAME.THANKYOU}
          />
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    customerDetails: state.reducer.customerDetails,
    labelListItem: state.reducer.labelListItem,
  };
};
export default connect(mapStateToProps, {
  actionLabelList,
})(ThankYou);
