import React, { Component } from "react";
import { Button, Grid, Typography, Card } from "@material-ui/core";
import { DisplayHeading } from "./../../../config/constants/content.constant";

class ViewContact extends Component {
  render() {
    const customerDetails = this.props.customerDetails;
    return (
      <div className="profile-info has-contact">
        <>
          <Grid container spacing={6} justify="space-between">
            {/* Contact info */}
            <Grid item xs={12} sm={12}>
              <Card className="profile-card card-3">
                <Grid
                  container
                  spacing={2}
                  alignItems="center"
                  className="title-mini"
                >
                  <Grid item xs container>
                    <Typography variant="h2" component="h2">
                      {DisplayHeading.CONTACT_INFORMATION}
                    </Typography>
                  </Grid>
                </Grid>
                <div className="profile-card-inn">
                  <div className="profile-card-single">
                    <div className="key">Name:</div>
                    <div className="val">
                      {!!customerDetails
                        ? customerDetails.customerfirstname +
                          " " +
                          customerDetails.customerlastname
                        : "-"}
                    </div>
                  </div>
                  <div className="profile-card-single">
                    <div className="key">Email:</div>
                    <div className="val">
                      {!!customerDetails ? customerDetails.customeremail : "-"}
                    </div>
                  </div>
                  <div className="profile-card-single">
                    <div className="key">Phone:</div>
                    <div className="val">
                      {!!customerDetails ? customerDetails.customerPhone1 : "-"}
                    </div>
                  </div>
                </div>
                <div className="btn-bottom-row">
                  <Button
                    variant="contained"
                    color="primary"
                    className="btn-link"
                    id="editContact"
                    aria-label="edit Contact Info"
                    onClick={() => this.props.setProfile(false)}
                  >
                    EDIT CONTACT INFO
                  </Button>
                </div>
              </Card>
            </Grid>
          </Grid>
        </>
      </div>
    );
  }
}
export default ViewContact;
