import React, { Component } from "react";
import {
  Grid,
  Button,
  Accordion,
  AccordionDetails,
  AccordionSummary,
} from "@material-ui/core";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import {
  SelectedLinkItem,
  SelectedSubCategory,
} from "../../../config/amplitude/SnacksAmplitude";
import { PAGENAME } from "../../../config/amplitude/Taxonomy.constants";

export default class CategoryTout extends Component {
  state = {
    isSelected: this.props.lastSelectedCategory || [],
    isSelectedSub: [],
  };

  onSelect = (item, subItem) => {
    const { selectCategory } = this.props;
    const { isSelected, isSelectedSub } = this.state;

    if (subItem) {
      const objIndex = isSelectedSub.findIndex(
        (obj) => obj === subItem.subCategoryId
      );

      if (objIndex === -1) {
        this.setState(
          {
            isSelectedSub: [...isSelectedSub, subItem.subCategoryId],
          },
          () =>
            SelectedSubCategory(
              item.categoryId,
              this.state.isSelectedSub,
              PAGENAME.SHOPBYCATEGORY
            )
        );
      } else {
        const filteredArray = isSelectedSub.filter(
          (item) => item !== isSelectedSub[objIndex]
        );
        this.setState(
          {
            isSelectedSub: filteredArray,
          },
          () =>
            SelectedSubCategory(
              item.categoryId,
              this.state.isSelectedSub,
              PAGENAME.SHOPBYCATEGORY
            )
        );
      }

      selectCategory(item, subItem);
    } else if (!subItem) {
      const objIndex = isSelected.findIndex((obj) => obj === item.categoryId);
      SelectedLinkItem(item.categoryId, PAGENAME.SHOPBYCATEGORY);
      if (objIndex === -1) {
        this.setState({
          isSelected: [...isSelected, item.categoryId],
        });
      } else {
        const filteredArray = isSelected.filter(
          (item) => item !== isSelected[objIndex]
        );
        this.setState({
          isSelected: filteredArray,
        });
      }

      selectCategory(item);
    }
  };

  render() {
    const { categoryList, currentStep, lastSelectedCategory } = this.props;

    if (currentStep === 0) {
      return (
        <Grid container spacing={1} className="main-category">
          {categoryList.map((item, index) => {
            return (
              <Grid
                item
                key={`${item.categoryId}-${index}`}
                xs={6}
                lg={3}
                sm={3}
              >
                <Button
                  className={`shop-by-category-category-item ${item.title
                    .replace(/\s+/g, "-")
                    .toLowerCase()} ${
                    lastSelectedCategory.includes(item.categoryId)
                      ? "selected"
                      : ""
                  }`}
                  onClick={() => this.onSelect(item)}
                >
                  {item.image && (
                    <img
                      className="main-image"
                      src={item.image}
                      alt={`${item.title} category`}
                    />
                  )}
                  {item.categoryHoverImage && (
                    <img
                      className="main-hover-image"
                      src={item.categoryHoverImage}
                      alt={`${item.title} category selected`}
                    />
                  )}
                  {!item.image && (
                    <div className="category-heading">{item.title}</div>
                  )}
                </Button>
              </Grid>
            );
          })}
        </Grid>
      );
    }

    return categoryList.map((item, index) => {
      const panelKey = `${item.title
        .replace(/\s+/g, "-")
        .toLowerCase()}-${index}`;
      return (
        <Accordion defaultExpanded className="main-sub-category" key={panelKey}>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            className={`category-heading ${item.title
              .replace(/\s+/g, "-")
              .toLowerCase()}`}
          >
            <h2>{item.title}</h2>
          </AccordionSummary>
          <AccordionDetails className="cat-accordion-wrapper">
            <Grid container spacing={3} className="sub-category">
              {item.subCategories.length === 0 && (
                <Grid className="no-sub-category" item xs={12} lg={12} sm={12}>
                  {`No Sub category present for ${item.title}`}
                </Grid>
              )}
              {item.subCategories.map((subItem, index) => {
                const { isSelectedSub } = this.state;

                return (
                  <Grid
                    item
                    key={`${subItem.subCategoryId}-${index}`}
                    xs={4}
                    sm={3}
                    lg={2}
                  >
                    <Button
                      className={`shop-by-category-category-item ${item.title
                        .replace(/\s+/g, "-")
                        .toLowerCase()} ${
                        isSelectedSub.includes(subItem.subCategoryId)
                          ? "selected"
                          : ""
                      }`}
                      onClick={() => this.onSelect(item, subItem)}
                    >
                      {subItem.image && (
                        <img
                          className="sub-main-image"
                          src={subItem.image}
                          alt={`${subItem.title} sub category of ${item.title}`}
                        />
                      )}
                      {subItem.subCategoryHoverImage && (
                        <img
                          className="sub-main-hover-image"
                          src={subItem.subCategoryHoverImage}
                          alt={`${subItem.title} sub category of ${item.title} selected`}
                        />
                      )}
                      {!subItem.image && (
                        <div className="sub-heading">{subItem.title}</div>
                      )}
                    </Button>
                  </Grid>
                );
              })}
            </Grid>
          </AccordionDetails>
        </Accordion>
      );
    });
  }
}
