import React, { Component } from "react";
import { Grid } from "@material-ui/core";
import { Link } from "react-router-dom";
import "./multipackConfirmation.scss";

export default class MultipackConfirmation extends Component {
  render() {
    const {
      config: {
        confirmationHeading,
        confirmationImage,
        confirmationDesc,
        confirmationSubHeading,
        confirmationContinueBtn,
        confirmationCheckoutBtn,
      },
    } = this.props;

    return (
      <Grid container justify="center">
        <Grid item sm={5} className="multipack-confirm-left">
          <img src={confirmationImage} alt={confirmationHeading} />
        </Grid>
        <Grid item sm={7} className="multipack-confirm-right">
          <h1>{confirmationHeading}</h1>
          <h2>{confirmationSubHeading}</h2>
          <p>{confirmationDesc}</p>
        </Grid>
        <Grid item sm={12} className="btn-group">
          <Link to="/productlist" className="btn-primary" id="continueShopping">
            {confirmationContinueBtn}
          </Link>
          <Link to="/cart" className="btn-secondary" id="proceedCheckout">
            {confirmationCheckoutBtn}
          </Link>
        </Grid>
      </Grid>
    );
  }
}
