import React, { Component } from "react";
import moment from "moment";
import { Button, Grid } from "@material-ui/core";
import DialogBox from "./../../commonComponents/dialogBox/DialogBox";
import { ClickedButton } from "../../../config/amplitude/SnacksAmplitude";
import {
  METHOD,
  BUTTONNAME,
} from "../../../config/amplitude/Taxonomy.constants";
class ProductImages extends Component {
  state = {
    cartContent: "",
    filterBy: "all",
    dialogOpen: false,
    selectedOrderId: 0,
    selectedOrder: sessionStorage.getItem("selectedOrder"),
    multipackDetail: "",
  };

  confirmUpdateCart = (items, orderId) => {
    const cartItems = sessionStorage.getItem("cartItems")
      ? JSON.parse(sessionStorage.getItem("cartItems"))
      : [];
    if (cartItems.length === 0) {
      this.setState({ selectedOrderId: orderId }, () => {
        this.updateCartItems(items);
      });
    } else {
      this.setState({
        selectedOrderId: orderId,
        dialogOpen: true,
        cartContent: items,
      });
    }
  };

  dialogClose() {
    this.setState({ dialogOpen: false, cartContent: "" });
  }

  dialogCloseConfirm() {
    this.updateCartItems(this.state.cartContent);
    this.dialogClose();
  }

  updateCartItems = (items) => {
    let multipack = this.props.orderImages.multipack;
    let multipackArray =
      multipack.length > 0 &&
      multipack.map((multipackItem, index) => {
        let rObj = {
          id: multipackItem.multipackId,
          image: multipackItem.image,
          price: multipackItem.price,
          availableQty: this.props.multiPackConfig.varietyPackAvailableQty,
          multipack: true,
          productDescription: this.props.multiPackConfig.shortDescription,
          multipackList: multipackItem.multipackList,
          productId: multipackItem.multipackId,
          quantity: multipackItem.quantity,
          size: this.props.multiPackConfig.mixCount,
          title: this.props.multiPackConfig.heading,
          productTitle: this.props.multiPackConfig.productTitle,
          zNonSku: multipackItem.zNonSku,
        };
        return rObj;
      });
    let itemsArray = this.props.orderImages.items;

    let cartItemsArray = [...itemsArray];
    if (multipackArray.length > 0) {
      cartItemsArray = [...multipackArray, ...itemsArray];
    }

    sessionStorage.setItem("cartItems", JSON.stringify(cartItemsArray));
    sessionStorage.setItem("selectedOrder", this.state.selectedOrderId);
    var event = new CustomEvent("cartitem");
    document.dispatchEvent(event);
    let zipcodeVerified = sessionStorage.getItem("Zipcode");
    if (zipcodeVerified) {
      window.location.href = "/cart";
    }
  };

  renderMultipackDetail = (multipackList) => {
    return (
      <Grid item xs={12} sm={12} md={12} className="multipack-cart-detail">
        <div className="multipack-cart-detail-inner">
          <Button
            id={`closeMultipackDetail`}
            aria-label={`Close Multipack Detail`}
            className="multipack-cart-detail-close"
            onClick={() =>
              this.setState({
                multipackDetail: "",
              })
            }
          >
            <i className="fa fa-times"></i>
          </Button>
          {multipackList.map((multipackDetailItem, index) => {
            return (
              <Grid container key={index}>
                <Grid item className="multipack-cart-detail-img">
                  <img
                    alt={`multipack-${multipackDetailItem.productDescription}-${multipackDetailItem.productId}`}
                    src={multipackDetailItem.image}
                    role="presentation"
                  />
                </Grid>
                <Grid item xs>
                  <div className="title-product">
                    {multipackDetailItem.productDescription}
                  </div>
                  <div className="count">
                    Quantity: {multipackDetailItem.quantity}
                  </div>
                </Grid>
              </Grid>
            );
          })}
        </div>
      </Grid>
    );
  };

  render() {
    const { dialogOpen } = this.state;
    const orderImages = this.props.orderImages;
    return (
      <div>
        <DialogBox
          dialogClose={() => this.dialogClose()}
          dialogOpen={dialogOpen}
          dialogHeading="Are you sure you want to Replace your Cart?"
          dialogWarning
          dialogCloseConfirm={() => this.dialogCloseConfirm()}
          ariaLabelYes={"Yes i would like to Replace my cart"}
          ariaLabelCancel={"No I don't want to replace my cart"}
        />
        <div className="pro-order-detail">
          <div className="ord-date">
            <p>
              <strong>Date Ordered:</strong>{" "}
              {moment(orderImages.orderDate).format("MM/DD/YY")}{" "}
            </p>
            <p>
              <strong>Order Number:</strong> {orderImages.orderId}
            </p>
            {/* <p>
              <strong>Status:</strong> {orderImages.orderSate}
            </p> */}
          </div>
          <div className="ord-ship-info">
            <p>
              <strong>Shipped to:</strong>{" "}
              {orderImages.shipping.shippingFirstname}{" "}
              {orderImages.shipping.shippingLastname} <br />
              {orderImages.shipping.shippingAddressline1}{" "}
              {orderImages.shipping.shippingCity}{" "}
              {orderImages.shipping.shippingState}{" "}
              {orderImages.shipping.shippingZip}
            </p>
          </div>
          <div className="ord-item-count">
            <p className="ord-item-status">
              <strong>Status:</strong> {orderImages.orderSate}
              {/* <strong>Item Count:</strong>
              {parseInt(orderImages.items.length) +
                parseInt(orderImages.multipack.length)}{" "} */}
            </p>
            <p className="ord-item-total">
              <strong>Total:</strong> $
              {parseFloat(orderImages.totalPrice.toFixed(2))}
            </p>
          </div>
          <div className="re-ord-btn">
            <Button
              type="submit"
              style={{ color: "#BD390D" }}
              variant="contained"
              color="primary"
              className="btn-link"
              onClick={() => {
                this.confirmUpdateCart(orderImages.items, orderImages.orderId);
                ClickedButton(BUTTONNAME.REORDER, METHOD.ORDERHISTORY, {
                  orderId: orderImages.orderId,
                });
              }}
            >
              REORDER
            </Button>
          </div>
        </div>
        <Grid
          container
          spacing={2}
          alignItems="center"
          className="bundle-card-summary is-title"
        >
          <Grid item className="summary-row-img"></Grid>
          <Grid item className="summary-row-info">
            <Grid container spacing={0}>
              <Grid item xs={6} sm={6} md={6}>
                Items
              </Grid>
              <Grid item xs={2} sm={2} md={2} className="text-center">
                Qty.
              </Grid>
              <Grid item xs={2} sm={2} md={2} className="text-center">
                Price
              </Grid>
              <Grid item xs={2} sm={2} md={2} className="text-center">
                Total
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        {orderImages.items.length > 0 &&
          orderImages.items.map((item, index) => (
            <Grid
              container
              spacing={2}
              alignItems="center"
              className="bundle-card-summary"
              key={`${item.title}-${index}`}
            >
              <Grid item className="summary-row-img">
                <div className="summary-img">
                  <img src={item.image} alt={item.title} />
                </div>
              </Grid>
              <Grid item className="summary-row-info">
                <Grid container spacing={0}>
                  <Grid item xs={6} sm={6} md={6}>
                    <div className="title-product">{item.title}</div>
                  </Grid>
                  <Grid item xs={2} sm={2} md={2}>
                    <div className="counter-wrapper">
                      <span className="title-mobile">Qty: </span>
                      {/* <label
                        className="label-hidden"
                        htmlFor={"quantity" + index}
                      >
                        summaryInput
                      </label>
                      <input
                        type="text"
                        disabled
                        id={"quantity" + index}
                        value={item.quantity}
                      /> */}
                      <div className="qty-value">{item.quantity}</div>
                    </div>
                  </Grid>
                  <Grid item xs={2} sm={2} md={2}>
                    <div className="price text-center">
                      <span className="title-mobile">Price: </span>
                      <span>${parseFloat(item.price).toFixed(2)}</span>
                    </div>
                  </Grid>
                  <Grid item xs={2} sm={2} md={2}>
                    <div className="price text-center">
                      <span className="title-mobile">Total: </span>$
                      {parseFloat(item.totalPrice).toFixed(2)}
                    </div>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          ))}

        {orderImages.multipack.length > 0 &&
          orderImages.multipack.map((multipackitem, index) => (
            <Grid
              container
              spacing={2}
              alignItems="center"
              className="bundle-card-summary"
              key={`${multipackitem.productTitle}-${index}`}
            >
              <Grid item className="summary-row-img">
                <div className="summary-img">
                  <img
                    src={multipackitem.image}
                    alt={multipackitem.multipackId}
                  />
                </div>
              </Grid>
              <Grid item className="summary-row-info">
                <Grid container spacing={0}>
                  <Grid item xs={6} sm={6} md={6}>
                    <div className="title-product">
                      {multipackitem.productTitle}
                    </div>
                  </Grid>
                  <Grid item xs={2} sm={2} md={2}>
                    <div className="counter-wrapper">
                      <span className="title-mobile">Qty: </span>
                      {/* <label
                        className="label-hidden"
                        htmlFor={"quantity" + index}
                      >
                        summaryInput
                      </label>
                      <input
                        type="text"
                        disabled
                        id={"quantity" + index}
                        value={item.quantity}
                      /> */}
                      <div className="qty-value">{multipackitem.quantity}</div>
                    </div>
                  </Grid>
                  <Grid item xs={2} sm={2} md={2}>
                    <div className="price text-center">
                      <span className="title-mobile">Price: </span>
                      <span>${parseFloat(multipackitem.price).toFixed(2)}</span>
                    </div>
                  </Grid>
                  <Grid item xs={2} sm={2} md={2}>
                    <div className="price text-center">
                      <span className="title-mobile">Total: </span>$
                      {parseFloat(
                        multipackitem.price * multipackitem.quantity
                      ).toFixed(2)}
                    </div>
                  </Grid>
                  <Grid item xs={2} sm={2} md={2}>
                    <Button
                      id="multipackdetails"
                      aria-label="multipackdetails"
                      className="multipack-view-detail"
                      onClick={() =>
                        this.setState({
                          multipackDetail: multipackitem.multipackId,
                        })
                      }
                    >
                      View Details
                    </Button>
                  </Grid>
                </Grid>
              </Grid>
              {multipackitem.multipackList.length > 0 &&
                this.state.multipackDetail === multipackitem.multipackId &&
                this.renderMultipackDetail(multipackitem.multipackList)}
            </Grid>
          ))}

        <div className="reorder-bottom-mob">
          <Button
            type="submit"
            style={{ color: "#BD390D" }}
            variant="contained"
            color="primary"
            className="btn-link"
            onClick={() =>
              this.confirmUpdateCart(orderImages.items, orderImages.orderId)
            }
          >
            REORDER
          </Button>
        </div>
      </div>
    );
  }
}
export default ProductImages;
