import React, { Component } from "react";
import { Dialog, DialogContent, Button } from "@material-ui/core";
import ProductDetail from "./ProductDetail";
import MultipackDetail from "./MultipackDetail";

export default class ProductDetailDialog extends Component {
  onDialogClose(event) {
    event.preventDefault();
    if (this.props.horizontalView) {
      window.location.reload();
    } else {
      this.props.statusUpdate(false);
    }
  }

  render() {
    const {
      status,
      productData,
      selectedItems,
      warningMsgFunc,
      selectedCartItems,
      isMultipack,
      multipackLimit,
    } = this.props;

    return (
      <Dialog
        open={status}
        onClose={(e) => this.onDialogClose(e)}
        maxWidth={false}
        scroll={"body"}
        classes={{
          paper: "modal-big modal-product-detail modal-paper",
          root: "modal-stack-top",
        }}
      >
        <DialogContent>
          <Button
            className="icn-close"
            id="productDetailClose"
            aria-label="productDetail Close"
            onClick={(e) => this.onDialogClose(e)}
          >
            <i className="fa fa-times"></i>
          </Button>

          {!isMultipack && (
            <ProductDetail
              productData={productData}
              items={selectedItems}
              warningMsgFunc={warningMsgFunc}
              selectedItems={selectedCartItems}
            />
          )}
          {isMultipack && (
            <MultipackDetail
              productData={productData}
              items={selectedItems}
              multipackLimit={multipackLimit}
              warningMsgFunc={warningMsgFunc}
              selectedItems={selectedCartItems}
            />
          )}
        </DialogContent>
      </Dialog>
    );
  }
}
