import React, { Component } from "react";
import {
  Button,
  Typography,
  Snackbar,
  Grid,
  Container,
  Tooltip,
} from "@material-ui/core";
import { connect } from "react-redux";
import { NavLink } from "react-router-dom";
import {
  actionGetMinAmount,
  actionGetCustomer,
  actionZipcodeVerification,
  actionLabelList,
} from "../../../actions/ProductAction";
import PopularProducts from "../../popularProducts/PopularProducts";
import Spinner from "../../Spinner";
import HeroBanner from "../../commonComponents/heroBanner/HeroBanner";
import MuiAlert from "@material-ui/lab/Alert";
import { isIE } from "react-device-detect";
import { trackEvent, EventNames } from "../../../appinsights/EventTrack";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import SnacksCarousel from "../../commonComponents/snacksCarousel/SnacksCarousel";
import {
  ViewedPage,
  SelectedLinkItem,
} from "../../../config/amplitude/SnacksAmplitude";
import { PAGENAME } from "../../../config/amplitude/Taxonomy.constants";
import "./Home.scss";

class Home extends Component {
  state = {
    clickedBtn: "",
    dailogOpen: false,
    isHomeContentLoading: false,
    shippingMinimum: "",
    minOrder: "",
  };

  zipValidation = (e, fromCategory, sectionName) => {
    e.preventDefault();

    let zipcodeVerified = sessionStorage.getItem("Zipcode");
    if (zipcodeVerified) {
      sectionName &&
        SelectedLinkItem(sectionName.replace(/\s+/g, ""), "Bottom");
      if (fromCategory) this.props.history.push(fromCategory);
      else this.props.history.push("/productlist");
      trackEvent(
        EventNames.Action.ACTION_ZIP_CODE_POPUP,
        EventNames.Event.EVENT_PAGE_VIEW,
        window.location.origin,
        window.location.pathname
      );
    }
  };

  scrollToContent = () => {
    window.scrollTo(0, document.getElementById("content").offsetTop - 25);
  };

  hideDialog() {
    this.setState({ dailogOpen: false });
  }

  actionGetMinAmount = () => {
    try {
      this.props.actionGetMinAmount().then(() => {
        const { minPrice } = this.props;
        if (!!minPrice && Object.keys(minPrice).length !== 0) {
          this.setState({
            minOrder: minPrice.minimumOrderAmount,
            shippingMinimum: minPrice.shippingMinimum,
            isHomeContentLoading: false,
          });
          sessionStorage.setItem(
            "minimumOrderAmount",
            minPrice.minimumOrderAmount || null
          );
          sessionStorage.setItem(
            "shippingMinimum",
            minPrice.shippingMinimum || null
          );
          sessionStorage.setItem(
            "shippingCharges",
            minPrice.shippingCharges || null
          );
          sessionStorage.setItem(
            "holidayShopStockText",
            minPrice.holidayShopStockText || null
          );

          sessionStorage.setItem("contactUsMsg", minPrice.contactUsMsg || null);
        } else {
          this.setState({
            isHomeContentLoading: false,
          });
        }
      });
    } catch (error) {
      this.setState({
        isHomeContentLoading: false,
      });
      console.error("someThing Went wrong while fetching min order value");
    }
  };

  async UNSAFE_componentWillMount() {
    this.setState({
      isHomeContentLoading: true,
    });
    await this.props.actionGetCustomer();
    await this.props.actionLabelList();
    let customerDetails = this.props.customerDetails;
    if (!!customerDetails && !sessionStorage.getItem("Zipcode")) {
      let primaryZipCode = customerDetails.shippingZipcode || "";
      let locationId = customerDetails.locationId || "";

      if (!!primaryZipCode && !!locationId) {
        sessionStorage.setItem("Zipcode", primaryZipCode);
        sessionStorage.setItem("_lo_No", locationId);
      }
    }

    if (localStorage.getItem("accessToken")) {
      ViewedPage(PAGENAME.LOGGEDINHOME);
    } else {
      ViewedPage(PAGENAME.HOME);
    }

    setTimeout(() => {
      this.actionGetMinAmount();
      this.setState({
        isHomeContentLoading: false,
      });
    }, 1500);
    let loginClicked = localStorage.getItem("loginClicked");
    if (loginClicked === "checkout") {
      localStorage.removeItem("loginClicked");
    }
    if (!localStorage.getItem("accessToken")) {
      document.getElementById("body").classList.add("is-home");
    } else {
      document.getElementById("body").classList.add("home-bg");
    }
  }

  componentDidMount() {
    sessionStorage.removeItem("selectedCategory");
    if (isIE) {
      document.getElementById("body").classList.add("has-toast");
    }
  }

  componentWillUnmount() {
    document.getElementById("body").classList.remove("is-home");
    document.getElementById("body").classList.remove("home-bg");
    document.getElementById("body").classList.remove("has-toast");
  }

  getHomeBanner = (homeBanner) => {
    return homeBanner.map((banner, index) => {
      const {
        sectionName,
        filterImage,
        filterLink,
        sectionDescription,
        buttonText,
      } = banner;
      const keyId = sectionName.replace(/\s+/g, "-").toLowerCase();
      let customClass = "category";
      if (keyId && keyId.includes("snacks")) {
        customClass = "explore";
      }
      return (
        <div
          className={`home-row ${customClass}-row`}
          key={`${keyId}-${index}`}
        >
          <Container className="container-root">
            <Grid
              container
              spacing={1}
              alignItems="center"
              className="home-row-container"
            >
              <Grid item xs={12} sm={7}>
                <NavLink
                  to={filterLink}
                  id={`${keyId}-${index}`}
                  tabIndex={0}
                  className="home-remove-decor"
                  onClick={(e) => {
                    trackEvent(
                      buttonText,
                      EventNames.Event.EVENT_BUTTON_PRESS,
                      window.location.origin,
                      window.location.pathname
                    );
                    this.zipValidation(e, filterLink, sectionName);
                  }}
                  aria-label={`${sectionName}`}
                >
                  <div className="home-text">
                    <h2 className="home-title">{sectionName}</h2>
                    <div className="home-content">{sectionDescription}</div>
                    <div
                      to={filterLink}
                      id={`${keyId}-${index}-button-text`}
                      className="home-link"
                      onClick={(e) =>
                        this.zipValidation(e, filterLink, sectionName)
                      }
                      aria-label={`${buttonText} ${sectionName} button text`}
                    >
                      {buttonText}
                    </div>
                  </div>
                </NavLink>
              </Grid>
              <Grid item xs={12} sm={5}>
                <img
                  src={filterImage}
                  alt={`Choose multiple packs from ${sectionName}`}
                  role="presentation"
                />
              </Grid>
            </Grid>
          </Container>
        </div>
      );
    });
  };

  getIEAlert = () => {
    return (
      <Snackbar
        open={true}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        className="snackbar snackbar-full IE-only"
      >
        <MuiAlert variant="filled" severity="error">
          Our site works best in all modern bowsers, except Internet Explorer.
        </MuiAlert>
      </Snackbar>
    );
  };

  getBannerList = (bannerList, order) => {
    if (order === "desc") {
      bannerList.sort((a, b) => b.sectionName.localeCompare(a.sectionName));
      return [bannerList[bannerList.length - 1], ...bannerList];
    }
    return [...bannerList, bannerList[0]];
  };

  render() {
    const { customerDetails, labelListItem } = this.props;
    let homeBanner = labelListItem ? labelListItem.Home : [];
    let authHome = labelListItem ? labelListItem.authHome : [];

    let heroBannerLeft = [],
      heroBannerRight = [],
      heroBannerMobile = [];

    if (labelListItem) {
      const {
        HeroBannerLeft,
        HeroBannerRight,
        HeroBannerMobile,
      } = labelListItem;
      const leftList = HeroBannerLeft && HeroBannerLeft.length > 0;
      const rightList = HeroBannerRight && HeroBannerRight.length > 0;
      const mobileList = HeroBannerMobile && HeroBannerMobile.length > 0;
      if (leftList) {
        heroBannerLeft = this.getBannerList(HeroBannerLeft);
      }

      if (rightList) {
        heroBannerRight = this.getBannerList(HeroBannerRight, "desc");
      }

      if (mobileList) {
        heroBannerMobile = this.getBannerList(HeroBannerMobile);
      }
    }

    if (this.state.isHomeContentLoading) {
      return <Spinner />;
    }

    if (localStorage.getItem("accessToken")) {
      return (
        <>
          {this.getIEAlert()}
          <div className="home-wrapper">
            <HeroBanner
              customerDetails={customerDetails}
              authHome={authHome}
              minAmount={
                this.state.minOrder ||
                sessionStorage.getItem("minimumOrderAmount")
              }
            />
            <PopularProducts
              customheading="Popular Items"
              customerDetails={customerDetails}
              PageTitle={PAGENAME.HOME}
            />
            <Container className="container-root">
              <div className="brought-to-you auth">
                <div>
                  <img
                    className="hero-background"
                    src="https://share.snacks.com/images/logo.png"
                    alt="fritolay-logo"
                    role="presentation"
                  />
                </div>
                <div className="text">
                  Brought to you by Frito-Lay and Quaker
                </div>
                <div>
                  <img
                    className="hero-background"
                    src="https://share.snacks.com/images/quaker-logo.png"
                    alt="quaker-logo"
                    role="presentation"
                  />
                </div>
              </div>
            </Container>
          </div>
        </>
      );
    }

    return (
      <React.Fragment>
        {this.getIEAlert()}
        <div className="home-wrapper">
          <div className="hero">
            <Grid
              container
              spacing={0}
              alignItems="center"
              className="hero-banner"
            >
              <Grid item xs={12} sm={6} className="banner-lays desktop-view">
                {heroBannerLeft.length > 0 && (
                  <SnacksCarousel
                    carouselCustomClass="left"
                    content={heroBannerLeft}
                    direction="vertical"
                  />
                )}
              </Grid>
              <Grid item xs={12} sm={6} className="banner-doritos desktop-view">
                {heroBannerRight.length > 0 && (
                  <SnacksCarousel
                    carouselCustomClass="right"
                    content={heroBannerRight}
                    direction="verticalReverse"
                  />
                )}
              </Grid>
              <Grid item xs={12} sm={12} className="banner-lays mobile-view">
                {heroBannerMobile.length > 0 && (
                  <SnacksCarousel
                    carouselCustomClass="center"
                    content={heroBannerMobile}
                    direction="horizontal"
                  />
                )}
              </Grid>
              <Grid item xs={12}>
                <Tooltip placeholder="top" title="scroll to content">
                  <Button
                    className="scroll-to"
                    id="scrollToContent"
                    aria-label="scroll to content"
                    onClick={this.scrollToContent}
                  >
                    <ExpandMoreIcon />
                  </Button>
                </Tooltip>
              </Grid>
            </Grid>
          </div>
          <div id="content">
            <Container className="container-root">
              <div className="hero-text">
                <Typography variant="h1" className="hero-header">
                  your favorite snacks are here
                </Typography>
                <p className="hero-content hero-content-padding">
                  Welcome to your one stop shop for all your Frito-Lay favs.
                  Personalize orders, sample the hottest new flavors, and more.
                  Plus free shipping on orders over $
                  {this.state.minOrder ||
                    sessionStorage.getItem("minimumOrderAmount")}
                  ! Its fun, easy, and tasty.
                </p>
              </div>
            </Container>
            <div className="">{this.getHomeBanner(homeBanner)}</div>
            <Container className="container-root">
              <div className="brought-to-you">
                <div>
                  <img
                    className="hero-background"
                    src="https://share.snacks.com/images/logo.png"
                    alt="fritolay"
                    role="presentation"
                  />
                </div>
                <div className="text">
                  Brought to you by Frito-Lay and Quaker
                </div>
                <div>
                  <img
                    className="hero-background"
                    src="https://share.snacks.com/images/quaker-logo.png"
                    alt="quaker-logo"
                    role="presentation"
                  />
                </div>
              </div>
            </Container>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    minPrice: state.reducer.minAmount,
    customerDetails: state.reducer.customerDetails,
    zipResponse: state.reducer.zipResponse,
    labelListItem: state.reducer.labelListItem,
  };
};
export default connect(mapStateToProps, {
  actionGetMinAmount,
  actionGetCustomer,
  actionZipcodeVerification,
  actionLabelList,
})(Home);
