import React, { Component } from "react";
import Truncate from "react-truncate";
import { Grid, Card, Tooltip, Button, IconButton } from "@material-ui/core";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import QuantityCount from "../commonComponents/quantityCount/QuantityCount";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import { isMobile } from "react-device-detect";
import Color from "color-thief-react";
import { DisplayHeading } from "../../config/constants/content.constant";
import MultipackProductTout from "../multipackProductTout/MultipackProductTout";
import { hexToRgbA } from "./../Utils";
import UrlConstants from "../../config/UrlConstants";
import APIUtil from "../../config/APIUtil";
import {
  SelectedLinkItem,
  ClickedButton,
} from "../../config/amplitude/SnacksAmplitude";
import {
  METHOD,
  PRODUCTTYPE,
  BUTTONNAME,
} from "../../config/amplitude/Taxonomy.constants";
import "./productTout.scss";

export default class ProductTout extends Component {
  state = {
    colors: [],
    isFavourite: false,
    showSizeList: false,
  };

  openDetailView = () => {
    const { detailedInfo, productInfo, index, PageTitle } = this.props;
    ClickedButton(BUTTONNAME.PRODUCTDETAILPOPUP, METHOD.PRODUCTTILE, {
      productId: productInfo.productId,
      PageTitle,
    });
    detailedInfo(productInfo, index);
  };

  onSelectedProduct = (productInfo, availableQtyLength) => {
    const productState =
      availableQtyLength === 0 ? "OutOfStockProduct" : "Product";
    SelectedLinkItem(productState, METHOD.PRODUCTTILE, {
      ...productInfo,
      PageTitle: this.props.PageTitle,
    });
    this.props.updateSelectedProduct(
      productInfo.productId,
      productInfo.backgroundColorRgb
    );
  };

  changeFavStatus = (productKeyValue, isFavValue) => {
    const payload = {
      productKey: productKeyValue,
      isFave: isFavValue,
    };
    APIUtil.postMethod(UrlConstants.FavouriteProducts, payload, true)
      .then((response) => {
        if (response.data.success) {
          this.setState({
            isFavourite: isFavValue,
          });
        }
      })
      .catch((error) => {
        this.setState({
          isFavourite: isFavValue,
        });
      });
  };

  getQuantitySection = (primaryIndex) => {
    const {
      warningMsgFunc,
      productInfo,
      selectedItems,
      isHolidayShopProduct,
      PageTitle,
    } = this.props;
    let ProductType = PRODUCTTYPE.SNACKS;

    return productInfo.map((product, index) => {
      if (isHolidayShopProduct) {
        product.isHolidayShopProduct = true;
        ProductType = PRODUCTTYPE.HOLIDAY;
      }
      product.multipack && (ProductType = PRODUCTTYPE.VARIETY);

      return (
        <QuantityCount
          primaryProduct={productInfo[primaryIndex]}
          productInfo={product}
          key={index}
          warningMsgFunc={warningMsgFunc}
          selectedItems={selectedItems}
          sizeName={product.size}
          price={product.price}
          availableQty={product.availableQty}
          currency={productInfo[primaryIndex].currency}
          PageTitle={PageTitle}
          ProductType={ProductType}
        />
      );
    });
  };

  productAlreadyExists = (productInfo) => {
    const cartItems = JSON.parse(sessionStorage.getItem("cartItems")) || [];
    let arraySum = [];
    productInfo.forEach((product) => {
      const filteredArray = cartItems.filter(
        (item) => item.productId === product.productId
      );
      arraySum = [...arraySum, ...filteredArray];
    });
    return arraySum.length;
  };

  renderBox = (rgbaColor, data) => {
    const {
      horizontalView,
      productInfo,
      storeProductId,
      isHolidayShopProduct,
    } = this.props;
    const primaryIndex = productInfo.findIndex((obj) => obj.primaryProduct);
    const availableQtyList = productInfo.filter(
      (item) => item.availableQty && item.availableQty > 0
    );
    const { showSizeList } = this.state;
    const {
      productId,
      productDescription,
      shortDescription,
      image,
      price,
      currency,
    } = productInfo[primaryIndex];
    const prodDesc = productDescription
      ? productDescription.replace(/(<([^>]+)>)/gi, "").replace("&nbsp;", " ")
      : shortDescription
      ? shortDescription.replace(/(<([^>]+)>)/gi, "").replace("&nbsp;", " ")
      : "";
    const selectedProduct = storeProductId && storeProductId === productId;
    const styleBg = selectedProduct
      ? `linear-gradient(to bottom, ${rgbaColor} ,${data})`
      : "transparent";
    const showSelectText =
      this.productAlreadyExists(productInfo) > 0
        ? DisplayHeading.SELECTED
        : DisplayHeading.SELECT;

    return (
      <Card
        className={`product-box-bundle-card bundle-card ${
          selectedProduct ? "active" : "inactive"
        } ${horizontalView ? "horizontal-bundle-card" : ""}`}
        key={productId}
        style={{
          backgroundImage: styleBg,
        }}
      >
        {!horizontalView && (
          <Grid
            container
            spacing={0}
            justify="space-between"
            className="product-options"
          >
            <Grid item>
              {/* <IconButton
                className="star"
                id="toggleFavourite"
                aria-label="add to favourite"
                onClick={() => {
                  if (localStorage.getItem("token") !== null)
                    this.changeFavStatus(productId, !this.state.isFavourite);
                  else
                    this.setState({
                      isFavourite: !this.state.isFavourite,
                    });
                }}
              >
                {!this.state.isFavourite ? <StarBorderIcon /> : <StarIcon />}
              </IconButton> */}
            </Grid>
            <Grid item>
              <IconButton
                className="more-vert"
                aria-label={`click here to open product details of ${prodDesc}`}
                onClick={this.openDetailView}
              >
                <MoreVertIcon />
              </IconButton>
            </Grid>
          </Grid>
        )}
        <div
          className="product-content-wrapper"
          onClick={() =>
            !selectedProduct
              ? !horizontalView && !isMobile
                ? this.onSelectedProduct(
                    productInfo[primaryIndex],
                    availableQtyList.length
                  )
                : this.openDetailView()
              : null
          }
        >
          <Button
            id={`productDetailDialog-${productId}`}
            name={`productDetailDialog-${productId}`}
            aria-label="click to open product detail dialog"
            className="media-card-wrapper"
          >
            <img
              alt={`${prodDesc} ${productId}`}
              src={
                typeof image === "string"
                  ? image
                  : "../images/product-placeholder.png"
              }
            />
          </Button>
          {!horizontalView && <div className="shadow"></div>}
          <div
            className={`product-information ${
              showSizeList ? "show-scroll" : ""
            } ${availableQtyList.length > 3 ? "align-top" : ""}`}
          >
            {!selectedProduct && (
              <>
                <Tooltip placement="top" title={prodDesc}>
                  <div className="product-heading">
                    <Truncate lines={2} ellipsis="...">
                      {prodDesc}
                    </Truncate>
                  </div>
                </Tooltip>
                {!horizontalView && (
                  <div className="product-brand">
                    {`${availableQtyList.length} size(s) available`}
                  </div>
                )}
              </>
            )}
            {!horizontalView && selectedProduct && (
              <>
                <div className={`quantity-box`}>
                  {this.getQuantitySection(primaryIndex)}
                </div>
                {availableQtyList.length > 3 && !showSizeList && (
                  <ExpandMoreIcon
                    aria-label="more sizes"
                    fontSize="large"
                    className="sizes-arrow-down"
                    onClick={() => this.setState({ showSizeList: true })}
                  />
                )}
              </>
            )}

            {availableQtyList.length > 0 &&
              !horizontalView &&
              !selectedProduct && (
                <div className="product-action">
                  <Button
                    className="selected-product"
                    aria-label={`${showSelectText} ${productInfo[primaryIndex].title}`}
                  >
                    {showSelectText}
                  </Button>
                </div>
              )}
            {(!isHolidayShopProduct ||
              !sessionStorage.getItem("holidayShopStockText")) &&
              availableQtyList.length === 0 &&
              !selectedProduct && (
                <div className="selected-product out-of-stock">
                  {DisplayHeading.OUT_OF_STOCK}
                </div>
              )}
            {isHolidayShopProduct &&
              sessionStorage.getItem("holidayShopStockText") &&
              availableQtyList.length === 0 && (
                <div className="selected-product out-of-stock">
                  {sessionStorage.getItem("holidayShopStockText")}
                </div>
              )}
            {horizontalView && availableQtyList.length > 0 && (
              <div className="product-action">
                <Button
                  aria-label={`${DisplayHeading.SELECT} ${productInfo[primaryIndex].title}`}
                  className="selected-product"
                >
                  {DisplayHeading.SELECT}
                </Button>
                <div className="price">
                  <sup>{currency}</sup>
                  {price}
                </div>
              </div>
            )}
          </div>
        </div>{" "}
      </Card>
    );
  };

  render() {
    const { productKey, productInfo, horizontalView } = this.props;
    const primaryIndex = productInfo.findIndex((obj) => obj.primaryProduct);

    if (primaryIndex === -1 && productInfo[0].hasOwnProperty("isHolidayShop")) {
      let holidayShopObj = productInfo[0];
      let productObj = [
        {
          heading: holidayShopObj.title,
          shortDescription: holidayShopObj.shortDescription,
          image: holidayShopObj.image
            ? holidayShopObj.image
            : "/www/images/holiday-tout.png",
          buttonText: holidayShopObj.buttonText
            ? holidayShopObj.buttonText
            : "Enter",
          isHolidayShop: true,
        },
      ];
      return <MultipackProductTout productInfo={productObj} />;
    }

    if (primaryIndex === -1 && productInfo[0].multipackId) {
      return <MultipackProductTout productInfo={productInfo} />;
    }
    const { image } = productInfo[primaryIndex];
    if (horizontalView) {
      return this.renderBox();
    }
    let rgbaColor = "";
    return (
      <Color key={productKey} src={image} crossOrigin="Anonymous" format="hex">
        {({ data, loading }) => {
          if (productInfo[primaryIndex].backgroundColorRgb !== "") {
            rgbaColor = hexToRgbA(
              "#" + productInfo[primaryIndex].backgroundColorRgb
            );
            data = hexToRgbA(
              "#" + productInfo[primaryIndex].backgroundColorRgb
            );
          } else {
            rgbaColor = data ? hexToRgbA(data) : "#000000";
          }
          return this.renderBox(rgbaColor, data);
        }}
      </Color>
    );
  }
}
