import React, { Component } from "react";
import BottomScrollListener from "react-bottom-scroll-listener";
import { Grid, Container, Button } from "@material-ui/core";
import SectionHeading from "../commonComponents/sectionHeading/SectionHeading";
import UrlConstants from "../../config/UrlConstants";
import APIUtil from "../../config/APIUtil";
import CircularProgress from "@material-ui/core/CircularProgress";
import ProductTout from "../ProductTout/ProductTout";
import SnacksAlertMessage from "./../commonComponents/snacksAlertMessage/SnacksAlertMessage";
import ProductDetailDialog from "../pages/productDetail/ProductDetailDialog";
import "./popularProducts.scss";

export default class PopularProducts extends Component {
  state = {
    popularProductsList: [],
    isProductLoading: true,
    locationNumber: sessionStorage.getItem("_lo_No") || null,
    offset: 0,
    limit: UrlConstants.popularProductsLimit,
    showAllProducts: false,
    showAllVisible: false,
    totalProducts: 0,
    lazyLoading: false,
    showProductDetail: false,
    productDetailIndex: "",
    msg: "",
    productInfo: [],
    selectedItems: JSON.parse(sessionStorage.getItem("cartItems")) || [],
    storeProductId: "",
  };

  updateProductDetailStatus = (status) => {
    this.setState({ showProductDetail: status });
  };

  productDetail = (productInfo, index) => {
    this.setState({
      showProductDetail: true,
      productInfo,
      productDetailIndex: index,
    });
  };

  popularProductsAPICall = (lazy) => {
    const { offset, limit, locationNumber, popularProductsList } = this.state;
    let payload = {
      offset,
      limit,
      locationNumber,
    };

    if (this.props.showItems) {
      payload = {
        ...payload,
        limit: this.props.showItems,
      };
    }
    let productsData =
      !!popularProductsList && popularProductsList.length > 0
        ? popularProductsList
        : [];
    APIUtil.postMethod(UrlConstants.PopularProducts, payload, true)
      .then((response) => {
        let productListData = response.data.Groups || [];
        if (
          productsData.length > 0 &&
          !!productListData &&
          productListData.length > 0 &&
          (!!this.state.lazyLoading || this.state.showAllProducts)
        ) {
          productListData.map((prods) => productsData.push(prods));
        } else {
          productsData = productListData;
        }
        this.setState({
          popularProductsList: productsData,
          isProductLoading: false,
          lazyLoading: false,
          totalProducts: response.data && response.data.totalProducts,
          showAllVisible: lazy
            ? false
            : productsData.length < response.data.totalProducts,
        });
      })
      .catch((error) => {
        console.error("Popular Products Api", error);
      });
  };

  handleOnDocumentBottom = () => {
    const { limit, totalProducts, offset } = this.state;
    const totalCount = offset + limit;
    if (totalProducts > totalCount) {
      this.setState(
        {
          lazyLoading: true,
          showAllProducts: true,
          showAllVisible: false,
          offset: offset + UrlConstants.popularProductsLimit,
        },
        () => {
          setTimeout(() => {
            this.popularProductsAPICall("lazy");
          }, 500);
        }
      );
    }
  };

  componentDidMount() {
    this.popularProductsAPICall();
  }

  warningMsgFunc = (msg, availableQty) => {
    this.setState(
      {
        msg: msg.replace(
          /##QTY##/g,
          function () {
            return parseInt(availableQty);
          },
          document.getElementById("body").classList.add("has-toast")
        ),
      },
      () =>
        setTimeout(() => {
          this.setState({ msg: null });
          document.getElementById("body").classList.remove("has-toast");
        }, 3000)
    );
  };

  showAllProductsFunction = () => {
    this.setState(
      {
        showAllVisible: false,
        showAllProducts: true,
        lazyLoading: true,
        offset: this.state.offset + UrlConstants.popularProductsLimit,
      },
      () => this.popularProductsAPICall()
    );
  };

  getAllProducts = (popularProducts) =>
    popularProducts.map(
      (productInfo, index) =>
        !!this.eliminateOutofStock(productInfo) && (
          <Grid item key={`${productInfo.id}-${index}`} xs={6} lg={3} sm={3}>
            <ProductTout
              productKey={`${productInfo.productId}-${index}`}
              productInfo={productInfo}
              index={index}
              detailedInfo={this.productDetail}
              warningMsgFunc={this.warningMsgFunc}
              updateSelectedProduct={this.updateSelectedProduct}
              storeProductId={this.state.storeProductId}
              PageTitle={this.props.PageTitle}
            />
          </Grid>
        )
    );

  updateSelectedProduct = (storeProductId) => {
    this.setState({ storeProductId });
  };

  eliminateOutofStock = (productInfo) => {
    const cartItemIds = this.eliminateCartItem();
    const popularProducts = productInfo;
    if (!!popularProducts && popularProducts.length > 0) {
      let proInfo = [];
      if (this.props.horizontalView) {
        proInfo = popularProducts.filter(
          (item) =>
            item.availableQty &&
            item.availableQty > 0 &&
            !cartItemIds.includes(item.productId)
        );
      } else {
        proInfo = popularProducts.filter(
          (item) => item.availableQty && item.availableQty > 0
        );
      }
      if (popularProducts.length === proInfo.length) {
        return true;
      }
    }
    return false;
  };

  eliminateCartItem = () => {
    let cartItems = this.state.selectedItems;
    const items = [];
    if (!!cartItems && cartItems.length > 0) {
      cartItems.map((item) => items.push(item.productId));
    }
    return items;
  };

  getAllProductsHorizontally = (popularProducts) =>
    popularProducts &&
    popularProducts.map(
      (productInfo, index) =>
        !!this.eliminateOutofStock(productInfo) && (
          <Grid
            item
            key={`${productInfo.id}-${index}`}
            xs={12}
            lg={12}
            sm={12}
            className="popular-products-horizontal"
          >
            <ProductTout
              productKey={`${productInfo.productId}-${index}`}
              productInfo={productInfo}
              index={index}
              detailedInfo={this.productDetail}
              warningMsgFunc={this.warningMsgFunc}
              horizontalView
              updateSelectedProduct={this.updateSelectedProduct}
              storeProductId={this.state.storeProductId}
              PageTitle={this.props.PageTitle}
            />
          </Grid>
        )
    );

  selectedCartItems = (selectedItems) => {
    this.setState({ selectedItems });
  };

  render() {
    const {
      popularProductsList,
      isProductLoading,
      showAllVisible,
      showAllProducts,
      lazyLoading,
      msg,
      showProductDetail,
      productInfo,
      selectedItems,
    } = this.state;

    const { customheading, horizontalView, showSeeAllAtBottom } = this.props;

    return (
      <Container className="popular-products">
        <div className="popular-products-container">
          <div className="popular-products-container-inner">
            {msg && <SnacksAlertMessage fullWidth msg={msg} />}
            <SectionHeading
              classField="popular-products"
              heading={customheading}
              ariaLabelHeading={`User specific ${customheading}`}
              showAllTitle="See All"
              ariaLabelShowAll={`See All ${customheading}`}
              showAllPosition="right"
              isShowAllVisible={showAllVisible}
              isFunction={this.showAllProductsFunction}
              horizontalView={horizontalView}
            />

            {showProductDetail && (
              <ProductDetailDialog
                status={showProductDetail}
                statusUpdate={this.updateProductDetailStatus}
                productData={productInfo}
                selectedItems={selectedItems}
                warningMsgFunc={this.warningMsgFunc}
                selectedCartItems={this.selectedCartItems}
                horizontalView={horizontalView}
              />
            )}
            <div className="popular-products-list">
              {isProductLoading && (
                <Grid
                  container
                  justify="center"
                  style={{ minHeight: "100px", textAlign: "center" }}
                >
                  <Grid item>
                    <CircularProgress />
                  </Grid>
                </Grid>
              )}
              {showAllProducts && (
                <BottomScrollListener
                  debounce={200}
                  offset={700}
                  onBottom={this.handleOnDocumentBottom}
                />
              )}
              {!horizontalView && (
                <Grid container spacing={3}>
                  {!isProductLoading &&
                    this.getAllProducts(popularProductsList)}
                </Grid>
              )}
              {horizontalView && (
                <Grid container spacing={3}>
                  {!isProductLoading &&
                    this.getAllProductsHorizontally(popularProductsList)}
                </Grid>
              )}
            </div>

            {lazyLoading && (
              <Grid
                container
                justify="center"
                style={{ minHeight: "100px", textAlign: "center" }}
              >
                <Grid item>
                  <CircularProgress />
                </Grid>
              </Grid>
            )}
            {showAllVisible && showSeeAllAtBottom && (
              <Grid
                container
                justify="center"
                spacing={2}
                className="show-all-view"
              >
                <Grid item>
                  <Button
                    onClick={this.showAllProductsFunction}
                    aria-label={`See All ${customheading}` || "See All"}
                    className="btn-yellow-red"
                  >
                    See All
                  </Button>
                </Grid>
              </Grid>
            )}
          </div>
        </div>
      </Container>
    );
  }
}
