// Material imports
import {
  Button,
  Grid,
  IconButton,
  Typography,
  Tooltip,
  TextField,
} from "@material-ui/core";
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import { classes } from "istanbul-lib-coverage";
import React, { Component } from "react";
import { connect } from "react-redux";
import {
  actionGetMinAmount,
  actionGetCustomer,
  actionCouponCode,
  actionRemoveCouponCode,
} from "./../../../actions/ProductAction";
import Spinner from "../../Spinner";
import SnacksAlertMessage from "./../../commonComponents/snacksAlertMessage/SnacksAlertMessage";
import CustomEvent from "custom-event";
import Login from "./../../login/login";
import DialogBox from "./../../commonComponents/dialogBox/DialogBox";
import { trackEvent, EventNames } from "./../../../appinsights/EventTrack";
import QuantityCount from "./../../commonComponents/quantityCount/QuantityCount";
import { DisplayHeading } from "./../../../config/constants/content.constant";
import {
  updatePrice,
  lineItemsListObject,
  shippingCostCalculator,
} from "./../../Utils";
import APIUtil from "../../../config/APIUtil";
import UrlConstants from "../../../config/UrlConstants";
import PopularProducts from "../../popularProducts/PopularProducts";
import {
  ViewedPage,
  ClickedButton,
  AddRemoveProductToCart,
} from "../../../config/amplitude/SnacksAmplitude";
import {
  PAGENAME,
  BUTTONNAME,
  PRODUCTTYPE,
} from "../../../config/amplitude/Taxonomy.constants";
import "./cartSummary.scss";

class CartSummary extends Component {
  state = {
    cartItems: JSON.parse(sessionStorage.getItem("cartItems")) || [],
    open: false,
    message: DisplayHeading.YOUR_EMPTY_CART,
    showDeletPrompt: false,
    itemIndex: "",
    cartData: [],
    totalPrice: 0,
    minPrice: "",
    shippingMinimum: "",
    discountMinPrice: "",
    updatedQty: [],
    inventoryInvalidData: [],
    deleteClicked: false,
    snackOpen: true,
    msg: "",
    selectedItems: [],
    showLoginModal: false,
    inventoryResponse: [],
    isInventoryLoading: true,
    disableContinue: false,
    inventoryIssue: false,
    errorMsg: "",
    isInventoryAvailable: false,
    coupon: sessionStorage.getItem("coupon") || "",
    couponError: "",
    discountedAmount:
      (sessionStorage.getItem("coupon") &&
        sessionStorage.getItem("discountedAmount")) ||
      0,
    discount:
      (sessionStorage.getItem("coupon") &&
        sessionStorage.getItem("discount")) ||
      0,
    automateCoupon: "",
    spinner: false,
    showMultipackDetail: "",
    shippingFeeApiResponse: {},
    isShippingFeeApiLoading: true,
  };

  componentDidMount() {
    ViewedPage(PAGENAME.CART);
    this.productInventoryCheck();
    this.shippingFeeAPI();
    this.props.actionGetMinAmount("", "", "", "").then(() => {
      if (
        !!this.props.minPrice &&
        Object.keys(this.props.minPrice).length > 0
      ) {
        const {
          minimumOrderAmount,
          shippingMinimum,
          discountEligibility,
          discountEligibilityCoupon,
        } = this.props.minPrice;

        this.setState(
          {
            minPrice: minimumOrderAmount,
            shippingMinimum: shippingMinimum,
            discountMinPrice:
              discountEligibility || this.state.discountMinPrice,
            automateCoupon:
              discountEligibilityCoupon || this.state.automateCoupon,
          },
          () => this.getData()
        );
      }
    });
    setTimeout(function () {
      window.scrollTo(0, 0);
    }, 10);
  }

  componentWillUnmount() {
    document.getElementById("body").classList.remove("has-toast");
  }

  UNSAFE_componentWillReceiveProps() {
    if (!!this.props.inventoryData) {
      this.setState({
        inventoryInvalidData: !!this.props.inventoryData
          ? this.props.inventoryData
          : [],
      });
    }
  }

  onCouponCodeChange = (e) => this.setState({ coupon: e.target.value });

  clearCart = () => {
    trackEvent(
      EventNames.Action.ACTION_CLEAR_CART,
      EventNames.Event.EVENT_BUTTON_PRESS,
      window.location.origin,
      window.location.pathname
    );
    ClickedButton(BUTTONNAME.CLEARALLCART, PAGENAME.CART);
    sessionStorage.removeItem("total");
    sessionStorage.removeItem("cartItems");
    sessionStorage.removeItem("shippingSku");
    sessionStorage.removeItem("selectedCategory");
    sessionStorage.removeItem("selectedOrder");
    this.setState(
      {
        cartData: [],
        cartItems: [],
        totalPrice: 0,
        showDeletPrompt: false,
      },
      () => {
        this.getData();
        //fire custom event to change cart item count
        var event = new CustomEvent("cartitem");
        document.dispatchEvent(event);
      }
    );
  };

  getTotalPrice = () => {
    const { cartItems } = this.state;
    let totalPrice = 0;
    if (cartItems.length > 0) {
      cartItems.map((items) => {
        let total = items.price * items.quantity;
        totalPrice = totalPrice + total;
      });
    }
    return totalPrice;
  };

  getData = () => {
    const {
      cartItems,
      shippingMinimum,
      automateCoupon,
      coupon,
      discountMinPrice,
      inventoryIssue,
    } = this.state;

    let totalPrice = this.getTotalPrice();
    if (cartItems.length > 0) {
      if (
        shippingMinimum.length > 0 &&
        parseFloat(totalPrice) >= parseFloat(shippingMinimum) &&
        !inventoryIssue
      ) {
        this.setState(
          {
            errorMsg: "",
            disableContinue: false,
          },
          document.getElementById("body").classList.remove("has-toast")
        );
      } else if (inventoryIssue) {
        this.setState({
          disableContinue: true,
        });
      } else {
        this.setState({
          disableContinue: true,
          errorMsg: `$ ${parseFloat(shippingMinimum).toFixed(
            2
          )} Minimum Order Requirement.`,
        });
        document.getElementById("body").classList.add("has-toast");
      }
      this.calculateShippingFee(this.state.shippingFeeApiResponse);
    }

    sessionStorage.setItem("total", parseFloat(totalPrice).toFixed(2));
    sessionStorage.setItem("subtotal", parseFloat(totalPrice).toFixed(2));
    sessionStorage.setItem("tax", 0);

    this.setState(
      {
        totalPrice: parseFloat(totalPrice).toFixed(2),
        coupon:
          coupon.length === 0 &&
          discountMinPrice.length > 0 &&
          parseFloat(totalPrice) >= parseFloat(discountMinPrice)
            ? automateCoupon
            : coupon,
      },
      () => {
        if (this.state.coupon.length > 0) this.callDiscountApi();
      }
    );
  };

  removeCartItem() {
    let productId = this.state.itemIndex;
    let ProductType = PRODUCTTYPE.SNACKS;

    if (productId) {
      let cartItems = JSON.parse(sessionStorage.getItem("cartItems"));
      let deletedProduct = cartItems.filter(
        (item) => item.productId === productId
      );
      if (deletedProduct && deletedProduct[0]) {
        deletedProduct[0].multipack && (ProductType = PRODUCTTYPE.VARIETY);
        deletedProduct[0].isHolidayShopProduct &&
          (ProductType = PRODUCTTYPE.HOLIDAY);
      }
      trackEvent(
        EventNames.Action.ACTION_PRODUCT_DELETE_CART,
        EventNames.Event.EVENT_BUTTON_PRESS,
        window.location.origin,
        window.location.pathname,
        { product: deletedProduct }
      );
      AddRemoveProductToCart({
        Method: "Delete",
        PageTitle: PAGENAME.CART,
        ProductType,
        ...deletedProduct[0],
      });

      cartItems = cartItems.filter((item) => item.productId !== productId);

      this.setState(
        {
          cartItems: cartItems,
          showDeletPrompt: false,
          cartData: cartItems,
          inventoryIssue: false,
        },
        () => {
          sessionStorage.setItem(
            "cartItems",
            JSON.stringify(this.state.cartItems)
          );
          this.getData();
          this.updateAvailableQuantity(this.state.inventoryResponse);
          var event = new CustomEvent("cartitem");
          document.dispatchEvent(event);
        }
      );
      if (cartItems.length <= 0) {
        sessionStorage.removeItem("total");
        sessionStorage.removeItem("selectedOrder");
        sessionStorage.removeItem("cartItems");
        this.setState({ cartData: [], cartItems: [], totalPrice: "0.00" });
        this.getData();
      }
    }
  }

  warningMsgFunc = (msg, availableQty) => {
    this.setState(
      {
        errorMsg: msg.replace(
          /##QTY##/g,
          function () {
            return parseInt(availableQty);
          },
          document.getElementById("body").classList.add("has-toast")
        ),
      },
      () =>
        setTimeout(() => {
          this.setState({ errorMsg: null });
          document.getElementById("body").classList.remove("has-toast");
        }, 3000)
    );
  };

  selectedCartItems = (selectedItems) => {
    this.setState({ selectedItems });
  };

  passQtyToCart = (updatedQty) =>
    this.setState({ updatedQty, cartItems: updatedQty }, () => {
      this.getData();
      this.updateAvailableQuantity(this.state.inventoryResponse);
    });

  shippingFeeAPI = () => {
    APIUtil.getMethod(UrlConstants.shippingFee, true)
      .then((response) => {
        this.setState(
          {
            shippingFeeApiResponse: response.data.data || {},
            isShippingFeeApiLoading: false,
          },
          () => this.calculateShippingFee(this.state.shippingFeeApiResponse)
        );
      })
      .catch((error) => {
        console.error("Shipping Fee API failed", error);
        this.setState({
          isShippingFeeApiLoading: false,
        });
      });
  };

  calculateShippingFee = (shippingResponse) => {
    const { shippingName, shippingTax } = shippingResponse;
    const { discount, cartItems } = this.state;
    const totalPrice = this.getTotalPrice();

    let totalAmount =
      discount && discount > 0 ? totalPrice - discount : totalPrice;
    const isMultipackExist = cartItems.filter((item) => item.multipack);
    sessionStorage.removeItem("shippingSku");
    sessionStorage.removeItem("shippingSkuCost");
    sessionStorage.setItem("shippingSkuName", shippingName);

    shippingCostCalculator({
      totalAmount,
      isMultipackExist,
      shippingTax: shippingTax || [],
    });
  };

  productInventoryCheck = () => {
    const { cartItems } = this.state;
    let productIdList = [],
      holidayInventory = [],
      multipackIdList = [];

    if (cartItems && cartItems.length > 0) {
      cartItems.map((item) => {
        if (item.multipack) {
          let subItemsMultipack = [];
          item.multipackList.map((subItem) => {
            subItemsMultipack = [
              ...new Set([...subItemsMultipack, subItem.productId]),
            ];
          });
          multipackIdList = [
            ...multipackIdList,
            {
              multipackId: item.productId,
              multipackProductId: subItemsMultipack,
            },
          ];
        } else if (item.isHolidayShopProduct) {
          holidayInventory = [
            ...new Set([...holidayInventory, item.productId]),
          ];
        } else {
          productIdList = [...new Set([...productIdList, item.productId])];
        }
      });

      let payload = {
        productInventory: productIdList,
        multipackInventory: multipackIdList,
        holidayInventory: holidayInventory,
        locationId: sessionStorage.getItem("_lo_No") || null,
      };
      APIUtil.postMethod(UrlConstants.ProductInventory, payload, true)
        .then((response) => {
          this.setState(
            {
              inventoryResponse: response.data.data || [],
              isInventoryLoading: false,
            },
            () => {
              this.updateAvailableQuantity(this.state.inventoryResponse);
            }
          );
        })
        .catch((error) => {
          console.error("Product Inventory Api", error);
        });
    }
  };

  updateBtnState = (btnState) => {
    this.setState({ disableContinue: btnState, inventoryIssue: btnState });
  };

  updateAvailableQuantity(inventoryResponse) {
    let { cartItems, shippingMinimum } = this.state;
    const { productInventory, multipackInventory } = inventoryResponse;
    let price = 0;
    cartItems.map((item) => {
      if (productInventory && productInventory.length > 0 && !item.multipack) {
        let itemID = productInventory.findIndex(
          (pId) => pId.productId === item.productId
        );

        if (
          itemID >= 0 &&
          productInventory[itemID].productId === item.productId
        ) {
          item.availableQty = productInventory[itemID].availableQty;
          price = price + item.price * item.quantity;
          if (item.quantity > item.availableQty) {
            this.updateBtnState(true);
          }

          if (item.quantity === item.availableQty) {
            this.updateBtnState(false);
          }
        }
        if (itemID === -1) {
          item.availableQty = 0;
          this.updateBtnState(true);
        }
      } else if (!item.multipack) {
        item.availableQty = 0;
        this.updateBtnState(true);
      }

      if (
        item.multipack &&
        multipackInventory &&
        multipackInventory.length > 0
      ) {
        let itemID = multipackInventory.findIndex(
          (pId) => pId.multipackId === item.productId
        );
        if (itemID >= 0) {
          let subPrice = 0;
          const { multipackProductId } = multipackInventory[itemID];
          item.multipackList.map((subItem) => {
            let inventoryIndex = multipackProductId.findIndex(
              (pId) => pId.productId === subItem.productId
            );
            if (
              inventoryIndex >= 0 &&
              multipackProductId[inventoryIndex].productId === subItem.productId
            ) {
              subItem.availableQty =
                multipackProductId[inventoryIndex].availableQty;
              subPrice = subPrice + subItem.price * subItem.quantity;

              if (subItem.quantity * item.quantity > subItem.availableQty) {
                this.setState({
                  disableContinue: true,
                  inventoryIssue: true,
                  errorMsg: UrlConstants.multipackAvailableQty.replace(
                    /##QTY##/g,
                    function () {
                      return parseInt(subItem.availableQty);
                    },
                    document.getElementById("body").classList.add("has-toast")
                  ),
                });

                if (subItem.quantity * item.quantity === subItem.availableQty) {
                  this.updateBtnState(false);
                }
              }
            }
            if (inventoryIndex === -1) {
              subItem.availableQty = 0;
              this.updateBtnState(true);
            }
          });
          price = price + subPrice * item.quantity;
        }
        if (itemID === -1) {
          item.availableQty = 0;
          this.updateBtnState(true);
        }
      } else if (item.multipack) {
        item.availableQty = 0;
        this.updateBtnState(true);
      }
    });
    if (parseFloat(price) < parseFloat(shippingMinimum)) {
      this.setState({ disableContinue: true });
    }

    this.setState({ cartItems });
    sessionStorage.setItem("cartItems", JSON.stringify(cartItems));
  }

  getCouponName = (message, checkDiscount) => {
    const { coupon, automateCoupon, discountMinPrice, totalPrice } = this.state;
    return coupon.length > 0 &&
      coupon === automateCoupon &&
      discountMinPrice.length > 0 &&
      parseFloat(totalPrice) < parseFloat(discountMinPrice)
      ? ""
      : message.length === 0 &&
        coupon !== automateCoupon &&
        discountMinPrice.length > 0 &&
        parseFloat(totalPrice) >= parseFloat(discountMinPrice) &&
        checkDiscount === true
      ? automateCoupon
      : coupon;
  };

  failCoupon = (message, checkDiscount) => {
    sessionStorage.removeItem("cartId");
    sessionStorage.removeItem("coupon");
    sessionStorage.removeItem("discountedAmount");
    sessionStorage.removeItem("discount");
    this.setState(
      {
        coupon: this.getCouponName(message, checkDiscount),
        couponError: message.toString(),
        discountedAmount: 0,
        discount: 0,
      },
      () => {
        if (
          this.state.coupon.length != 0 &&
          checkDiscount === true &&
          this.state.automateCoupon.length > 0
        )
          this.callDiscountApi();
      }
    );
  };

  callDiscountApi = () => {
    const {
      coupon,
      shippingMinimum,
      discountMinPrice,
      totalPrice,
      automateCoupon,
    } = this.state;
    if (
      coupon.length === 0 ||
      (shippingMinimum.length > 0 &&
        parseFloat(totalPrice) < parseFloat(shippingMinimum)) ||
      (coupon.length > 0 &&
        coupon === automateCoupon &&
        discountMinPrice.length > 0 &&
        parseFloat(totalPrice) < parseFloat(discountMinPrice))
    ) {
      this.failCoupon("", false);
    } else {
      let data = {
        voucherName: coupon,
        cartAmount: totalPrice,
      };

      if (!!sessionStorage.getItem("cartId")) {
        data = {
          ...data,
          ...{
            cartId: sessionStorage.getItem("cartId"),
          },
        };
      }
      this.setState(
        {
          spinner: true,
        },
        () => {
          this.props.actionCouponCode(data).then(() => {
            this.setState(
              {
                spinner: false,
              },
              () => {
                if (!!this.props.couponData.message) {
                  this.failCoupon(this.props.couponData.message, false);
                } else {
                  sessionStorage.removeItem("totalDiscount");
                  sessionStorage.setItem("coupon", this.state.coupon);
                  sessionStorage.setItem(
                    "cartId",
                    this.props.couponData.cartId
                  );
                  sessionStorage.setItem(
                    "discountedAmount",
                    this.props.couponData.discountDetails[0].discountedAmount
                  );
                  sessionStorage.setItem(
                    "discount",
                    this.props.couponData.discountDetails[0].discount
                  );

                  this.setState({
                    couponError: "",
                    discountedAmount: this.props.couponData.discountDetails[0]
                      .discountedAmount,
                    discount: this.props.couponData.discountDetails[0].discount,
                  });
                }
              }
            );
          });
        }
      );
    }
  };

  callRemoveDiscountApi = (fromRemove) => {
    //if (this.state.coupon !== sessionStorage.getItem("coupon"))
    let data = {
      cartId: sessionStorage.getItem("cartId"),
      voucherToken: this.state.coupon,
    };

    this.setState(
      {
        spinner: true,
      },
      () => {
        this.props.actionRemoveCouponCode(data).then(() => {
          this.setState(
            {
              spinner: false,
            },
            () => {
              if (fromRemove) {
                let temp = this.state.coupon;
                this.setState(
                  {
                    coupon: "",
                  },
                  () =>
                    this.failCoupon(
                      "",
                      temp === this.state.automateCoupon ? false : true
                    )
                );
              } else this.failCoupon("", false);
            }
          );
        });
      }
    );
  };

  inventoryObject = () => {
    const { inventoryResponse } = this.state;
    let inventoryData = [];
    inventoryResponse.forEach((e, index) => {
      e.msg = null;
      inventoryData[e.productId] = e;
    });
    return inventoryData;
  };

  renderMultipackDetail = (cartItem) => {
    let inventoryInvalidData = [];
    const { inventoryResponse, isInventoryLoading } = this.state;

    const { multipackList, productId } = cartItem;

    return (
      <Grid item xs={12} sm={12} md={12} className="multipack-cart-detail">
        <div className="multipack-cart-detail-inner">
          <Button
            id={`closeMultipackDetail`}
            aria-label={`Close Multipack Detail`}
            className="multipack-cart-detail-close"
            onClick={() =>
              this.setState({
                showMultipackDetail: "",
              })
            }
          >
            <i className="fa fa-times"></i>
          </Button>
          {multipackList.map((multipackItem, index) => {
            inventoryResponse &&
              inventoryResponse.multipackInventory.length > 0 &&
              inventoryResponse.multipackInventory.map((item) => {
                if (item.multipackId === productId) {
                  item.multipackProductId.map((subItem) => {
                    if (subItem.productId === multipackItem.productId) {
                      inventoryInvalidData = subItem;
                    }
                  });
                }
              });

            return (
              <Grid container key={index}>
                <Grid item className="multipack-cart-detail-img">
                  <img
                    alt={`multipack-${multipackItem.productDescription}-
                    ${multipackItem.productId}`}
                    src={multipackItem.image}
                    role="presentation"
                  />
                </Grid>
                <Grid item xs>
                  <div className="title-product">
                    {multipackItem.productDescription}
                  </div>
                  <div className="count">
                    Quantity: {multipackItem.quantity}
                  </div>
                </Grid>
                {!isInventoryLoading && inventoryInvalidData.length === 0 && (
                  <div className="font-14 text-danger cart-summary-error">
                    {DisplayHeading.NOT_AVAILABLE_FLAG_MSG}
                  </div>
                )}
              </Grid>
            );
          })}
        </div>
      </Grid>
    );
  };

  renderItem(cartItem, index) {
    let ProductType = PRODUCTTYPE.SNACKS;
    const {
      price,
      quantity,
      productId,
      image,
      size,
      currency,
      availableQty,
      productTitle,
      productDescription,
      shortDescription,
      multipackList,
      multipack,
      isHolidayShopProduct,
    } = cartItem;
    /** total price calculation based on product count group */
    const { updatedQty, inventoryResponse, isInventoryLoading } = this.state;

    if (isInventoryLoading) {
      return <Spinner />;
    }

    const { productInventory, multipackInventory } = inventoryResponse;
    let totalProductPrice = price * quantity;
    updatedQty.map((item) => {
      if (productId === item.productId) {
        totalProductPrice = item.quantity * item.price;
      }
    });

    let inventoryInvalidData = [],
      exceededAvailableQty = false,
      outOfStock = false,
      multipackInventoryFailed = false;

    if (productInventory && productInventory.length > 0) {
      productInventory.map((item) => {
        if (item.productId === productId) {
          inventoryInvalidData = item;

          if (item.availableQty < quantity) {
            exceededAvailableQty = true;
          }
          if (item.availableQty === 0) {
            outOfStock = true;
          }
        }
      });
    }

    multipack && (ProductType = PRODUCTTYPE.VARIETY);

    isHolidayShopProduct && (ProductType = PRODUCTTYPE.HOLIDAY);

    if (multipack && multipackInventory && multipackInventory.length > 0) {
      let inventoryIndex = multipackInventory.findIndex(
        (pId) => pId.multipackId === productId
      );
      if (inventoryIndex >= 0) {
        const { multipackProductId } = multipackInventory[inventoryIndex];
        if (multipackList.length !== multipackProductId.length) {
          multipackInventoryFailed = true;
        }
      }
    }

    const productDesc =
      productTitle ||
      `${productDescription} - ${size}` ||
      `${shortDescription} - ${size}`;

    let showError = "";
    if (!cartItem.multipack && !isInventoryLoading) {
      inventoryInvalidData.length === 0 &&
        (showError = DisplayHeading.NOT_AVAILABLE_FLAG_MSG);

      exceededAvailableQty &&
        (showError = UrlConstants.qtyAvailableMsg.replace(
          /##QTY##/g,
          function () {
            return parseInt(availableQty);
          }
        ));

      outOfStock && (showError = "This product is out of stock");
    }
    return (
      <Grid container key={index} className="bundle-card-summary">
        <Grid item xs={3} sm={2} md={2} className="summary-row-img">
          <div className="summary-img">
            {typeof image === "string" ? (
              <img
                alt={`${productDesc}${
                  cartItem.multipack ? `- ${index + 1}` : ""
                }`}
                src={image}
                role="presentation"
              />
            ) : (
              <img
                alt={productDesc}
                src={image.Product_Image}
                role="presentation"
              />
            )}
          </div>
        </Grid>
        <Grid item xs={9} sm={10} md={10}>
          <Grid container spacing={2} className="summary-row-info">
            <Grid item xs={12} sm={6} md={6}>
              <div className="title-product">{productDesc}</div>
            </Grid>
            <Grid item xs={6} sm={3} md={3} className="cart-count">
              {!multipackInventoryFailed && (
                <QuantityCount
                  primaryProduct={cartItem}
                  productInfo={cartItem}
                  warningMsgFunc={this.warningMsgFunc}
                  selectedItems={this.selectedCartItems}
                  passQtyToCart={this.passQtyToCart}
                  availableQty={availableQty}
                  currency={currency}
                  PageTitle={PAGENAME.CART}
                  ProductType={ProductType}
                />
              )}
            </Grid>
            <Grid item xs={2} sm={1} md={1} className="cart-delete">
              <Tooltip placement="top" title={"delete"}>
                <IconButton
                  id={"summaryDelete" + index}
                  aria-label="Product Delete dialog box will be open"
                  className={classes.margin + " btn-trash"}
                  onClick={() => {
                    this.setState({
                      showDeletPrompt: true,
                      itemIndex: productId,
                      deleteClicked: true,
                    });
                  }}
                >
                  <i className="fa fa-trash-alt"></i>
                </IconButton>
              </Tooltip>
            </Grid>
            <Grid item xs={4} sm={2} md={2} className="cart-pric">
              <div className="price">
                <sup>{currency}</sup>
                {totalProductPrice > 0 ? totalProductPrice.toFixed(2) : 0}
              </div>
            </Grid>
            {cartItem.multipack && (
              <Grid
                item
                xs={12}
                sm={12}
                md={12}
                className="multipack-cart-buttons"
              >
                <Button
                  id={`multipackDetails${index}`}
                  aria-label={`multipack details ${index}`}
                  onClick={() =>
                    this.setState({
                      showMultipackDetail: cartItem.id,
                    })
                  }
                >
                  Details
                </Button>
                {!multipackInventoryFailed && (
                  <>
                    <span className="devider">|</span>
                    <Button
                      id={`multipackEdit${index}`}
                      aria-label={`edit multipack ${index}`}
                      onClick={() => this.editMultipack(cartItem.id)}
                    >
                      Edit
                    </Button>
                  </>
                )}
              </Grid>
            )}
          </Grid>
        </Grid>
        <Grid item xs={12} sm={12} md={12}>
          {inventoryInvalidData.msg && (
            <div className="font-14 text-danger card-summary-error">
              {inventoryInvalidData.msg}
            </div>
          )}

          {showError && (
            <div className="font-14 text-danger cart-summary-error">
              {showError}
            </div>
          )}
          {cartItem.multipack &&
            !isInventoryLoading &&
            multipackInventoryFailed && (
              <div className="font-14 text-danger cart-summary-error">
                {DisplayHeading.MULTIPACK_PRODUCT}
              </div>
            )}
        </Grid>
        {cartItem.multipack &&
          this.state.showMultipackDetail === cartItem.id &&
          this.renderMultipackDetail(cartItem)}
      </Grid>
    );
  }

  editMultipack = (multipackId) => {
    const { cartItems } = this.state;
    const filterMultipack = cartItems.filter((item) => item.id === multipackId);

    sessionStorage.setItem(
      "multipackCartItems",
      JSON.stringify(filterMultipack[0].multipackList)
    );
    sessionStorage.setItem("multipackEditId", multipackId);
    this.props.history.push(`/varietypack`);
  };

  moveToNextStep(selectedCategory, isData, isload) {
    sessionStorage.removeItem("isPayment");
    let minAmount = sessionStorage.getItem("shippingMinimum");
    ClickedButton(BUTTONNAME.CHECKOUT, PAGENAME.CART);
    if (!minAmount) {
      this.props
        .actionGetMinAmount("", "", "", "")
        .then(() => {
          this.minAmountValidation(selectedCategory, isload);
        })
        .catch((productData) => {
          this.setState({ spinner: false });
        });
    } else {
      this.minAmountValidation(selectedCategory, isload);
    }
  }

  minAmountValidation = (selectedCategory, isload) => {
    var totalData = sessionStorage.getItem("total")
      ? sessionStorage.getItem("total")
      : 0;
    let shippingChargeAmount = this.props.minPrice
      ? this.props.minPrice.shippingCharges
      : sessionStorage.getItem("shippingCharges");
    this.setState({ shippingCharges: shippingChargeAmount });
    var minOrder = this.props.minPrice
      ? this.props.minPrice.shippingMinimum
      : sessionStorage.getItem("shippingMinimum");
    if (
      parseFloat(totalData) >= parseFloat(minOrder) ||
      this.state.activeStep == 0
    ) {
      this.checkZipCodeValidation();
    }
  };

  checkZipCodeValidation() {
    let cartItems = !!sessionStorage.getItem("cartItems")
      ? JSON.parse(sessionStorage.getItem("cartItems"))
      : [];
    let formData = !!sessionStorage.getItem("formValues")
      ? JSON.parse(sessionStorage.getItem("formValues"))
      : null;
    let loggedInUser = localStorage.getItem("accessToken");

    if (loggedInUser) {
      let shippingCountry = null,
        shippingState = null,
        shippingCity = null,
        zipCode = null,
        shippingAddressLine1 = null,
        shippingAddressLine2 = null,
        payload = {};

      if (formData) {
        shippingCountry = formData.shippingCountry;
        shippingState = formData.shippingState;
        shippingCity = formData.shippingCity;
        zipCode = formData.shippingZipCode;
        shippingAddressLine1 = formData.shippingAddressLine1;
        shippingAddressLine2 = formData.shippingAddressLine2;
      }

      payload = {
        destination: {
          postalArea: sessionStorage.getItem("Zipcode"),
          stateOrProvinceName: shippingState,
        },
        Address: {
          Region: `${shippingCity}, ${shippingState}, ${zipCode}`,
          CountryCode: shippingCountry,
          AddressLine: [`${shippingAddressLine1} ${shippingAddressLine2}`],
        },
        documentDate: new Date(),
        transactionType: "SALE",
        sourceLocationId: sessionStorage.getItem("_lo_No") || null,
        isAddress:
          shippingAddressLine1 &&
          shippingCountry &&
          shippingState &&
          shippingCity &&
          zipCode,
        lineItems: lineItemsListObject(cartItems),
        shippingSku: sessionStorage.getItem("shippingSku"),
      };
      if (sessionStorage.getItem("coupon")) {
        payload = {
          ...payload,
          voucherToken: sessionStorage.getItem("coupon"),
        };
      }

      if (shippingAddressLine1) {
        APIUtil.postMethod(UrlConstants.orderTax, payload, true)
          .then((response) => {
            if (
              response.data.success === true &&
              response.data.zipSuccess === true
            ) {
              response.data.zipcode = sessionStorage.getItem("Zipcode");
              sessionStorage.setItem("taxFree", response.data.taxFree);

              if (response.data.taxSuccess === true) {
                response.data.totalTaxAmount = response.data.data.tax
                  ? response.data.data.tax.totalTaxAmount
                  : "";
                updatePrice(response);
              }

              this.props.history.push("/checkout");
            } else {
              this.props.history.push("/checkout");
            }
          })
          .catch((response) => {
            this.setState({ errorMsg: null, spinner: false });
          });
      } else {
        if (this.props.customerDetails && this.props.customerDetails !== null) {
          let primaryShippingAddress = this.props.customerDetails.accountAddresses.filter(
            (data) => data.primaryAddr === true
          );
          this.setState({
            errorMsg: null,
            spinner: false,
            zipcodeValidData: primaryShippingAddress[0],
          });
          //window.location = "/checkout";
          this.props.history.push("/checkout");
        }
      }
    } else {
      if (this.props.history.location.pathname === "/cart") {
        sessionStorage.setItem("prevUrl", "/cart-preview");
        this.setState({
          errorMsg: null,
          spinner: false,
          zipcodeValidData: sessionStorage.getItem("Zipcode"),
          showLoginModal: true,
        });
      } else {
        this.setState({
          errorMsg: null,
          spinner: false,
          zipcodeValidData: sessionStorage.getItem("Zipcode"),
          showLoginModal: false,
        });
      }
    }
  }

  hideLoginModal = () => {
    this.setState({
      showLoginModal: false,
    });
  };

  renderCommonButtons = (placement) => {
    const { disableContinue, totalPrice, shippingMinimum } = this.state;
    return (
      <div className="button-group">
        {placement !== "header" && (
          <a
            onClick={() => {
              sessionStorage.removeItem("isPayment");
              ClickedButton(BUTTONNAME.CONTINUESHOPPING, PAGENAME.CART);
              trackEvent(
                EventNames.Action.ACTION_KEEP_SHOPPING,
                EventNames.Event.EVENT_BUTTON_PRESS,
                window.location.origin,
                window.location.pathname
              );
              this.props.history.push("/productlist");
            }}
            className="btn-secondary"
            id={`ContinueShopping-${placement}`}
            aria-label={`Click Continue Shopping from ${placement}`}
            tabIndex={0}
          >
            {DisplayHeading.CONTINUE_SHOPPING}
          </a>
        )}

        <Button
          className={`btn-primary show-web ${
            disableContinue ||
            (shippingMinimum.length > 0 &&
              parseFloat(totalPrice) < parseFloat(shippingMinimum))
              ? "disable-btn"
              : ""
          }`}
          disabled={
            disableContinue ||
            (shippingMinimum.length > 0 &&
              parseFloat(totalPrice) < parseFloat(shippingMinimum))
          }
          id={`Checkout-${placement}`}
          aria-label={`Click Checkout from ${placement}`}
          onClick={(e) => this.moveToNextStep(e, true)}
        >
          {DisplayHeading.CHECKOUT}
        </Button>
      </div>
    );
  };

  render() {
    const {
      couponError,
      cartItems,
      showLoginModal,
      message,
      discount,
      coupon,
    } = this.state;

    const deleteMsg = this.state.deleteClicked
      ? " Are you sure you would like to remove this item from the cart?"
      : "Are you sure you would like to remove all items from the cart?";

    return (
      <div className="card-summary">
        {this.state.spinner === true && <Spinner />}
        <Dialog
          open={showLoginModal}
          onClose={() => this.hideLoginModal()}
          maxWidth={false}
          scroll={"body"}
          classes={{
            paper: "modal-container modal-paper modal-login is-cart",
            root: "modal-stack-top",
          }}
          aria-labelledby="signIn-dialogBox"
        >
          <DialogContent>
            <Login
              changeForgotToLogin={() => {}}
              fromForgot={false}
              closeLoginDialog={() => this.hideLoginModal()}
            />
            <Button
              className="icn-close"
              id="LoginClose"
              aria-label="Login Dialog Close"
              onClick={() => this.hideLoginModal()}
            >
              <i className="fa fa-times"></i>
            </Button>
          </DialogContent>
        </Dialog>

        <SnacksAlertMessage fullWidth msg={this.state.errorMsg} />

        <div className="sticky-head has-button has-button-mobile">
          <div className="sticky-head-inner">
            <h1 className="sticky-title">{DisplayHeading.REVIEW_YOUR_CART}</h1>
            {this.renderCommonButtons("header")}
          </div>
        </div>
        <Grid container spacing={2} className="card-summary-wrapper">
          <Grid item xs={12} sm={8} lg={8}>
            <div className="card-summary-container">
              <div className="card-summary-header">
                <h2>YOUR CART</h2>

                {cartItems.length > 0 && (
                  <Button
                    className="clear-cart"
                    id="emptyCart"
                    aria-label={DisplayHeading.CLEAR_CART}
                    onClick={() =>
                      this.setState({
                        showDeletPrompt: true,
                        deleteClicked: false,
                      })
                    }
                  >
                    {DisplayHeading.CLEAR_CART}
                  </Button>
                )}
              </div>
              <div className="card-summary-content">
                {!cartItems.length === 0 ? (
                  <Grid
                    container
                    spacing={2}
                    alignItems="center"
                    className="card-summary-head"
                  >
                    <Grid item>
                      <div className="summary-img"></div>
                    </Grid>
                    <Grid item xs={12} sm container alignItems="center">
                      <Grid item xs={5} sm={5} md={5}>
                        <Typography variant="h4" className="card-summary-title">
                          Product Name
                        </Typography>
                      </Grid>
                      <Grid item xs={3} sm={3} md={3} className="text-center">
                        <Typography variant="h4" className="card-summary-title">
                          Quantity
                        </Typography>
                      </Grid>
                      <Grid
                        item
                        xs={3}
                        sm={3}
                        md={3}
                        align="start"
                        className="card-summary-action"
                      >
                        <Grid container spacing={2}>
                          <Grid item xs={12} sm container>
                            <Typography
                              variant="h4"
                              className="card-summary-title cost-title"
                            >
                              Cost
                            </Typography>
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid item xs={1} sm={1} md={1}></Grid>
                    </Grid>
                  </Grid>
                ) : (
                  ""
                )}
                <div className="card-summary-loop">
                  {cartItems.length === 0 ? (
                    <div className="card-no-item">{message}</div>
                  ) : (
                    cartItems.map((item, index) => {
                      if (item) {
                        return this.renderItem(item, index);
                      } else {
                        return "";
                      }
                    })
                  )}
                  {cartItems.length === 0 ? (
                    ""
                  ) : (
                    <div>
                      <div
                        className={`discount-apply ${
                          discount != 0 ? "applied" : ""
                        }`}
                      >
                        <div className="discount-label">
                          {discount == 0 ? (
                            <div>Do you have a Promo code?</div>
                          ) : (
                            <div>
                              Promo code{" "}
                              <span className="code">({coupon})</span> applied
                            </div>
                          )}
                        </div>
                        <div className="discount-apply-input">
                          {discount == 0 ? (
                            <TextField
                              error={couponError.length > 0}
                              id={
                                couponError.length === 0
                                  ? "outlined-basic"
                                  : "outlined-error-helper-text"
                              }
                              className="coupon-code"
                              label="Enter Code"
                              defaultValue={coupon}
                              variant="outlined"
                              onChange={this.onCouponCodeChange}
                              value={coupon}
                            />
                          ) : (
                            <div className="coupon-buttons">
                              <Button
                                className="btn-link"
                                id={`changeDiscount`}
                                aria-label={`Change Discount`}
                                onClick={() =>
                                  this.callRemoveDiscountApi(false)
                                }
                              >
                                change
                              </Button>
                              <Button
                                className="btn-link"
                                id={`removeDiscount`}
                                aria-label={`Remove Discount`}
                                onClick={() => this.callRemoveDiscountApi(true)}
                              >
                                remove
                              </Button>
                            </div>
                          )}
                        </div>
                        <div className="discount-apply-button">
                          {this.state.discount == 0 ? (
                            <Button
                              className="btn-primary"
                              disabled={
                                this.state.coupon == "" ||
                                (this.state.shippingMinimum.length > 0 &&
                                  parseFloat(this.state.totalPrice) <
                                    parseFloat(this.state.shippingMinimum))
                              }
                              onClick={this.callDiscountApi}
                              id={`applyDiscount`}
                              aria-label={`apply discount`}
                            >
                              Apply
                            </Button>
                          ) : (
                            <span className="discount-price">
                              (-<sup>$</sup>
                              {this.state.discount != 0
                                ? this.state.discount
                                : "0.0"}
                              )
                            </span>
                          )}
                        </div>
                        {this.state.couponError.length > 0 && (
                          <div className="coupon-error">
                            {this.state.couponError}
                          </div>
                        )}
                      </div>
                      <div className="card-summary-final">
                        <div className="card-summary-information">
                          <span>{DisplayHeading.ALL_CART_FAVORITES}</span>
                          <span>{`${DisplayHeading.CART_FREE_SHIPPING}${this.state.minPrice}!`}</span>
                        </div>
                        <div className="card-summary-total">
                          <span className="title"> Total </span>
                          <span className="amount">
                            <sup>$</sup>
                            {this.state.discountedAmount != 0
                              ? this.state.discountedAmount
                              : this.state.totalPrice}
                          </span>
                        </div>
                      </div>
                    </div>
                  )}
                </div>

                {/* Dialog for delete confirmation */}
                <DialogBox
                  dialogClose={() =>
                    this.setState({
                      showDeletPrompt: false,
                      deleteClicked: false,
                    })
                  }
                  dialogOpen={this.state.showDeletPrompt}
                  dialogHeading={deleteMsg}
                  dialogWarning
                  dialogCloseConfirm={() => {
                    this.state.deleteClicked
                      ? this.removeCartItem()
                      : this.clearCart();
                  }}
                  ariaLabelYes={
                    this.state.deleteClicked
                      ? "Yes I want to remove the product"
                      : "Yes I want to empty my cart"
                  }
                  ariaLabelCancel={
                    this.state.deleteClicked
                      ? "No I don't want to remove the product"
                      : "No I don't want to empty my cart"
                  }
                />
              </div>
              <div className="card-summary-page-footer">
                <div className="card-summary-page-footer-inner">
                  {this.renderCommonButtons("footer")}
                </div>
              </div>
              <Grid item xs={12} sm={12} lg={12} className="card-summary-notes">
                <div className="summary-text-info">
                  {DisplayHeading.ALL_SALES_NOTE}
                </div>
                <div className="thankyou-note">
                  {DisplayHeading.COVID_WARNING}
                </div>
              </Grid>
            </div>
          </Grid>
          <Grid item xs={12} sm={4} lg={4} className="card-summary-popular">
            <div className="card-summary-container horizontal-product-list">
              <PopularProducts
                horizontalView
                customheading="SIMILAR PRODUCTS YOU MAY LIKE"
                customerDetails={this.props.customerDetails}
                showItems={15}
                PageTitle={PAGENAME.CART}
              />
            </div>
          </Grid>
        </Grid>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    minPrice: state.reducer.minAmount,
    customerDetails: state.reducer.customerDetails,
    couponData: state.reducer.couponData,
  };
};

export default connect(mapStateToProps, {
  actionGetMinAmount,
  actionGetCustomer,
  actionCouponCode,
  actionRemoveCouponCode,
})(CartSummary);
