import React, { Component } from "react";
import {
  Button,
  FormControl,
  Card,
  Grid,
  InputLabel,
  OutlinedInput,
  Typography,
} from "@material-ui/core";
import { ErrorMessage, Formik, Form } from "formik";
import * as Yup from "yup";
import { DisplayHeading } from "./../../../config/constants/content.constant";

class EditContact extends Component {
  state = {
    initialValues: null,
    submitDisabled: true,
  };

  componentDidMount() {
    this.setState({ initialValues: this.props.customerDetails });
  }

  render() {
    return (
      <div className="profile-info has-contact">
        {/* Contact info */}
        {!!this.state.initialValues ? (
          <Formik
            //key={this.state.initialValues.shipping.length}
            enableReinitialize
            initialValues={this.state.initialValues}
            validationSchema={Yup.object().shape({
              customerfirstname: Yup.string()
                .trim()
                .required("First Name is required.")
                .min(2, "Name must be at least 2 characters.")
                .test(
                  "customerfirstname",
                  "First Name must be only alphabets",
                  function (value) {
                    if (value === null || value === "") {
                      return true; // As it is not required.
                    }
                    var regexp = /^[a-zA-Z.&' ]*$/;
                    if (!regexp.test(value)) {
                      return false;
                    }
                    return true;
                  }
                ),
              customerlastname: Yup.string()
                .trim()
                .required("Last Name is required.")
                .min(2, "Name must be at least 2 characters.")
                .test(
                  "customerlastname",
                  "Last Name must be only alphabets",
                  function (value) {
                    if (value === null || value === "") {
                      return true; // As it is not required.
                    }
                    var regexp = /^[a-zA-Z.&' ]*$/;
                    if (!regexp.test(value)) {
                      return false;
                    }
                    return true;
                  }
                ),
              customerPhone1: Yup.string()
                .trim()
                .nullable()
                .required("Phone number is required.")
                .test(
                  "customerPhone1",
                  "Please enter a valid phone number.",
                  function (value) {
                    if (value === null || value === "") {
                      return true; // As it is not required.
                    }
                    var regexp = /((\([0-9]{3}\))|[0-9]{3})[\s-]?[\0-9]{3}[\s-]?[0-9]{4}$/;
                    if (!regexp.test(value)) {
                      return false;
                    }
                    return true;
                  }
                )
                .min(10, "Phone Number must be 10 digits")
                .max(10, "Phone Number must be 10 digits"),
            })}
            onSubmit={(values, { resetForm }) => {
              this.props.handleSubmit(values);
            }}
          >
            {({
              resetForm,
              setFieldValue,
              setFieldTouched,
              errors,
              values,
              touched,
            }) => (
              <Form
                id="startEditContact"
                name="form1"
                autoComplete="startEditContact"
              >
                <Grid container spacing={6} justify="space-between">
                  <Grid item xs={12} sm={12}>
                    <label
                      htmlFor="prevent_autofill_Contact"
                      className="heading-hidden"
                    >
                      Auto Fill
                    </label>
                    <input
                      type="text"
                      name="prevent_autofill_Contact"
                      readOnly
                      id="prevent_autofill_Contact"
                      value=""
                      style={{ display: "none" }}
                    />
                    {/* Contact info */}
                    <Card className="profile-card profile-card-form">
                      <Grid
                        container
                        spacing={2}
                        alignItems="center"
                        className="title-mini title-pad"
                      >
                        <Grid item xs container>
                          <Typography variant="h2" component="h2">
                            {DisplayHeading.CONTACT_INFORMATION}
                          </Typography>
                        </Grid>
                      </Grid>
                      <Grid
                        container
                        spacing={6}
                        className="form-card-container"
                      >
                        {/* First Name */}
                        <Grid item xs={12} sm={6}>
                          <FormControl
                            fullWidth
                            className="form-group"
                            variant="outlined"
                          >
                            <InputLabel
                              className="form-label"
                              htmlFor="customerfirstname"
                            >
                              First Name<span className="text-danger">*</span>
                            </InputLabel>
                            <OutlinedInput
                              autoComplete="customerfirstname"
                              id="customerfirstname"
                              name="customerfirstname"
                              autoFocus
                              inputProps={{
                                "aria-required": "true",
                                role: "alert",
                                "aria-invalid": "false",
                              }}
                              value={values.customerfirstname}
                              onChange={(e) =>
                                setFieldValue(
                                  `customerfirstname`,
                                  e.target.value
                                )
                              }
                              onBlur={() => {
                                setFieldTouched(`customerfirstname`, true);
                                this.setState({ submitDisabled: false });
                              }}
                              error={Boolean(
                                touched.customerfirstname &&
                                  errors.customerfirstname
                              )}
                            />
                            <ErrorMessage
                              component="div"
                              name={`customerfirstname`}
                              className="text-danger"
                              role="alert"
                            />
                          </FormControl>
                        </Grid>

                        {/* Last Name */}
                        <Grid item xs={12} sm={6}>
                          <FormControl
                            fullWidth
                            className="form-group"
                            variant="outlined"
                          >
                            <InputLabel
                              className="form-label"
                              htmlFor="customerlastname"
                            >
                              Last Name<span className="text-danger">*</span>
                            </InputLabel>
                            <OutlinedInput
                              autoComplete="customerlastname"
                              id="customerlastname"
                              name="customerlastname"
                              inputProps={{
                                "aria-required": "true",
                                role: "alert",
                                "aria-invalid": "false",
                              }}
                              value={values.customerlastname}
                              onChange={(e) =>
                                setFieldValue(
                                  `customerlastname`,
                                  e.target.value
                                )
                              }
                              onBlur={() => {
                                setFieldTouched(`customerlastname`, true);
                                this.setState({ submitDisabled: false });
                              }}
                              error={Boolean(
                                touched.customerlastname &&
                                  errors &&
                                  errors.customerlastname
                              )}
                            />
                            <ErrorMessage
                              component="div"
                              name={`customerlastname`}
                              className="text-danger"
                              role="alert"
                            />
                          </FormControl>
                        </Grid>
                        {/* Phone */}
                        <Grid item xs={12} sm={6}>
                          <FormControl
                            fullWidth
                            className="form-group"
                            variant="outlined"
                          >
                            <InputLabel
                              className="form-label"
                              htmlFor="customerPhone1"
                            >
                              Phone<span className="text-danger">*</span>
                            </InputLabel>
                            <OutlinedInput
                              autoComplete="customerPhone1"
                              id="customerPhone1"
                              name="customerPhone1"
                              inputProps={{
                                "aria-required": "true",
                                role: "alert",
                                "aria-invalid": "false",
                              }}
                              value={values.customerPhone1}
                              onChange={(e) =>
                                setFieldValue(`customerPhone1`, e.target.value)
                              }
                              onBlur={() => {
                                setFieldTouched(`customerPhone1`, true);
                                this.setState({ submitDisabled: false });
                              }}
                              error={Boolean(
                                touched &&
                                  touched.customerPhone1 &&
                                  errors &&
                                  errors.customerPhone1
                              )}
                            />
                            <ErrorMessage
                              component="div"
                              name={`customerPhone1`}
                              className="text-danger"
                              role="alert"
                            />
                          </FormControl>
                        </Grid>
                        <Grid item xs={12} sm={12}>
                          *At this time changing email address isn't permissible
                        </Grid>
                      </Grid>
                      <div className="text-right">
                        <Button
                          style={{ color: "#BD390D" }}
                          type="button"
                          onClick={() => this.props.setProfile(true)}
                          variant="contained"
                          className="btn-link"
                          id="CancelContact"
                          aria-label="Cancel Contact Info"
                        >
                          {" "}
                          Cancel{" "}
                        </Button>
                        <Button
                          style={{ color: "#BD390D" }}
                          type="submit"
                          variant="contained"
                          id="SaveContact"
                          className="btn-link"
                          aria-label="Save Contact Info"
                          disabled={this.state.submitDisabled}
                        >
                          {" "}
                          Save{" "}
                        </Button>
                      </div>
                    </Card>
                    <Grid item xs={12} sm={12}>
                      <Grid item></Grid>
                    </Grid>
                  </Grid>
                </Grid>
              </Form>
            )}
          </Formik>
        ) : (
          ""
        )}
      </div>
    );
  }
}
export default EditContact;
