import {
  Button,
  Snackbar,
  FormControl,
  FormControlLabel,
  Grid,
  InputLabel,
  OutlinedInput,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  RadioGroup,
  Radio,
} from "@material-ui/core";
import { ErrorMessage, Formik } from "formik";
import React from "react";
import { withRouter } from "react-router-dom";
import * as Yup from "yup";
import { actionZipcodeVerification } from "../../../actions/ProductAction";
import APIUtil from "../../../config/APIUtil";
import UrlConstants from "../../../config/UrlConstants";
import MuiAlert from "@material-ui/lab/Alert";
import { connect } from "react-redux";
import usStateData from "../../data/states_list.json";
import AutoCompleteSelect from "../../commonComponents/autocompletetext/AutoCompleteSelect";
import {
  updatePrice,
  getOrderTaxPayload,
  addressVerification,
} from "./../../Utils";
import { MessagesUtil } from "../../../config/constants/content.constant";

const countryOptions = [{ value: "US", label: "United States" }];
const zipRegex = /^[0-9\b]+$/;

class ChangeNewAddress extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      billingFirstname: "",
      billingLastname: "",
      billingAddressLine1: null,
      billingAddressLine2: "",
      billingCity: "",

      intialAddress: this.props.intialAddress
        ? {
            ...this.props.intialAddress,
            billingFirstname: this.getFirstName(
              this.props.intialAddress.billingFirstname
            ),
            billingLastname: this.getLastName(
              this.props.intialAddress.billingLastname
            ),
          }
        : {
            billingState: JSON.parse(sessionStorage.getItem("formChangeValues"))
              ? JSON.parse(sessionStorage.getItem("formChangeValues"))
                  .billingState
              : "",

            billingCity: JSON.parse(sessionStorage.getItem("formChangeValues"))
              ? JSON.parse(sessionStorage.getItem("formChangeValues"))
                  .billingCity
              : "",

            billingFirstname: JSON.parse(
              sessionStorage.getItem("formChangeValues")
            )
              ? JSON.parse(sessionStorage.getItem("formChangeValues"))
                  .billingFirstname
              : "",
            billingLastname: JSON.parse(
              sessionStorage.getItem("formChangeValues")
            )
              ? JSON.parse(sessionStorage.getItem("formChangeValues"))
                  .billingLastname
              : "",
            billingAddressLine1: JSON.parse(
              sessionStorage.getItem("formChangeValues")
            )
              ? JSON.parse(sessionStorage.getItem("formChangeValues"))
                  .billingAddressLine1
              : "",
            billingAddressLine2: JSON.parse(
              sessionStorage.getItem("formChangeValues")
            )
              ? JSON.parse(sessionStorage.getItem("formChangeValues"))
                  .billingAddressLine2
              : "",
            billingZipCode: sessionStorage.getItem("Zipcode"),
            billingCountry:
              JSON.parse(sessionStorage.getItem("formChangeValues")) &&
              JSON.parse(sessionStorage.getItem("formChangeValues"))
                .billingCountry.length > 0
                ? JSON.parse(sessionStorage.getItem("formChangeValues"))
                    .billingCountry
                : countryOptions[0].value,
          },
      isAddress: null,
      submitDisabled: false,
      ZipfailMessage: sessionStorage.getItem("errorPaymentMsg"),
      addressVerifyData: [],
      selectedAddress: 0,
      addressModal: false,
      addrKey: 0,
    };
    this.nameFieldValidation.bind(this);
  }

  getLastName(str) {
    if (!!str) {
      var n = str.trim().split(" "); //split by space

      if (n.length > 1) {
        n.shift();
      }
      return n.join(" "); //Get last element
    }
    return "";
  }

  getFirstName(str) {
    if (!!str) {
      var res = str.trim().split(" "); //split by space
      if (res.length >= 1) {
        return res[0];
      }
    }
    return "";
  }

  componentDidMount() {
    var response = this.props.zipcodeValidData;
    if (
      !!response &&
      !!response.data &&
      response.data.success === true &&
      response.data.zipSuccess === true
    ) {
      if (!UrlConstants.isTaxFree) {
        updatePrice(response);
        // sessionStorage.setItem('Zipcode', response);
        if (response.data.taxSuccess === true) {
          setTimeout(() => {
            this.props.getTaxtLoader(false, response);
          }, 100);
        } else {
          if (!this.state.ZipfailMessage) {
            setTimeout(() => {
              this.props.getTaxtLoader(false, response);
            }, 100);
            this.setState({
              submitDisabled: true,
              ZipfailMessage: response.data.taxErrorMessage,
            });
          } else {
            if (
              !response.data.addressSuccess &&
              !response.data.candidateFound
            ) {
              if (!!response.data.addressErrorMessage) {
                this.setState(
                  {
                    submitDisabled: true,
                    ZipfailMessage: response.data.addressErrorMessage,
                  },
                  () => {
                    document.getElementById("body").classList.add("has-toast");
                  }
                );
              }
            }
          }
        }
      } else {
        sessionStorage.setItem(
          "totalAmount",
          response.data.data.tax.totalAmount ||
            sessionStorage.getItem("total") ||
            0
        );
        sessionStorage.setItem(
          "totalDiscount",
          response.data.data.tax.totalDiscount || 0
        );
        if (!response.data.addressSuccess && !response.data.candidateFound) {
          if (!!response.data.addressErrorMessage) {
            this.setState(
              {
                submitDisabled: true,
                ZipfailMessage: response.data.addressErrorMessage,
              },
              () => {
                document.getElementById("body").classList.add("has-toast");
              }
            );
          }
        }
      }
    } else {
      if (!!response && !!response.data && !this.state.ZipfailMessage) {
        setTimeout(() => {
          this.props.getTaxtLoader(false, null);
        }, 100);
        this.setState({
          submitDisabled: true,
          ZipfailMessage: !!response.data ? response.data.zipErrorMessage : "",
        });
      }
    }

    if (sessionStorage.getItem("shippingInvalid") == "yes") {
      this.setState({ submitDisabled: true });
    }
  }

  componentWillUnmount() {
    document.getElementById("body").classList.remove("has-toast");
  }

  zip = (e, setFieldValue) => {
    if (zipRegex.test(e.target.value) || e.target.value === "") {
      if (e.target.value.toString().length <= 5) {
        setFieldValue("billingZipCode", e.target.value);
        //sessionStorage.setItem('Zipcode', e.target.value);
      }
    }
    //this.setState({ submitDisabled: false });
  };

  /**
   *
   * @description Validate the Zipcode by user input
   * @memberof Registration
   */
  handleZipFieldBlur = (e, handleBlur, values) => {
    handleBlur(e);
    this.checkTaxDetails(values, "", "billing");
  };

  checkTaxDetails(values, isInput, isAddress) {
    if (!!values.billingZipCode && values.billingZipCode.length == "5") {
      var objData = getOrderTaxPayload(values, isAddress);
      //if(isInput){
      objData["isAddress"] = true;
      if (isAddress == "billing") {
        objData["isBillingAddress"] = true;
      }
      //}
      let url = UrlConstants.orderTax;
      let defaultErrorMsg = UrlConstants.errorMsg;
      this.setState({ submitDisabled: true });
      //Validate Zipcode & Adress. Fetch Tax Amount
      APIUtil.postMethod(url, objData, true)
        .then((response) => {
          addressVerification(response);
          if (
            response.data.success === true &&
            response.data.zipSuccess === true
          ) {
            this.setState({ ZipfailMessage: response.data.zipErrorMessage });
            document.getElementById("body").classList.remove("has-toast");

            if (
              !response.data.addressSuccess &&
              !response.data.candidateFound
            ) {
              if (!!response.data.addressErrorMessage) {
                response.data.zipcode = values.billingZipCode;
                response.data.totalTaxAmount = 0;
                this.props.getTaxtLoader(false, response);
                this.setState(
                  {
                    submitDisabled: true,
                    ZipfailMessage: response.data.addressErrorMessage,
                  },
                  () => {
                    document.getElementById("body").classList.add("has-toast");
                  }
                );
                if (isAddress == "billing") {
                  sessionStorage.setItem("billingInvalid", "yes");
                }
                return false;
              }
            } else {
              if (isAddress == "billing") {
                sessionStorage.setItem("billingInvalid", "no");
                this.setState({ submitDisabled: false });
              }
              if (!!response.data.candidateFound) {
                response.data.zipcode = values.billingZipCode;
                sessionStorage.setItem("taxZipCode", values.shippingZip);
                response.data.totalTaxAmount = 0;
                this.props.getTaxtLoader(false, response);
                var addrArray =
                  !!response.data.data &&
                  !!response.data.data.address &&
                  !!response.data.data.address.data &&
                  !!response.data.data.address.data.XAVResponse
                    ? response.data.data.address.data.XAVResponse.Candidate
                    : [];
                var finalAdata = addrArray;
                if (!Array.isArray(addrArray)) {
                  finalAdata = [addrArray];
                }
                this.setState({ addressVerifyData: finalAdata }, () =>
                  this.setState({
                    isAddress: isAddress ? isAddress : null,
                    addressModal: true,
                  })
                );
                return false;
              }
            }

            if (!response.data.taxFree) {
              if (response.data.taxSuccess === true) {
                updatePrice(response);
                response.data.zipcode = values.billingZipCode;
                response.data.totalTaxAmount = response.data.data.tax
                  ? response.data.data.tax.totalTaxAmount
                  : "";
                setTimeout(() => {
                  this.props.getTaxtLoader(false, response);
                }, 100);
              }
            } else {
              sessionStorage.setItem(
                "totalAmount",
                response.data.data.tax.totalAmount ||
                  sessionStorage.getItem("total") ||
                  0
              );
              sessionStorage.setItem(
                "totalDiscount",
                response.data.data.tax.totalDiscount || 0
              );
              this.setState({
                ZipfailMessage: response.data.taxErrorMessage,
              });
              if (response.data.taxErrorMessage) {
                this.setState({
                  submitDisabled: true,
                });
              }
              response.data.zipcode = values.shippingZipCode;
              response.data.totalTaxAmount = 0;
              this.props.getTaxtLoader(false, response);
              setTimeout(() => {
                this.setState({ ZipfailMessage: null });
              }, 3000);
            }
          } else {
            response.data.zipcode = values.billingZipCode;
            response.data.totalTaxAmount = 0;
            setTimeout(() => {
              this.props.getTaxtLoader(false, response);
            }, 100);
            this.setState(
              {
                submitDisabled: true,
                ZipfailMessage: response.data.zipErrorMessage,
              },
              () => {
                document.getElementById("body").classList.add("has-toast");
              }
            );
          }
        })
        .catch((response) => {
          if (response.data) {
            response.data.zipcode = values.billingZipCode;
            response.data.totalTaxAmount = 0;
          }
          this.setState(
            {
              ZipfailMessage:
                "Please check that your city, state and zip code match for your shipping address.",
              submitDisabled: true,
            },
            () => {
              document.getElementById("body").classList.add("has-toast");
            }
          );
          this.props.getTaxtLoader(false, response);
        });
    } else {
      this.setState({ submitDisabled: true });
    }
  }

  getAdressLine(e, index) {
    var city = "";
    var state = "";
    var country = "";
    var zipcode = "";
    var addressLine = !!e.AddressLine ? e.AddressLine : "";
    if (Array.isArray(e.AddressLine)) {
      addressLine =
        (!!e.AddressLine[0] ? e.AddressLine[0] : "") +
        (!!e.AddressLine[1] ? ", " + e.AddressLine[1] : "");
    }
    if (!!e.CountryCode) {
      country = e.CountryCode;
    }
    if (!!e.PoliticalDivision1) {
      state = e.PoliticalDivision1;
    }
    if (!!e.PoliticalDivision2) {
      city = e.PoliticalDivision2;
    }
    if (!!e.PostcodePrimaryLow) {
      zipcode = e.PostcodePrimaryLow;
    }
    const addressLabel =
      (!!addressLine ? addressLine : "") +
      (!!city ? " " + city : "") +
      (!!state ? " " + state : "") +
      (!!country ? ", " + country : "") +
      (!!zipcode ? " " + zipcode : "");
    return (
      <FormControlLabel
        onChange={(e) => this.setState({ selectedAddress: index })}
        value={index}
        key={`${"address1" + index}`}
        control={<Radio />}
        label={addressLabel}
      />
    );
  }

  manageSuggestedAddress() {
    const addressData = !!this.state.addressVerifyData[
      this.state.selectedAddress
    ]
      ? this.state.addressVerifyData[this.state.selectedAddress]
          .AddressKeyFormat
      : "";
    var intialAddress = !!sessionStorage.getItem("formChangeValues")
      ? JSON.parse(sessionStorage.getItem("formChangeValues"))
      : {};
    if (this.state.isAddress != "billing") {
      if (Array.isArray(addressData.AddressLine)) {
        intialAddress.billingAddressLine1 = addressData.AddressLine[0];
        intialAddress.billingAddressLine2 =
          addressData.AddressLine.length > 1 ? addressData.AddressLine[1] : "";
      } else {
        intialAddress.billingAddressLine1 = addressData.AddressLine;
        intialAddress.billingAddressLine2 = "";
      }
      intialAddress.billingCountry = !!addressData.CountryCode
        ? addressData.CountryCode
        : "";
      intialAddress.billingState = !!addressData.PoliticalDivision1
        ? addressData.PoliticalDivision1
        : "";
      intialAddress.billingCity = !!addressData.PoliticalDivision2
        ? addressData.PoliticalDivision2
        : "";
      intialAddress.billingZipCode = !!addressData.PostcodePrimaryLow
        ? addressData.PostcodePrimaryLow
        : "";
    }
    if (this.state.billingSameAsShipping || this.state.isAddress == "billing") {
      if (Array.isArray(addressData.AddressLine)) {
        intialAddress.billingAddressLine1 = addressData.AddressLine[0];
        intialAddress.billingAddressLine2 =
          addressData.AddressLine.length > 1 ? addressData.AddressLine[1] : "";
      } else {
        intialAddress.billingAddressLine1 = addressData.AddressLine;
        intialAddress.billingAddressLine2 = "";
      }
      intialAddress.billingCountry = !!addressData.CountryCode
        ? addressData.CountryCode
        : "";
      intialAddress.billingState = !!addressData.PoliticalDivision1
        ? addressData.PoliticalDivision1
        : "";
      intialAddress.billingCity = !!addressData.PoliticalDivision2
        ? addressData.PoliticalDivision2
        : "";
      intialAddress.billingZipCodeCode = !!addressData.PostcodePrimaryLow
        ? addressData.PostcodePrimaryLow
        : "";
    }
    this.setState(
      {
        intialAddress,
        selectedAddress: 0,
        addrKey: this.state.addrKey + 1,
        addressModal: false,
      },
      () =>
        this.checkTaxDetails(
          this.state.intialAddress,
          true,
          this.state.isAddress
        )
    );
  }

  handleClose = () => {
    this.setState({ ZipfailMessage: "" });
    document.getElementById("body").classList.remove("has-toast");
  };

  nameFieldValidation = (e, setFieldValue) => {
    this.setState({ [e.target.id]: "" });
    var re = /^[a-zA-Z '.]*$/;
    if (!re.test(e.target.value)) {
      if (
        e.target.id === "firstname" ||
        e.target.id === "billingFirstname" ||
        e.target.id === "billingFirstname"
      ) {
        const eventValue = e.target.id;
        this.setState(
          { [e.target.id]: "First Name must be only alphabets." },
          () => {
            setTimeout(() => {
              this.setState({ [eventValue]: null }, () => {});
            }, 3000);
          }
        );
      } else {
        const eventValue = e.target.id;
        this.setState(
          { [e.target.id]: "Last Name must be only alphabets." },
          () => {
            setTimeout(() => {
              this.setState({ [eventValue]: null }, () => {});
            }, 3000);
          }
        );
      }
      if (e.target.value === "") {
        setFieldValue([e.target.id], e.target.value);
        this.setState({ [e.target.id]: "" });
      }
    } else if (e.target.value || e.target.value === "") {
      if (this.state.billingSameAsShipping) {
        if (e.target.id === "billingFirstname") {
          setFieldValue("billingFirstname", e.target.value);
        }
        if (e.target.id === "billingLastname") {
          setFieldValue("billingLastname", e.target.value);
        }
      }
      setFieldValue([e.target.id], e.target.value);
    }
  };

  addressFieldValidation = (e, values, setFieldValue, handleBlur) => {
    this.setState({ [e.target.id]: "" });
    var re = /^[a-zA-Z0-9 '.#,/'-]*$/;
    if (!re.test(e.target.value)) {
      if (
        e.target.id === "billingAddressLine1" ||
        e.target.id === "billingAddressLine1"
      ) {
        const eventValue = e.target.id;
        this.setState(
          { [e.target.id]: "Address Line 1 must be only alphabets." },
          () => {
            setTimeout(() => {
              this.setState({ [eventValue]: null });
            }, 3000);
          }
        );
      } else if (
        e.target.id === "billingAddressLine2" ||
        e.target.id === "billingAddressLine2"
      ) {
        const eventValue = e.target.id;
        this.setState(
          { [e.target.id]: "Address Line 2 must be only alphabets." },
          () => {
            setTimeout(() => {
              this.setState({ [eventValue]: null });
            }, 3000);
          }
        );
      } else if (
        e.target.id === "billingCity" ||
        e.target.id === "billingCity"
      ) {
        const eventValue = e.target.id;
        this.setState({ [e.target.id]: "City must be only alphabets." }, () => {
          setTimeout(() => {
            this.setState({ [eventValue]: null });
          }, 3000);
        });
      }
      if (e.target.value === "") {
        setFieldValue([e.target.id], e.target.value);
        this.setState({ [e.target.id]: "" });
      }
    } else if (e.target.value || e.target.value === "") {
      if (this.state.billingSameAsShipping) {
        if (e.target.id === "billingAddressLine1") {
          setFieldValue("billingAddressLine1", e.target.value);
        }
        if (e.target.id === "billingAddressLine2") {
          setFieldValue("billingAddressLine2", e.target.value);
        }
        if (e.target.id === "billingCity") {
          setFieldValue("billingCity", e.target.value);
        }
        //setFieldValue( "billingCity",  e.target.value);
      }
      if (e.target.id === "billingAddressLine1") {
        values.billingAddressLine1 = e.target.value;
      }
      if (e.target.id === "billingCity") {
        values.billingCity = e.value;
        this.checkTaxDetails(e, handleBlur, values, true);
      }
      setFieldValue([e.target.id], e.target.value);
    }
  };

  validateEmail = (email) => {
    const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  };

  render() {
    var addressPattern = /\bP(ost|ostal)?([ \.]*(O|0)(ffice)?)?([ \.]*Box)\b/i;
    return (
      <div>
        {/* {this.props.spinner ? <Spinner /> : ''} */}
        <Formik
          key={this.state.addrKey}
          initialValues={this.state.intialAddress}
          enableReinitialize={true}
          validationSchema={Yup.object().shape({
            billingAddressLine1: Yup.string()
              .trim()
              .required("Address Line 1 is Required.")
              .max(30, MessagesUtil.ADDRESSLINE_MAX_LIMIT),

            billingAddressLine2: Yup.string()
              .nullable()
              .notRequired()
              .max(30, MessagesUtil.ADDRESSLINE_MAX_LIMIT),

            billingFirstname: Yup.string()
              .trim()
              .required("First Name is required.")
              .min(2, "Name must be at least 2 characters.")
              .max(30, MessagesUtil.NAME_MAX_LIMIT),

            billingLastname: Yup.string()
              .trim()
              .min(2, "Name must be at least 2 characters.")
              .required("Last Name is required.")
              .max(30, MessagesUtil.NAME_MAX_LIMIT),

            billingZipCode: Yup.string()
              .trim()
              .matches(/^[0-9]{5}$/, "Zip code must be exactly 5 digits.")
              .required("Zip is required"),

            billingCountry: Yup.string()
              .trim()
              .required("Country is required."),
            billingState: Yup.string().trim().required("State is required."),
            billingCity: Yup.string().trim().required("City is required."),
          })}
          onSubmit={(fields) => {
            sessionStorage.setItem("formChangeValues", JSON.stringify(fields));
            this.props.saveBillingAddress(fields);
          }}
        >
          {(props) => {
            const {
              values,
              touched,
              errors,
              handleChange,
              handleBlur,
              handleSubmit,
              setFieldValue,
              setFieldTouched,
            } = props;

            return (
              <form
                id="startChangeNewAddress"
                name="form1"
                onSubmit={handleSubmit}
                onChange={sessionStorage.setItem(
                  "formChangeValues",
                  JSON.stringify(values)
                )}
              >
                <div className="checkout-content-body-inner full-view inner-view">
                  {/* zipcode valication */}
                  {this.state.ZipfailMessage ? (
                    <Snackbar
                      open={true}
                      anchorOrigin={{ vertical: "top", horizontal: "center" }}
                      className="snackbar snackbar-full"
                    >
                      <MuiAlert
                        variant="filled"
                        onClose={this.handleClose}
                        severity="error"
                      >
                        {this.state.ZipfailMessage}
                      </MuiAlert>
                    </Snackbar>
                  ) : (
                    ""
                  )}

                  {/**  Shipping information */}
                  <fieldset className="form-card">
                    <Grid container spacing={2} className="form-card-container">
                      {/** First Name */}
                      <Grid item xs={12} sm={6}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel
                            className="form-label"
                            htmlFor="billingFirstname"
                          >
                            First Name
                            <span className="text-danger">*</span>
                          </InputLabel>
                          <OutlinedInput
                            id="billingFirstname"
                            name="billingFirstname"
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                            onChange={(e) =>
                              this.nameFieldValidation(e, setFieldValue)
                            }
                            onBlur={handleBlur}
                            autoFocus
                            error={
                              touched.billingFirstname &&
                              Boolean(errors.billingFirstname)
                            }
                            value={values.billingFirstname}
                          />
                          {this.state.billingFirstname ? (
                            <div className="text-danger" role="alert">
                              {this.state.billingFirstname}
                            </div>
                          ) : (
                            <ErrorMessage
                              component="div"
                              name="billingFirstname"
                              className="text-danger"
                            />
                          )}
                        </FormControl>
                      </Grid>
                      {/** Last Name  */}
                      <Grid item xs={12} sm={6}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel
                            className="form-label"
                            htmlFor="billingLastname"
                          >
                            Last Name
                            <span className="text-danger">*</span>
                          </InputLabel>
                          <OutlinedInput
                            id="billingLastname"
                            name="billingLastname"
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                            onChange={(e) =>
                              this.nameFieldValidation(e, setFieldValue)
                            }
                            onBlur={handleBlur}
                            error={
                              touched.billingLastname &&
                              Boolean(errors.billingLastname)
                            }
                            value={values.billingLastname}
                          />
                          {this.state.billingLastname ? (
                            <div className="text-danger" role="alert">
                              {this.state.billingLastname}
                            </div>
                          ) : (
                            <ErrorMessage
                              component="div"
                              name="billingLastname"
                              className="text-danger"
                            />
                          )}
                        </FormControl>
                      </Grid>

                      {/** Address Line 1 */}
                      <Grid item xs={12} sm={12}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel
                            className="form-label"
                            htmlFor="billingAddressLine1"
                          >
                            Address Line 1<span className="text-danger">*</span>
                          </InputLabel>

                          <OutlinedInput
                            id="billingAddressLine1"
                            name="billingAddressLine1"
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                            value={values.billingAddressLine1}
                            onChange={(e) =>
                              this.addressFieldValidation(
                                e,
                                values,
                                setFieldValue
                              )
                            }
                            onBlur={(e) => {
                              if (
                                !!values.billingAddressLine1 &&
                                !!values.billingCountry &&
                                !!values.billingState &&
                                !!values.billingCity &&
                                !!values.billingZipCode
                              ) {
                                this.checkTaxDetails(values, true, "billing");
                              }
                            }}
                            error={
                              touched.billingAddressLine1 &&
                              Boolean(errors.billingAddressLine1)
                            }
                          />

                          {this.state.billingAddressLine1 ? (
                            <div className="text-danger" role="alert">
                              {this.state.billingAddressLine1}
                            </div>
                          ) : (
                            <ErrorMessage
                              component="div"
                              name="billingAddressLine1"
                              className="text-danger"
                            />
                          )}
                        </FormControl>
                      </Grid>
                      {/** Address Line 2 */}
                      <Grid item xs={12} sm={12}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel
                            className="form-label"
                            htmlFor="billingAddressLine2"
                          >
                            Address Line 2
                          </InputLabel>

                          <OutlinedInput
                            id="billingAddressLine2"
                            name="billingAddressLine2"
                            value={values.billingAddressLine2}
                            onChange={(e) =>
                              this.addressFieldValidation(
                                e,
                                values,
                                setFieldValue
                              )
                            }
                            onBlur={() => {
                              if (
                                !!values.billingAddressLine1 &&
                                !!values.billingCountry &&
                                !!values.billingState &&
                                !!values.billingCity &&
                                !!values.billingZipCode
                              ) {
                                this.checkTaxDetails(values, true, "billing");
                              }
                            }}
                          />
                          {this.state.billingAddressLine2 ? (
                            <div className="text-danger" role="alert">
                              {this.state.billingAddressLine2}
                            </div>
                          ) : (
                            <ErrorMessage
                              component="div"
                              name="billingAddressLine2"
                              className="text-danger"
                            />
                          )}
                        </FormControl>
                      </Grid>
                      {/* billingCountry Drop */}
                      <Grid item xs={12} sm={6}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <label htmlFor="billingCountry">
                            <AutoCompleteSelect
                              id="billingCountry"
                              label="Country"
                              touched={touched.billingCountry}
                              options={countryOptions}
                              defaultValue={countryOptions.find((option) => {
                                if (option.value === values.billingCountry)
                                  return option;
                              })}
                              value={countryOptions.find((option) => {
                                if (option.value === values.billingCountry)
                                  return option;
                              })}
                              onChange={(e, value) => {
                                if (value !== null) {
                                  setFieldValue("billingCountry", value.value);
                                  if (this.state.billingSameAsShipping) {
                                    setFieldValue(
                                      "billingCountry",
                                      value.value
                                    );
                                  }
                                  values.billingCountry = value.value;
                                }
                              }}
                              onBlur={(e, value) => {
                                setFieldTouched("billingCountry", true);

                                if (
                                  !!values.billingAddressLine1 &&
                                  !!values.billingCountry &&
                                  !!values.billingState &&
                                  !!values.billingCity &&
                                  !!values.billingZipCode
                                ) {
                                  this.checkTaxDetails(values, true, "billing");
                                }
                              }}
                              error={errors.billingCountry}
                            />
                          </label>
                          <ErrorMessage
                            component="div"
                            name="billingCountry"
                            className="text-danger"
                          />
                        </FormControl>
                      </Grid>
                      {/** State */}
                      <Grid item xs={12} sm={6}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <label htmlFor="billingState">
                            <AutoCompleteSelect
                              id="billingState"
                              label="State"
                              touched={touched.billingState}
                              options={usStateData}
                              defaultValue={usStateData.find((option) => {
                                if (option.value === values.billingState)
                                  return option;
                              })}
                              value={usStateData.find((option) => {
                                if (option.value === values.billingState)
                                  return option;
                              })}
                              onChange={(e, value) => {
                                if (value !== null) {
                                  setFieldValue("billingState", value.value);
                                  if (this.state.billingSameAsShipping) {
                                    setFieldValue("billingState", value.value);
                                  }
                                  values.billingState = value.value;
                                }
                              }}
                              onBlur={(e, value) => {
                                setFieldTouched("billingState", true);

                                if (
                                  !!values.billingAddressLine1 &&
                                  !!values.billingCountry &&
                                  !!values.billingState &&
                                  !!values.billingCity &&
                                  !!values.billingZipCode
                                ) {
                                  this.checkTaxDetails(values, true, "billing");
                                }
                              }}
                              error={errors.billingState}
                            />
                          </label>

                          <ErrorMessage
                            component="div"
                            name="billingState"
                            className="text-danger"
                          />
                        </FormControl>
                      </Grid>
                      {/** City */}
                      <Grid item xs={12} sm={6}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel
                            className="form-label"
                            htmlFor="billingCity"
                          >
                            City <span className="text-danger">*</span>
                          </InputLabel>
                          <OutlinedInput
                            id="billingCity"
                            name="billingCity"
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                            onChange={(e) =>
                              this.addressFieldValidation(
                                e,
                                values,
                                setFieldValue,
                                handleBlur
                              )
                            }
                            onBlur={(e) => {
                              if (
                                !!values.billingAddressLine1 &&
                                !!values.billingCountry &&
                                !!values.billingState &&
                                !!values.billingCity &&
                                !!values.billingZipCode
                              ) {
                                this.checkTaxDetails(values, true, "billing");
                              }
                            }}
                            error={
                              touched.billingCity && Boolean(errors.billingCity)
                            }
                            value={values.billingCity}
                          />
                          {this.state.billingCity ? (
                            <div className="text-danger" role="alert">
                              {this.state.billingCity}
                            </div>
                          ) : (
                            <ErrorMessage
                              component="div"
                              name="billingCity"
                              className="text-danger"
                            />
                          )}
                        </FormControl>
                      </Grid>
                      {/** ZipCode */}
                      <Grid item xs={12} sm={6}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel
                            className="form-label"
                            htmlFor="billingZipCode"
                          >
                            Zip <span className="text-danger">*</span>
                          </InputLabel>
                          <OutlinedInput
                            id="billingZipCode"
                            name="billingZipCode"
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                            ref={(input) => {
                              this.nameInput = input;
                            }}
                            autoComplete="new-password"
                            onChange={(e) => this.zip(e, setFieldValue)}
                            onBlur={(e) => {
                              this.handleZipFieldBlur(
                                e,
                                handleBlur,
                                values,
                                false
                              );
                            }}
                            error={
                              touched.billingZipCode &&
                              Boolean(errors.billingZipCode)
                            }
                            value={values.billingZipCode}
                          />
                          <ErrorMessage
                            component="div"
                            name="billingZipCode"
                            className="text-danger"
                          />
                        </FormControl>
                      </Grid>
                    </Grid>
                  </fieldset>

                  <div className="button-group button-group-inner text-right">
                    <Button
                      className="btn btn-secondary btn-sm"
                      id="CancelBillingAddress"
                      aria-label="Cancel Billing Address"
                      onClick={() => {
                        this.props.cancelChangeAddress();
                      }}
                    >
                      Cancel
                    </Button>

                    <Button
                      type="submit"
                      disabled={
                        this.state.submitDisabled ||
                        values.billingFirstname.trim().length < 2 ||
                        values.billingLastname.trim().length < 2
                      }
                      className="btn btn-primary btn-sm"
                      id="SaveBillingAddress"
                      aria-label="Save Billing Address"
                    >
                      Save
                    </Button>
                  </div>
                </div>
              </form>
            );
          }}
        </Formik>
        {/* relevant address modal */}
        <Dialog
          open={this.state.addressModal}
          onClose={() => this.setState({ addressModal: false })}
          aria-labelledby="address-modal"
          disableBackdropClick
          disableEscapeKeyDown
          maxWidth={false}
          classes={{
            paper: "modal-address",
            root: "modal-stack-top",
          }}
        >
          {/* Dialog Content */}
          <DialogTitle id="address-modal" className="modal-address-title">
            Relevant Address
          </DialogTitle>
          <DialogContent dividers className="modal-address-content">
            <RadioGroup
              aria-label="Select Relevant Address"
              name="SelectRelevantAddress"
              value={this.state.selectedAddress}
              //onChange={(e) => this.setState({selectedAddress : e.target.value})}
            >
              {!!this.state.addressVerifyData &&
              this.state.addressVerifyData.length > 0 ? (
                this.state.addressVerifyData.map((e, index) => {
                  return this.getAdressLine(e.AddressKeyFormat, index);
                })
              ) : (
                <h3>Address is not available!!</h3>
              )}
            </RadioGroup>
          </DialogContent>
          <DialogActions className="modal-address-action">
            <Button
              onClick={() =>
                this.setState({
                  sDisabledBillingfield: false,
                  selectedAddress: 0,
                  addressModal: false,
                })
              }
              variant="contained"
              color="primary"
              className={" btn-secondary"}
              aria-label="Cancel"
            >
              Cancel
            </Button>
            {!!this.state.addressVerifyData &&
            this.state.addressVerifyData.length > 0 ? (
              <Button
                onClick={() => this.manageSuggestedAddress()}
                color="primary"
                className={" btn-primary"}
                id="applyAddress"
                aria-label="Apply Address"
              >
                {" "}
                Apply{" "}
              </Button>
            ) : (
              ""
            )}
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    zipResponse: state.reducer.zipResponse,
  };
};
export default connect(mapStateToProps, { actionZipcodeVerification })(
  withRouter(ChangeNewAddress)
);
