import {
  Button,
  Snackbar,
  FormControl,
  FormControlLabel,
  Grid,
  InputLabel,
  OutlinedInput,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  RadioGroup,
  Radio,
} from "@material-ui/core";
import { ErrorMessage, Formik } from "formik";
import React from "react";
import { withRouter } from "react-router-dom";
import * as Yup from "yup";
import { actionZipcodeVerification } from "../../../actions/ProductAction";
import APIUtil from "../../../config/APIUtil";
import UrlConstants from "../../../config/UrlConstants";
import MuiAlert from "@material-ui/lab/Alert";
import { connect } from "react-redux";
import usStateData from "../../data/states_list.json";
import AutoCompleteSelect from "../../commonComponents/autocompletetext/AutoCompleteSelect";
import {
  updatePrice,
  getOrderTaxPayload,
  addressVerification,
} from "./../../Utils";

const countryOptions = [{ value: "US", label: "United States" }];
const zipRegex = /^[0-9\b]+$/;

class AddNewAddress extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      shippingFirstname: "",
      shippingLastname: "",
      shippingAddressline1: null,
      shippingAddressLine2: "",
      shippingCity: "",

      intialAddress: this.props.intialAddress
        ? this.props.intialAddress
        : {
            shippingState: JSON.parse(sessionStorage.getItem("formAddValues"))
              ? JSON.parse(sessionStorage.getItem("formAddValues"))
                  .shippingState
              : "",

            shippingCity: JSON.parse(sessionStorage.getItem("formAddValues"))
              ? JSON.parse(sessionStorage.getItem("formAddValues")).shippingCity
              : "",

            shippingFirstname: JSON.parse(
              sessionStorage.getItem("formAddValues")
            )
              ? JSON.parse(sessionStorage.getItem("formAddValues"))
                  .shippingFirstname
              : "",
            shippingLastname: JSON.parse(
              sessionStorage.getItem("formAddValues")
            )
              ? JSON.parse(sessionStorage.getItem("formAddValues"))
                  .shippingLastname
              : "",
            shippingAddressline1: JSON.parse(
              sessionStorage.getItem("formAddValues")
            )
              ? JSON.parse(sessionStorage.getItem("formAddValues"))
                  .shippingAddressline1
              : "",
            shippingAddressLine2: JSON.parse(
              sessionStorage.getItem("formAddValues")
            )
              ? JSON.parse(sessionStorage.getItem("formAddValues"))
                  .shippingAddressLine2
              : "",
            shippingZip:
              JSON.parse(sessionStorage.getItem("formAddValues")) &&
              JSON.parse(sessionStorage.getItem("formAddValues")).shippingZip
                .length > 0
                ? JSON.parse(sessionStorage.getItem("formAddValues"))
                    .shippingZip
                : sessionStorage.getItem("Zipcode"),
            shippingCountry: JSON.parse(sessionStorage.getItem("formAddValues"))
              ? JSON.parse(sessionStorage.getItem("formAddValues"))
                  .shippingCountry
              : countryOptions[0].value,
            isPrimary: false,
            isViewShipping: false,
          },
      isAddress: null,
      submitDisabled: false,
      ZipfailMessage: sessionStorage.getItem("errorPaymentMsg"),
      addressVerifyData: [],
      selectedAddress: 0,
      addressModal: false,
      addrKey: 0,
    };
    this.nameFieldValidation.bind(this);
  }

  componentDidMount() {
    var response = this.props.zipcodeValidData;
    if (
      !!response &&
      !!response.data &&
      response.data.success === true &&
      response.data.zipSuccess === true
    ) {
      if (!UrlConstants.isTaxFree) {
        updatePrice(response);
        // sessionStorage.setItem('Zipcode', values.shippingZip);
        if (response.data.taxSuccess === true) {
          setTimeout(() => {
            this.props.getTaxtLoader(false, response);
          }, 100);
        } else {
          if (!this.state.ZipfailMessage) {
            setTimeout(() => {
              this.props.getTaxtLoader(false, response);
            }, 100);
            this.setState({
              submitDisabled: true,
              ZipfailMessage: response.data.taxErrorMessage,
            });
          } else {
            if (
              !response.data.addressSuccess &&
              !response.data.candidateFound
            ) {
              if (!!response.data.addressErrorMessage) {
                this.setState(
                  {
                    submitDisabled: true,
                    ZipfailMessage: response.data.addressErrorMessage,
                  },
                  () => {
                    document.getElementById("body").classList.add("has-toast");
                  }
                );
              }
            }
          }
        }
      } else {
        sessionStorage.setItem(
          "totalAmount",
          response.data.data.tax.totalAmount ||
            sessionStorage.getItem("total") ||
            0
        );
        sessionStorage.setItem(
          "totalDiscount",
          response.data.data.tax.totalDiscount || 0
        );
        if (!response.data.addressSuccess && !response.data.candidateFound) {
          if (!!response.data.addressErrorMessage) {
            this.setState(
              {
                submitDisabled: true,
                ZipfailMessage: response.data.addressErrorMessage,
              },
              () => {
                document.getElementById("body").classList.add("has-toast");
              }
            );
          }
        }
      }
    } else {
      if (!!response && !!response.data && !this.state.ZipfailMessage) {
        setTimeout(() => {
          this.props.getTaxtLoader(false, null);
        }, 100);
        this.setState({
          submitDisabled: true,
          ZipfailMessage: !!response.data ? response.data.zipErrorMessage : "",
        });
      }
    }
    // const formAddValues = !!sessionStorage.getItem('formAddValues')
    // 	? JSON.parse(sessionStorage.getItem('formAddValues'))
    // 	: null;

    if (sessionStorage.getItem("shippingInvalid") === "yes") {
      this.setState({ submitDisabled: true });
    }

    if (
      this.state.intialAddress &&
      this.state.intialAddress.shippingAddressline1.length > 0 &&
      this.state.intialAddress.shippingCountry.length > 0 &&
      this.state.intialAddress.shippingState.length > 0 &&
      this.state.intialAddress.shippingCity.length > 0 &&
      this.state.intialAddress.shippingZip.length === 5
    ) {
      this.checkTaxDetails(this.state.intialAddress, true, "shipping");
    }
  }
  componentWillUnmount() {
    document.getElementById("body").classList.remove("has-toast");
  }

  /**
   *
   * @description show Registration form
   * @memberof Registration
   *
   */
  editRegistration(e) {
    if (e === "shipping") {
      this.setState({
        editModeShipping: !this.state.editModeShipping,
      });
    } else {
      this.setState({
        editModeBilling: !this.state.editModeBilling,
      });
    }
  }

  zip = (e, setFieldValue) => {
    if (zipRegex.test(e.target.value) || e.target.value === "") {
      if (e.target.value.toString().length <= 5) {
        setFieldValue("shippingZip", e.target.value);
      }
    }
  };

  onContactChange = (e, setFieldValue) => {
    if (e.target.value.toString().length <= 10) {
      setFieldValue("contact", e.target.value);
    }
  };

  /**
   *
   * @description Validate the Zipcode by user input
   * @memberof Registration
   */
  handleZipFieldBlur = (e, handleBlur, values) => {
    handleBlur(e);
    this.checkTaxDetails(values, "", "shipping");
  };

  checkTaxDetails(values, isInput, isAddress) {
    let addressRequestObj = {
      ...values,
      shippingZipCode: values.shippingZip,
      shippingAddressLine1: values.shippingAddressline1,
      shippingAddressLine2: values.shippingAddressline2,
    };

    if (!!values.shippingZip && values.shippingZip.length === 5) {
      var objData = getOrderTaxPayload(addressRequestObj, isAddress);
      objData["isAddress"] = true;
      if (isAddress === "billing") {
        objData["isBillingAddress"] = true;
      }
      let url = UrlConstants.orderTax;
      // let defaultErrorMsg = UrlConstants.errorMsg;
      this.setState({ submitDisabled: true });
      //Validate Zipcode & Adress. Fetch Tax Amount
      APIUtil.postMethod(url, objData, true)
        .then((response) => {
          addressVerification(response);
          if (
            response.data.success === true &&
            response.data.zipSuccess === true
          ) {
            this.setState({ ZipfailMessage: response.data.zipErrorMessage });
            document.getElementById("body").classList.remove("has-toast");
            if (
              !response.data.addressSuccess &&
              !response.data.candidateFound
            ) {
              if (!!response.data.addressErrorMessage) {
                response.data.zipcode = values.shippingZip;
                response.data.totalTaxAmount = 0;
                this.props.getTaxtLoader(false, response);
                this.setState(
                  {
                    submitDisabled: true,
                    ZipfailMessage: response.data.addressErrorMessage,
                  },
                  () => {
                    document.getElementById("body").classList.add("has-toast");
                  }
                );
                if (isAddress === "shipping") {
                  this.billingFieldDisable(values);
                  sessionStorage.setItem("shippingInvalid", "yes");
                }
                return false;
              }
            } else {
              if (isAddress === "shipping") {
                sessionStorage.setItem("shippingInvalid", "no");

                this.setState({ submitDisabled: false });
              }
              if (!!response.data.candidateFound) {
                response.data.zipcode = values.shippingZip;
                response.data.totalTaxAmount = 0;
                this.props.getTaxtLoader(false, response);
                var addrArray =
                  !!response.data.data &&
                  !!response.data.data.address &&
                  !!response.data.data.address.data &&
                  !!response.data.data.address.data.XAVResponse
                    ? response.data.data.address.data.XAVResponse.Candidate
                    : [];
                var finalAdata = addrArray;
                if (!Array.isArray(addrArray)) {
                  finalAdata = [addrArray];
                }
                this.setState({ addressVerifyData: finalAdata }, () =>
                  this.setState({
                    isAddress: isAddress ? isAddress : null,
                    addressModal: true,
                  })
                );
                return false;
              }
            }
            if (!response.data.taxFree) {
              if (response.data.taxSuccess === true) {
                updatePrice(response);
                response.data.zipcode = values.shippingZip;
                response.data.totalTaxAmount = response.data.data.tax
                  ? response.data.data.tax.totalTaxAmount
                  : "";
                setTimeout(() => {
                  this.props.getTaxtLoader(false, response);
                }, 100);
              }
            } else {
              sessionStorage.setItem(
                "totalAmount",
                response.data.data.tax.totalAmount ||
                  sessionStorage.getItem("total") ||
                  0
              );
              sessionStorage.setItem(
                "totalDiscount",
                response.data.data.tax.totalDiscount || 0
              );
              this.setState({
                ZipfailMessage: response.data.taxErrorMessage,
              });
              if (response.data.taxErrorMessage) {
                this.setState({
                  submitDisabled: true,
                });
              }
              response.data.zipcode = values.shippingZipCode;
              response.data.totalTaxAmount = 0;
              this.props.getTaxtLoader(false, response);
              setTimeout(() => {
                this.setState({ ZipfailMessage: null });
              }, 3000);
            }
          } else {
            response.data.zipcode = values.shippingZip;
            response.data.totalTaxAmount = 0;
            setTimeout(() => {
              this.props.getTaxtLoader(false, response);
            }, 100);
            this.setState(
              {
                submitDisabled: true,
                ZipfailMessage: response.data.zipErrorMessage,
              },
              () => {
                document.getElementById("body").classList.add("has-toast");
              }
            );
          }
        })
        .catch((response) => {
          if (response.data) {
            response.data.zipcode = values.shippingZip;
            response.data.totalTaxAmount = 0;
          }
          this.setState(
            {
              ZipfailMessage:
                "Please check that your city, state and zip code match for your shipping address.",
              submitDisabled: true,
            },
            () => {
              document.getElementById("body").classList.add("has-toast");
            }
          );
          this.props.getTaxtLoader(false, response);
        });
    } else {
      this.setState({ submitDisabled: true });
    }
  }

  getAdressLine(e, index) {
    var city = "";
    var state = "";
    var country = "";
    var zipcode = "";
    var addressLine = !!e.AddressLine ? e.AddressLine : "";
    if (Array.isArray(e.AddressLine)) {
      addressLine =
        (!!e.AddressLine[0] ? e.AddressLine[0] : "") +
        (!!e.AddressLine[1] ? ", " + e.AddressLine[1] : "");
    }
    if (!!e.CountryCode) {
      country = e.CountryCode;
    }
    if (!!e.PoliticalDivision1) {
      state = e.PoliticalDivision1;
    }
    if (!!e.PoliticalDivision2) {
      city = e.PoliticalDivision2;
    }
    if (!!e.PostcodePrimaryLow) {
      zipcode = e.PostcodePrimaryLow;
    }
    const addressLabel =
      (!!addressLine ? addressLine : "") +
      (!!city ? " " + city : "") +
      (!!state ? " " + state : "") +
      (!!country ? ", " + country : "") +
      (!!zipcode ? " " + zipcode : "");
    return (
      <FormControlLabel
        onChange={(e) => this.setState({ selectedAddress: index })}
        value={index}
        key={`${"address1" + index}`}
        control={<Radio />}
        label={addressLabel}
      />
    );
  }

  manageSuggestedAddress() {
    const addressData = !!this.state.addressVerifyData[
      this.state.selectedAddress
    ]
      ? this.state.addressVerifyData[this.state.selectedAddress]
          .AddressKeyFormat
      : "";
    var intialAddress = !!sessionStorage.getItem("formAddValues")
      ? JSON.parse(sessionStorage.getItem("formAddValues"))
      : {};
    if (this.state.isAddress !== "billing") {
      if (Array.isArray(addressData.AddressLine)) {
        intialAddress.shippingAddressline1 = addressData.AddressLine[0];
        intialAddress.shippingAddressLine2 =
          addressData.AddressLine.length > 1 ? addressData.AddressLine[1] : "";
      } else {
        intialAddress.shippingAddressline1 = addressData.AddressLine;
        intialAddress.shippingAddressLine2 = "";
      }
      intialAddress.shippingCountry = !!addressData.CountryCode
        ? addressData.CountryCode
        : "";
      intialAddress.shippingState = !!addressData.PoliticalDivision1
        ? addressData.PoliticalDivision1
        : "";
      intialAddress.shippingCity = !!addressData.PoliticalDivision2
        ? addressData.PoliticalDivision2
        : "";
      intialAddress.shippingZip = !!addressData.PostcodePrimaryLow
        ? addressData.PostcodePrimaryLow
        : "";
    }
    if (
      this.state.billingSameAsShipping ||
      this.state.isAddress === "billing"
    ) {
      if (Array.isArray(addressData.AddressLine)) {
        intialAddress.billingAddressLine1 = addressData.AddressLine[0];
        intialAddress.billingAddressLine2 =
          addressData.AddressLine.length > 1 ? addressData.AddressLine[1] : "";
      } else {
        intialAddress.billingAddressLine1 = addressData.AddressLine;
        intialAddress.billingAddressLine2 = "";
      }
      intialAddress.billingCountry = !!addressData.CountryCode
        ? addressData.CountryCode
        : "";
      intialAddress.billingState = !!addressData.PoliticalDivision1
        ? addressData.PoliticalDivision1
        : "";
      intialAddress.billingCity = !!addressData.PoliticalDivision2
        ? addressData.PoliticalDivision2
        : "";
      intialAddress.billingZipCode = !!addressData.PostcodePrimaryLow
        ? addressData.PostcodePrimaryLow
        : "";
    }
    this.setState(
      {
        intialAddress,
        selectedAddress: 0,
        addrKey: this.state.addrKey + 1,
        addressModal: false,
      },
      () =>
        this.checkTaxDetails(
          this.state.intialAddress,
          true,
          this.state.isAddress
        )
    );
  }

  handleClose = () => {
    this.setState({ ZipfailMessage: "" });
    document.getElementById("body").classList.remove("has-toast");
  };

  nameFieldValidation = (e, setFieldValue) => {
    this.setState({ [e.target.id]: "" });
    var re = /^[a-zA-Z '.]*$/;
    if (!re.test(e.target.value)) {
      if (
        e.target.id === "firstname" ||
        e.target.id === "shippingFirstname" ||
        e.target.id === "billingFirstname"
      ) {
        const eventValue = e.target.id;
        this.setState(
          { [e.target.id]: "First Name must be only alphabets." },
          () => {
            setTimeout(() => {
              this.setState({ [eventValue]: null }, () => {});
            }, 3000);
          }
        );
      } else {
        const eventValue = e.target.id;
        this.setState(
          { [e.target.id]: "Last Name must be only alphabets." },
          () => {
            setTimeout(() => {
              this.setState({ [eventValue]: null }, () => {});
            }, 3000);
          }
        );
      }
      if (e.target.value === "") {
        setFieldValue([e.target.id], e.target.value);
        this.setState({ [e.target.id]: "" });
      }
    } else if (e.target.value || e.target.value === "") {
      if (this.state.billingSameAsShipping) {
        if (e.target.id === "shippingFirstname") {
          setFieldValue("billingFirstname", e.target.value);
        }
        if (e.target.id === "shippingLastname") {
          setFieldValue("billingLastname", e.target.value);
        }
      }
      setFieldValue([e.target.id], e.target.value);
    }
  };

  addressFieldValidation = (e, values, setFieldValue, handleBlur) => {
    this.setState({ [e.target.id]: "" });
    var re = /^[a-zA-Z0-9 '.#,/'-]*$/;
    if (!re.test(e.target.value)) {
      if (
        e.target.id === "shippingAddressline1" ||
        e.target.id === "billingAddressLine1"
      ) {
        const eventValue = e.target.id;
        this.setState(
          { [e.target.id]: "Address Line 1 must be only alphabets." },
          () => {
            setTimeout(() => {
              this.setState({ [eventValue]: null });
            }, 3000);
          }
        );
      } else if (
        e.target.id === "shippingAddressLine2" ||
        e.target.id === "billingAddressLine2"
      ) {
        const eventValue = e.target.id;
        this.setState(
          { [e.target.id]: "Address Line 2 must be only alphabets." },
          () => {
            setTimeout(() => {
              this.setState({ [eventValue]: null });
            }, 3000);
          }
        );
      } else if (
        e.target.id === "shippingCity" ||
        e.target.id === "billingCity"
      ) {
        const eventValue = e.target.id;
        this.setState({ [e.target.id]: "City must be only alphabets." }, () => {
          setTimeout(() => {
            this.setState({ [eventValue]: null });
          }, 3000);
        });
      }
      if (e.target.value === "") {
        setFieldValue([e.target.id], e.target.value);
        this.setState({ [e.target.id]: "" });
      }
    } else if (e.target.value || e.target.value === "") {
      if (this.state.billingSameAsShipping) {
        if (e.target.id === "shippingAddressline1") {
          setFieldValue("billingAddressLine1", e.target.value);
        }
        if (e.target.id === "shippingAddressLine2") {
          setFieldValue("billingAddressLine2", e.target.value);
        }
        if (e.target.id === "shippingCity") {
          setFieldValue("billingCity", e.target.value);
        }
      }
      if (e.target.id === "shippingAddressline1") {
        values.shippingAddressline1 = e.target.value;
      }
      if (e.target.id === "shippingCity") {
        values.shippingCity = e.value;
        this.checkTaxDetails(e, handleBlur, values, "shipping");
      }
      setFieldValue([e.target.id], e.target.value);
    }
  };

  validateEmail = (email) => {
    const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  };

  render() {
    var addressPattern = /\bP(ost|ostal)?([ \.]*(O|0)(ffice)?)?([ \.]*Box)\b/i;
    return (
      <div>
        {/* {this.props.spinner ? <Spinner /> : ''} */}
        <Formik
          key={this.state.addrKey}
          initialValues={this.state.intialAddress}
          enableReinitialize={true}
          validationSchema={Yup.object().shape({
            shippingAddressline1: Yup.string()
              .trim()
              .required("Address Line 1 is Required.")
              .test(
                "shippingAddressline1",
                UrlConstants.poBoxErrorMsg.replace(/##ADDTYPE##/g, function () {
                  return "shipping";
                }),
                function (value) {
                  if (addressPattern.test(value)) {
                    return false;
                  }
                  return true;
                }
              ),
            shippingAddressLine2: Yup.string()
              .nullable()
              .notRequired()
              .test(
                "shippingAddressLine2",
                UrlConstants.poBoxErrorMsg.replace(/##ADDTYPE##/g, function () {
                  return "shipping";
                }),
                function (value) {
                  if (addressPattern.test(value)) {
                    return false;
                  }
                  return true;
                }
              ),

            shippingFirstname: Yup.string()
              .trim()
              .required("First Name is required.")
              .min(2, "Name must be at least 2 characters."),

            shippingLastname: Yup.string()
              .trim()
              .required("Last Name is required.")
              .min(2, "Name must be at least 2 characters."),

            shippingZip: Yup.string()
              .trim()
              .matches(/^[0-9]{5}$/, "Zip code must be exactly 5 digits.")
              .required("Zip is required"),

            shippingCountry: Yup.string()
              .trim()
              .required("Country is required."),
            shippingState: Yup.string().trim().required("State is required."),
            shippingCity: Yup.string().trim().required("City is required."),
          })}
          onSubmit={(fields) => {
            sessionStorage.setItem("formAddValues", JSON.stringify(fields));

            this.props.saveAddAddress(fields);
          }}
        >
          {(props) => {
            const {
              values,
              touched,
              errors,
              handleChange,
              handleBlur,
              handleSubmit,
              setFieldValue,
              setFieldTouched,
            } = props;

            return (
              <form
                id="startAddNewAddress"
                name="form1"
                onSubmit={handleSubmit}
                onChange={sessionStorage.setItem(
                  "formAddValues",
                  JSON.stringify(values)
                )}
              >
                <div className="checkout-content-body-inner full-view inner-view">
                  {/* zipcode valication */}
                  {this.state.ZipfailMessage ? (
                    <Snackbar
                      open={true}
                      anchorOrigin={{ vertical: "top", horizontal: "center" }}
                      className="snackbar snackbar-full"
                    >
                      <MuiAlert
                        variant="filled"
                        onClose={this.handleClose}
                        severity="error"
                      >
                        {this.state.ZipfailMessage}
                      </MuiAlert>
                    </Snackbar>
                  ) : (
                    ""
                  )}

                  {/**  Shipping information */}
                  <fieldset className="form-card">
                    <Grid
                      container
                      spacing={0}
                      justify="space-between"
                      alignItems="center"
                      className="mb-15"
                    >
                      <Grid item>
                        <span className="shipping-note">
                          At this time, we are unable to ship to P.O. boxes.
                        </span>
                      </Grid>
                    </Grid>

                    <Grid container spacing={2} className="form-card-container">
                      {/** First Name */}
                      <Grid item xs={12} sm={6}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel
                            className="form-label"
                            htmlFor="shippingFirstname"
                          >
                            First Name
                            <span className="text-danger">*</span>
                          </InputLabel>
                          <OutlinedInput
                            id="shippingFirstname"
                            name="shippingFirstname"
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                            onChange={(e) =>
                              this.nameFieldValidation(e, setFieldValue)
                            }
                            onBlur={handleBlur}
                            error={
                              touched.shippingFirstname &&
                              Boolean(errors.shippingFirstname)
                            }
                            value={values.shippingFirstname}
                          />
                          {this.state.shippingFirstname ? (
                            <div className="text-danger" role="alert">
                              {this.state.shippingFirstname}
                            </div>
                          ) : (
                            <ErrorMessage
                              role="alert"
                              component="div"
                              name="shippingFirstname"
                              className="text-danger"
                            />
                          )}
                        </FormControl>
                      </Grid>
                      {/** Last Name  */}
                      <Grid item xs={12} sm={6}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel
                            className="form-label"
                            htmlFor="shippingLastname"
                          >
                            Last Name
                            <span className="text-danger">*</span>
                          </InputLabel>
                          <OutlinedInput
                            id="shippingLastname"
                            name="shippingLastname"
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                            onChange={(e) =>
                              this.nameFieldValidation(e, setFieldValue)
                            }
                            onBlur={handleBlur}
                            error={
                              touched.shippingLastname &&
                              Boolean(errors.shippingLastname)
                            }
                            value={values.shippingLastname}
                          />
                          {this.state.shippingLastname ? (
                            <div className="text-danger" role="alert">
                              {this.state.shippingLastname}
                            </div>
                          ) : (
                            <ErrorMessage
                              component="div"
                              role="alert"
                              name="shippingLastname"
                              className="text-danger"
                            />
                          )}
                        </FormControl>
                      </Grid>

                      {/** Address Line 1 */}
                      <Grid item xs={12} sm={12}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel
                            className="form-label"
                            htmlFor="shippingAddressline1"
                          >
                            Address Line 1<span className="text-danger">*</span>
                          </InputLabel>

                          <OutlinedInput
                            id="shippingAddressline1"
                            name="shippingAddressline1"
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                            value={values.shippingAddressline1}
                            onChange={(e) =>
                              this.addressFieldValidation(
                                e,
                                values,
                                setFieldValue
                              )
                            }
                            onBlur={(e) => {
                              setFieldTouched("shippingAddressline1", true);
                              if (
                                !addressPattern.test(
                                  values.shippingAddressline1
                                ) &&
                                !addressPattern.test(
                                  values.shippingAddressLine2
                                )
                              ) {
                                if (
                                  !!values.shippingAddressline1 &&
                                  !!values.shippingCountry &&
                                  !!values.shippingState &&
                                  !!values.shippingCity &&
                                  !!values.shippingZip
                                ) {
                                  this.checkTaxDetails(
                                    values,
                                    true,
                                    "shipping"
                                  );
                                }
                              }
                            }}
                            error={
                              touched.shippingAddressline1 &&
                              Boolean(errors.shippingAddressline1)
                            }
                          />

                          {this.state.shippingAddressline1 ? (
                            <div className="text-danger" role="alert">
                              {this.state.shippingAddressline1}
                            </div>
                          ) : (
                            <ErrorMessage
                              component="div"
                              role="alert"
                              name="shippingAddressline1"
                              className="text-danger"
                            />
                          )}
                        </FormControl>
                      </Grid>
                      {/** Address Line 2 */}
                      <Grid item xs={12} sm={12}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel
                            className="form-label"
                            htmlFor="shippingAddressLine2"
                          >
                            Address Line 2
                          </InputLabel>

                          <OutlinedInput
                            id="shippingAddressLine2"
                            name="shippingAddressLine2"
                            value={values.shippingAddressLine2}
                            onChange={(e) =>
                              this.addressFieldValidation(
                                e,
                                values,
                                setFieldValue
                              )
                            }
                            onBlur={() => {
                              setFieldTouched("shippingAddressLine2", true);
                              if (
                                !addressPattern.test(
                                  values.shippingAddressline1
                                ) &&
                                !addressPattern.test(
                                  values.shippingAddressLine2
                                )
                              ) {
                                if (
                                  !!values.shippingAddressline1 &&
                                  !!values.shippingCountry &&
                                  !!values.shippingState &&
                                  !!values.shippingCity &&
                                  !!values.shippingZip
                                ) {
                                  this.checkTaxDetails(
                                    values,
                                    true,
                                    "shipping"
                                  );
                                }
                              }
                            }}
                          />
                          {this.state.shippingAddressLine2 ? (
                            <div className="text-danger" role="alert">
                              {this.state.shippingAddressLine2}
                            </div>
                          ) : (
                            <ErrorMessage
                              component="div"
                              role="alert"
                              name="shippingAddressLine2"
                              className="text-danger"
                            />
                          )}
                        </FormControl>
                      </Grid>
                      {/* shippingCountry Drop */}
                      <Grid item xs={12} sm={6}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <label htmlFor="shippingCountry">
                            <AutoCompleteSelect
                              id="shippingCountry"
                              label="Country"
                              touched={touched.shippingCountry}
                              options={countryOptions}
                              defaultValue={countryOptions.find(
                                (option) =>
                                  option.value === values.shippingCountry
                              )}
                              value={countryOptions.find(
                                (option) =>
                                  option.value === values.shippingCountry
                              )}
                              onChange={(e, value) => {
                                if (value !== null) {
                                  setFieldValue("shippingCountry", value.value);
                                  if (this.state.billingSameAsShipping) {
                                    setFieldValue(
                                      "billingCountry",
                                      value.value
                                    );
                                  }
                                  values.shippingCountry = value.value;
                                }
                              }}
                              onBlur={(e, value) => {
                                setFieldTouched("shippingCountry", true);
                                if (
                                  !addressPattern.test(
                                    values.shippingAddressline1
                                  ) &&
                                  !addressPattern.test(
                                    values.shippingAddressLine2
                                  )
                                ) {
                                  if (
                                    !!values.shippingAddressline1 &&
                                    !!values.shippingCountry &&
                                    !!values.shippingState &&
                                    !!values.shippingCity &&
                                    !!values.shippingZip
                                  ) {
                                    this.checkTaxDetails(
                                      values,
                                      true,
                                      "shipping"
                                    );
                                  }
                                }
                              }}
                              error={errors.shippingCountry}
                            />
                          </label>
                          <ErrorMessage
                            component="div"
                            name="shippingCountry"
                            role="alert"
                            className="text-danger"
                          />
                        </FormControl>
                      </Grid>
                      {/** State */}
                      <Grid item xs={12} sm={6}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <label htmlFor="shippingState">
                            <AutoCompleteSelect
                              id="shippingState"
                              label="State"
                              touched={touched.shippingState}
                              options={usStateData}
                              defaultValue={usStateData.find(
                                (option) =>
                                  option.value === values.shippingState
                              )}
                              value={usStateData.find(
                                (option) =>
                                  option.value === values.shippingState
                              )}
                              onChange={(e, value) => {
                                if (value !== null) {
                                  setFieldValue("shippingState", value.value);
                                  if (this.state.billingSameAsShipping) {
                                    setFieldValue("billingState", value.value);
                                  }
                                  values.shippingState = value.value;
                                }
                              }}
                              onBlur={(e, value) => {
                                setFieldTouched("shippingState", true);
                                if (
                                  !addressPattern.test(
                                    values.shippingAddressline1
                                  ) &&
                                  !addressPattern.test(
                                    values.shippingAddressLine2
                                  )
                                ) {
                                  if (
                                    !!values.shippingAddressline1 &&
                                    !!values.shippingCountry &&
                                    !!values.shippingState &&
                                    !!values.shippingCity &&
                                    !!values.shippingZip
                                  ) {
                                    this.checkTaxDetails(
                                      values,
                                      true,
                                      "shipping"
                                    );
                                  }
                                }
                              }}
                              error={errors.shippingState}
                            />
                          </label>

                          <ErrorMessage
                            component="div"
                            role="alert"
                            name="shippingState"
                            className="text-danger"
                          />
                        </FormControl>
                      </Grid>
                      {/** City */}
                      <Grid item xs={12} sm={6}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel
                            className="form-label"
                            htmlFor="shippingCity"
                          >
                            City <span className="text-danger">*</span>
                          </InputLabel>
                          <OutlinedInput
                            id="shippingCity"
                            name="shippingCity"
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                            onChange={(e) =>
                              this.addressFieldValidation(
                                e,
                                values,
                                setFieldValue,
                                handleBlur
                              )
                            }
                            onBlur={(e) => {
                              setFieldTouched("shippingCity", true);
                              if (
                                !addressPattern.test(
                                  values.shippingAddressline1
                                ) &&
                                !addressPattern.test(
                                  values.shippingAddressLine2
                                )
                              ) {
                                if (
                                  !!values.shippingAddressline1 &&
                                  !!values.shippingCountry &&
                                  !!values.shippingState &&
                                  !!values.shippingCity &&
                                  !!values.shippingZip
                                ) {
                                  this.checkTaxDetails(
                                    values,
                                    true,
                                    "shipping"
                                  );
                                }
                              }
                            }}
                            error={
                              touched.shippingCity &&
                              Boolean(errors.shippingCity)
                            }
                            value={values.shippingCity}
                          />
                          {this.state.shippingCity ? (
                            <div className="text-danger" role="alert">
                              {this.state.shippingCity}
                            </div>
                          ) : (
                            <ErrorMessage
                              component="div"
                              role="alert"
                              name="shippingCity"
                              className="text-danger"
                            />
                          )}
                        </FormControl>
                      </Grid>
                      {/** ZipCode */}
                      <Grid item xs={12} sm={6}>
                        <FormControl
                          fullWidth
                          className="form-group"
                          variant="outlined"
                        >
                          <InputLabel
                            className="form-label"
                            htmlFor="shippingZip"
                          >
                            Zip <span className="text-danger">*</span>
                          </InputLabel>
                          <OutlinedInput
                            id="shippingZip"
                            name="shippingZip"
                            inputProps={{
                              "aria-required": "true",
                              role: "alert",
                              "aria-invalid": "false",
                            }}
                            ref={(input) => {
                              this.nameInput = input;
                            }}
                            autoComplete="new-password"
                            onChange={(e) => this.zip(e, setFieldValue)}
                            onBlur={(e) => {
                              setFieldTouched("shippingZip", true);
                              if (
                                !addressPattern.test(
                                  values.shippingAddressline1
                                ) &&
                                !addressPattern.test(
                                  values.shippingAddressLine2
                                )
                              ) {
                                this.handleZipFieldBlur(
                                  e,
                                  handleBlur,
                                  values,
                                  false
                                );
                              }
                            }}
                            error={
                              touched.shippingZip && Boolean(errors.shippingZip)
                            }
                            value={values.shippingZip}
                          />
                          <ErrorMessage
                            component="div"
                            role="alert"
                            name="shippingZip"
                            className="text-danger"
                          />
                        </FormControl>
                      </Grid>
                    </Grid>
                  </fieldset>

                  <div className="button-group button-group-inner text-right">
                    <Button
                      className={"btn btn-secondary btn-sm"}
                      id="cancelAddNewAddress"
                      aria-label="cancel Add New Address"
                      onClick={() => {
                        this.props.cancelAddAddress();
                      }}
                    >
                      Cancel
                    </Button>
                    <Button
                      type="submit"
                      disabled={
                        this.state.submitDisabled ||
                        values.shippingFirstname.trim().length < 2 ||
                        values.shippingLastname.trim().length < 2
                      }
                      className={"btn btn-primary btn-sm"}
                      id="saveAddNewAddress"
                      aria-label="Save New Address"
                    >
                      Save
                    </Button>
                  </div>
                </div>
              </form>
            );
          }}
        </Formik>
        {/* relevant address modal */}
        <Dialog
          open={this.state.addressModal}
          onClose={() => this.setState({ addressModal: false })}
          aria-labelledby="address-modal"
          disableBackdropClick
          disableEscapeKeyDown
          maxWidth={false}
          classes={{
            paper: "modal-address",
            root: "modal-stack-top",
          }}
        >
          {/* Dialog Content */}
          <DialogTitle id="address-modal" className="modal-address-title">
            Relevant Address
          </DialogTitle>
          <DialogContent dividers className="modal-address-content">
            <RadioGroup
              aria-label="add new address"
              name="addNewAddress"
              value={this.state.selectedAddress}
            >
              {!!this.state.addressVerifyData &&
              this.state.addressVerifyData.length > 0 ? (
                this.state.addressVerifyData.map((e, index) => {
                  return this.getAdressLine(e.AddressKeyFormat, index);
                })
              ) : (
                <h3>Address is not available!!</h3>
              )}
            </RadioGroup>
          </DialogContent>
          <DialogActions className="modal-address-action">
            <Button
              onClick={() =>
                this.setState({
                  sDisabledBillingfield: false,
                  selectedAddress: 0,
                  addressModal: false,
                  submitDisabled: true,
                })
              }
              variant="contained"
              color="primary"
              className={" btn-secondary"}
              aria-label="Cancel"
            >
              Cancel
            </Button>
            {!!this.state.addressVerifyData &&
            this.state.addressVerifyData.length > 0 ? (
              <Button
                onClick={() => this.manageSuggestedAddress()}
                color="primary"
                className={" btn-primary"}
                id="applyAddress"
                aria-label="Apply Address"
              >
                {" "}
                Apply{" "}
              </Button>
            ) : (
              ""
            )}
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    zipResponse: state.reducer.zipResponse,
  };
};

export default connect(mapStateToProps, { actionZipcodeVerification })(
  withRouter(AddNewAddress)
);
