import React, { Component } from "react";
import CustomEvent from "custom-event";
import { Button } from "@material-ui/core";
import UrlConstants from "../../../config/UrlConstants";
import { trackEvent, EventNames } from "../../../appinsights/EventTrack";
import DialogBox from "./../dialogBox/DialogBox";
import {
  DisplayHeading,
  RegexUtil,
} from "../../../config/constants/content.constant";
import { AddRemoveProductToCart } from "../../../config/amplitude/SnacksAmplitude";
import "./quantityCount.scss";

export default class QuantityCount extends Component {
  state = {
    quantity: 0,
    warningMsg: "",
    disableIncrement: false,
    deleteClicked: false,
    isDeleted: 0,
    showDeletPrompt: false,
    packCount: 0,
  };

  componentDidMount() {
    this.updateExistingQuantity();
    this.multipackCount();
    let that = this;
    document.addEventListener("multipackCartItems", function (e) {
      that.multipackCount();
    });
  }

  UNSAFE_componentWillReceiveProps() {
    this.updateExistingQuantity();
  }

  multipackCount = () => {
    const multipackCartItem = JSON.parse(
      sessionStorage.getItem("multipackCartItems") || "[]"
    );
    let packCount = 0;
    multipackCartItem &&
      multipackCartItem.map((item) => {
        packCount = packCount + parseInt(item.quantity);
        return packCount;
      });
    this.setState({ packCount });
  };

  updateExistingQuantity = () => {
    const { multipack } = this.props;
    const cartItems = multipack
      ? sessionStorage.getItem("multipackCartItems")
      : sessionStorage.getItem("cartItems");
    let cartArray = typeof cartItems === "string" ? JSON.parse(cartItems) : [];

    const indexExist = cartArray.findIndex(
      (item) => item.productId === this.props.productInfo.productId
    );
    if (indexExist === -1) this.setState({ quantity: 0 });
    else this.setState({ quantity: cartArray[indexExist].quantity });
  };

  /*
    Update the Object
  */
  updateObject = (id, cartItems) => {
    const { quantity } = this.state;
    const { passQtyToCart, selectedItems, multipack } = this.props;
    const objIndex = cartItems.findIndex((obj) => obj.productId === id);

    const updatedObj = {
      ...cartItems[objIndex],
      quantity: quantity,
    };

    const updatedCartItems = [
      ...cartItems.slice(0, objIndex),
      updatedObj,
      ...cartItems.slice(objIndex + 1),
    ];

    const filteredCartItems = updatedCartItems.filter(
      (item) => item.quantity !== 0
    );

    selectedItems && selectedItems(filteredCartItems);
    if (!multipack) {
      sessionStorage.setItem("cartItems", JSON.stringify(filteredCartItems));
      passQtyToCart && passQtyToCart(filteredCartItems);
      document.dispatchEvent(new CustomEvent("cartitem"));
    }
    if (multipack) {
      sessionStorage.setItem(
        "multipackCartItems",
        JSON.stringify(filteredCartItems)
      );
      document.dispatchEvent(new CustomEvent("multipackCartItems"));
    }
  };

  /*
    Add or Update the product in the cartItems.
  */
  updateQuantity = (qty) => {
    const {
      productInfo,
      warningMsgFunc,
      primaryProduct,
      multipack,
    } = this.props;
    const cartItems = multipack
      ? sessionStorage.getItem("multipackCartItems")
      : sessionStorage.getItem("cartItems");
    let cartArray = typeof cartItems === "string" ? JSON.parse(cartItems) : [];

    const indexExist = cartArray.findIndex(
      (item) => item.productId === productInfo.productId
    );

    if (indexExist === -1)
      cartArray.push({ ...primaryProduct, ...productInfo });
    const trimmedQty = qty && qty.trim();
    if (trimmedQty && trimmedQty > productInfo.availableQty) {
      this.setState({ warningMsg: UrlConstants.qtyAvailableMsg }, () => {
        warningMsgFunc(this.state.warningMsg, productInfo.availableQty);
      });
    }
    if (trimmedQty && trimmedQty <= productInfo.availableQty) {
      this.setState({ quantity: parseInt(qty) }, () => {
        this.updateObject(productInfo.productId, cartArray);
      });
    } else {
      this.updateObject(productInfo.productId, cartArray);
    }
  };

  deleteCart = (productId, cartItems) => {
    let deletedProduct = [];
    const {
      passQtyToCart,
      selectedItems,
      PageTitle,
      multipack,
      ProductType,
    } = this.props;
    deletedProduct = cartItems.filter((item) => item.productId === productId);
    cartItems = cartItems.filter((item) => item.productId !== productId);
    selectedItems && selectedItems(cartItems);
    this.setState({
      showDeletPrompt: false,
      deleteClicked: false,
      quantity: 0,
    });
    if (!multipack) {
      sessionStorage.setItem("cartItems", JSON.stringify(cartItems));
      passQtyToCart && passQtyToCart(cartItems);
      document.dispatchEvent(new CustomEvent("cartitem"));
    }
    if (multipack) {
      sessionStorage.setItem("multipackCartItems", JSON.stringify(cartItems));
      document.dispatchEvent(new CustomEvent("multipackCartItems"));
    }
    AddRemoveProductToCart({
      Method: "Delete",
      ProductType,
      PageTitle,
      ...deletedProduct[0],
    });
  };

  /*
    Decrease quantity of a product
  */
  decrement = () => {
    const { quantity } = this.state;
    const { primaryProduct, PageTitle, productInfo, ProductType } = this.props;

    if (quantity > 0) {
      let productInformation = { ...primaryProduct, ...productInfo };
      //&& quantity <= this.props.productInfo.availableQty
      trackEvent(
        EventNames.Action.ACTION_PRODUCT_DECREASE_CART,
        EventNames.Event.EVENT_BUTTON_PRESS,
        window.location.origin,
        window.location.pathname,
        { product: productInformation }
      );

      this.setState(
        { disableIncrement: false, quantity: parseInt(quantity) - 1 },
        () => {
          this.updateQuantity();
          AddRemoveProductToCart({
            Method: "Decrement",
            ProductType,
            PageTitle,
            ...productInformation,
            quantity: this.state.quantity,
          });
        }
      );
    }
  };

  /*
    Increase quantity of a product
  */
  increment = () => {
    const { quantity } = this.state;
    const {
      productInfo,
      warningMsgFunc,
      primaryProduct,
      PageTitle,
      ProductType,
    } = this.props;

    if (quantity >= 0 && quantity < productInfo.availableQty) {
      let productInformation = { ...primaryProduct, ...productInfo };

      trackEvent(
        EventNames.Action.ACTION_PRODUCT_INCREASE_CART,
        EventNames.Event.EVENT_BUTTON_PRESS,
        window.location.origin,
        window.location.pathname,
        { product: productInformation }
      );

      this.setState({ quantity: parseInt(quantity) + 1 }, () => {
        this.updateQuantity();
        AddRemoveProductToCart({
          Method: "Increment",
          ProductType,
          PageTitle,
          ...productInformation,
          quantity: this.state.quantity,
        });
      });
    }
    if (productInfo.availableQty <= quantity) {
      this.setState(
        { warningMsg: UrlConstants.qtyAvailableMsg, disableIncrement: true },
        () => {
          trackEvent(
            EventNames.Action.ACTION_PRODUCT_MAX_WARN,
            EventNames.Event.EVENT_BUTTON_PRESS,
            window.location.origin,
            window.location.pathname,
            { product: primaryProduct }
          );

          warningMsgFunc(this.state.warningMsg, productInfo.availableQty);
        }
      );
    }
  };

  showTrashBtn = () => {
    const { quantity } = this.state;
    const { sizeName } = this.props;
    return (
      <Button
        aria-label={
          sizeName
            ? `on clicking on this button ${sizeName} Product item will be deleted`
            : "on clicking on this button Product item will be deleted"
        }
        onClick={() => {
          this.setState({
            showDeletPrompt: true,
            deleteClicked: true,
          });
        }}
        disabled={quantity === 0}
        className="trash-icon-btn"
      >
        <i className="fas fa-trash-alt"></i>
      </Button>
    );
  };

  showInputBox = () => {
    const { multipack, productInfo, mainHeading } = this.props;
    const { quantity } = this.state;
    let inputBoxId = !mainHeading
      ? `productListInput-${productInfo.productId}`
      : `productListInput-${productInfo.productId}-${mainHeading.replace(
          /\s+/g,
          "-"
        )}`;
    return (
      <>
        <label htmlFor={inputBoxId} className="label-hidden">
          Product Quantity
        </label>
        <input
          type="text"
          id={inputBoxId}
          value={RegexUtil.ZIP_CODE_OR_NUM.test(quantity) ? quantity : 0}
          onChange={(e) => this.updateQuantity(e.target.value)}
          ref="quantityBox"
          disabled={multipack}
        />
      </>
    );
  };

  render() {
    const { quantity, disableIncrement, packCount } = this.state;
    const {
      productInfo,
      sizeName,
      price,
      availableQty,
      currency,
      multipack,
      multipackLimit,
    } = this.props;
    let cartItems = multipack
      ? JSON.parse(sessionStorage.getItem("multipackCartItems") || "[]")
      : JSON.parse(sessionStorage.getItem("cartItems") || "[]");
    const deleteMsg = this.state.deleteClicked
      ? " Are you sure you would like to remove this item from the cart?"
      : "Are you sure you would like to remove all items from the cart?";

    if (availableQty <= 0) {
      return "";
    }
    return (
      <div className="quantity-count-contain">
        {sizeName && (
          <div className="size-wrapper">
            <div aria-label={sizeName} className="size">
              {sizeName}
            </div>
            {price && (
              <div className="price">
                <sup>{currency}</sup>
                {price}
              </div>
            )}
          </div>
        )}
        <div className="quantity-count-wrapper">
          {availableQty <= 0 && (
            <div className="out-of-stock">{DisplayHeading.OUT_OF_STOCK}</div>
          )}

          {availableQty > 0 && quantity > 1 && (
            <Button
              aria-label={
                sizeName
                  ? `on clicking on this button ${sizeName} Product Quantity will be decremented by 1`
                  : "on clicking on this button Product Quantity will be decremented by 1"
              }
              onClick={this.decrement}
              disabled={quantity === 0}
            >
              <i className="fas fa-minus"></i>
            </Button>
          )}

          {/* Trash Icon */}
          {availableQty > 0 &&
            !multipack &&
            quantity === 1 &&
            this.showTrashBtn()}
          {availableQty > 0 &&
            multipack &&
            quantity <= 1 &&
            this.showTrashBtn()}

          {/* Input Box */}
          {availableQty > 0 &&
            !multipack &&
            quantity > 0 &&
            this.showInputBox()}

          {availableQty > 0 && multipack && this.showInputBox()}

          {/* Dialog for delete confirmation */}
          <DialogBox
            dialogClose={() =>
              this.setState({ showDeletPrompt: false, deleteClicked: false })
            }
            dialogOpen={this.state.showDeletPrompt}
            dialogHeading={deleteMsg}
            dialogWarning
            dialogCloseConfirm={() => {
              this.deleteCart(productInfo.productId, cartItems);
            }}
            ariaLabelYes={
              this.state.deleteClicked
                ? "Yes I want to remove the product"
                : "Yes I want to empty my cart"
            }
            ariaLabelCancel={
              this.state.deleteClicked
                ? "No I don't want to remove the product"
                : "No I don't want to empty my cart"
            }
          />

          {availableQty > 0 && (
            <Button
              aria-label={
                sizeName
                  ? `on clicking on this button ${sizeName} Product Quantity will be incremented by 1`
                  : "on clicking on this button Product Quantity will be incremented by 1"
              }
              onClick={this.increment}
              disabled={
                disableIncrement ||
                (multipackLimit && multipackLimit === packCount)
              }
            >
              <i className="fas fa-plus"></i>
            </Button>
          )}
        </div>
      </div>
    );
  }
}
