import React, { Component } from "react";
import { Button, Typography } from "@material-ui/core";
import {
  ViewedPage,
  ClickedButton,
} from "../../config/amplitude/SnacksAmplitude";
import {
  PAGENAME,
  BUTTONNAME,
} from "../../config/amplitude/Taxonomy.constants";

class PageNotFound extends Component {
  componentDidMount() {
    ViewedPage(PAGENAME.PAGENOTFOUND);
  }

  render() {
    return (
      <div className="common-box">
        <div className="common-box-header">
          <Typography variant="h2">404 Page</Typography>
        </div>
        <div className="common-box-body">
          <h4>Something went wrong on server, please try again</h4>
        </div>
        <div className="button-group text-right">
          <Button
            type="button"
            variant="contained"
            color="primary"
            className="btn-mui btn-blue"
            id="submitContact"
            onClick={() => {
              ClickedButton(BUTTONNAME.RELOAD, PAGENAME.PAGENOTFOUND);
              window.location.reload();
            }}
            aria-label="submit Contact"
          >
            Reload
          </Button>
        </div>
      </div>
    );
  }
}

export default PageNotFound;
