import React, { Component } from "react";
import { connect } from "react-redux";
import Spinner from "../../Spinner";
import moment from "moment";
import {
  Button,
  FormControl,
  Grid,
  InputLabel,
  Typography,
  Select,
} from "@material-ui/core";
import { Link } from "react-router-dom";
import { fetchOrderDetails } from "../../../actions/ProductAction";
import ProductImages from "./ProductImages";
import { trackEvent, EventNames } from "../../../appinsights/EventTrack";
import APIUtil from "../../../config/APIUtil";
import UrlConstants from "../../../config/UrlConstants";
import {
  ClickedButton,
  SelectedLinkItem,
} from "../../../config/amplitude/SnacksAmplitude";
import {
  METHOD,
  BUTTONNAME,
  PAGENAME,
} from "../../../config/amplitude/Taxonomy.constants";
import { DisplayHeading } from "../../../config/constants/content.constant";

class OrderHistory extends Component {
  state = {
    orderHistoryDetails: [],
    spinner: false,
    showItemInfo: -1,
    isShowItemInfo: false,
    orderImages: [],
    cartContent: "",
    filterBy: "7",
    selectedOrderId: 0,
    selectedOrder: sessionStorage.getItem("selectedOrder"),
    multiPackConfig: {},
    locationNumber:
      sessionStorage.getItem("_lo_No") || localStorage.getItem("_lo_No"),
    offset: 0,
    limit: UrlConstants.productsLimit,
  };

  componentDidMount() {
    let filterValue = this.state.filterBy;
    this.setState({ spinner: true });
    this.props
      .fetchOrderDetails(filterValue)
      .then((res) => {
        this.setState({ spinner: false });
      })
      .catch((err) => {
        this.setState({ spinner: false });
      });
    this.getConfig();
  }

  getConfig = () => {
    const { offset, limit, locationNumber } = this.state;
    const payload = { offset, limit, locationNumber };

    APIUtil.postMethod(UrlConstants.multipackConfig, payload, true)
      .then((response) => {
        this.setState({
          multiPackConfig: (response.data.data && response.data.data[0]) || {},
        });
      })
      .catch((error) => console.error("Multipack Config API", error));
  };

  UNSAFE_componentWillReceiveProps(newProps) {
    if (
      newProps.orderHistory &&
      newProps.orderHistory.length > 0 &&
      localStorage.getItem("accessToken")
    ) {
      this.setState({
        orderHistoryDetails: newProps.orderHistory,
        spinner: false,
      });
    } else {
      this.setState({ spinner: false });
    }
  }

  showItemDetail = (index) => {
    ClickedButton(BUTTONNAME.ORDERHISTORYDETAIL, METHOD.ORDERHISTORY, {
      orderId: this.state.orderHistoryDetails[index].orderId,
    });
    this.setState(
      {
        isShowItemInfo:
          index != this.state.showItemInfo ? true : !this.state.isShowItemInfo,
      },
      () =>
        this.setState({
          showItemInfo: index,
          // orderImages:order.items
        })
    );
  };

  handleOrderFIlter(value) {
    trackEvent(
      EventNames.Action.ACTION_ORDER_HISTORY_FILTER,
      EventNames.Event.EVENT_BUTTON_PRESS,
      window.location.origin,
      window.location.pathname,
      { filterBy: value }
    );
    SelectedLinkItem(value, METHOD.ORDERHISTORY);
    this.setState({ filterBy: value, spinner: true });
    this.props
      .fetchOrderDetails(value)
      .then((res) => {
        this.setState({ spinner: false });
      })
      .catch((err) => {
        this.setState({ spinner: false });
      });
  }

  render() {
    return (
      <div className="profile-info order-history profile-order-summery">
        {this.state.spinner && (
          <div>
            <Spinner />
          </div>
        )}

        <Grid container spacing={6} justify="space-between">
          {/* Biling Address */}
          <Grid item xs={12} sm={12}>
            <Grid
              container
              spacing={2}
              alignItems="center"
              className="title-mini pro-order-title"
            >
              <Grid item xs={12} sm={3}>
                <Typography variant="h2" component="h2">
                  {this.state.orderHistoryDetails.length} Orders Placed in the
                  past
                </Typography>
              </Grid>
              <Grid
                item
                xs={12}
                sm={4}
                className="text-right profile-pro-filter"
              >
                <FormControl
                  fullWidth
                  className="form-group phone-select phone-select-full"
                  variant="outlined"
                >
                  <InputLabel className="form-label" htmlFor="filter">
                    Filter
                  </InputLabel>
                  <Select
                    native
                    id="filter"
                    name="filter"
                    onChange={(e) => this.handleOrderFIlter(e.target.value)}
                  >
                    <option value="7">7 days</option>
                    <option value="30">30 days</option>
                    <option value="all">All Results</option>
                  </Select>
                </FormControl>
              </Grid>
            </Grid>
            <div className="order-history-accord">
              <div className="acc-heading">
                <ul>
                  <li className="date">Date Ordered</li>
                  <li className="order-number">Order</li>
                  <li className="trck-no">Tracking</li>
                  <li className="total">Total</li>
                  <li className="ord-detail">Order Details</li>
                </ul>
              </div>
              {this.state.orderHistoryDetails.length > 0 ? (
                this.state.orderHistoryDetails.map((order, index) => (
                  <div
                    key={index}
                    className={
                      this.state.showItemInfo == index &&
                      this.state.isShowItemInfo
                        ? "acc-body show-div"
                        : "acc-body"
                    }
                  >
                    <ul>
                      <li className="date">
                        <span className="mob-title">Date Ordered</span>
                        <span>
                          {moment(order.orderDate).format("MM/DD/YY")}
                        </span>
                      </li>
                      <li className="order-number">
                        <span className="mob-title">Order</span>
                        <span>{order.orderId}</span>
                      </li>
                      <li className="trck-no color-title">
                        <span className="mob-title">Tracking</span>
                        {order.trackingId && order.trackingId.length > 0 ? (
                          <a
                            className={"highlighed"}
                            target="blank"
                            href={`${"https://www.ups.com/track?loc=en_US&tracknum="} ${
                              order.trackingId
                            } ${"&requester=WT/trackdetails"}`}
                            aria-label={`${"Tracking Id"} ${order.trackingId}`}
                            tabIndex={0}
                          >
                            {order.trackingId}
                          </a>
                        ) : (
                          <Button disabled className="color-title">
                            No Tracking Id
                          </Button>
                        )}
                      </li>
                      <li className="total">
                        <span className="mob-title">Total</span>
                        <span>${order.totalPrice.toFixed(2)}</span>
                      </li>
                      <li className="ord-detail color-title">
                        <Button
                          aria-label={`${"click here to see order detail-"}${index}`}
                          id="OrderDetails"
                          onClick={() => this.showItemDetail(index)}
                        >
                          Order Details
                        </Button>
                      </li>
                    </ul>
                    {this.state.showItemInfo == index &&
                    this.state.isShowItemInfo ? (
                      <div
                        className={
                          this.state.isShowItemInfo
                            ? "ordered-cart open"
                            : "ordered-cart"
                        }
                      >
                        <ProductImages
                          orderImages={order}
                          multiPackConfig={this.state.multiPackConfig}
                        />
                      </div>
                    ) : (
                      ""
                    )}
                  </div>
                ))
              ) : (
                <div className="no-items">No Order History Available</div>
              )}
            </div>

            <div className="order-history-action">
              <Link
                className="btn-continue-shopping btn-shopping"
                id="ContinueShopping"
                aria-label="Continue Shopping"
                to="/productlist"
                onClick={ClickedButton(
                  BUTTONNAME.CONTINUESHOPPING,
                  PAGENAME.ORDERHISTORY
                )}
              >
                {DisplayHeading.CONTINUE_SHOPPING}
              </Link>
            </div>
          </Grid>
        </Grid>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    orderHistory: state.reducer.orderHistory,
  };
};

export default connect(mapStateToProps, { fetchOrderDetails })(OrderHistory);
