import React, { Component } from "react";
import { Button, Grid, CircularProgress } from "@material-ui/core";
import SectionHeading from "../../commonComponents/sectionHeading/SectionHeading";
import SnacksAlertMessage from "./../../commonComponents/snacksAlertMessage/SnacksAlertMessage";
import CategoryTout from "./CategoryTout";
import UrlConstants from "../../../config/UrlConstants";
import APIUtil from "../../../config/APIUtil";
import { DisplayHeading } from "./../../../config/constants/content.constant";
import { ViewedPage } from "../../../config/amplitude/SnacksAmplitude";
import { PAGENAME } from "../../../config/amplitude/Taxonomy.constants";
import "./shopByCategory.scss";

export default class ShopByCategory extends Component {
  state = {
    currentStep: 0,
    categoryList: [],
    selectedCategory: [],
    countCategory: 0,
    countSubCategory: 0,
    errorMessage: "",
    subCategoryList: [],
    isCategoryLoading: true,
    lastSelectedCategory: [],
  };

  componentDidMount() {
    ViewedPage(PAGENAME.SHOPBYCATEGORY);
    this.shopByCategoryApiCall();
  }

  shopByCategoryApiCall = () => {
    const payload = {
      locationNumber: sessionStorage.getItem("_lo_No"),
    };
    APIUtil.postMethod(UrlConstants.ShopByCategory, payload, true)
      .then((response) => {
        this.setState({
          categoryList: response.data.data || [],
          isCategoryLoading: false,
        });
      })
      .catch((error) => {
        console.error("shop By Category Api", error);
      });
  };

  retainLastSelectedCategory = (item) => {
    const { lastSelectedCategory } = this.state;

    const objIndex = lastSelectedCategory.findIndex(
      (obj) => obj === item.categoryId
    );

    if (objIndex === -1) {
      this.setState({
        lastSelectedCategory: [...lastSelectedCategory, item.categoryId],
      });
    } else {
      const filteredArray = lastSelectedCategory.filter(
        (item) => item !== lastSelectedCategory[objIndex]
      );
      this.setState({
        lastSelectedCategory: filteredArray,
      });
    }
  };

  selectCategory = (category, subCategory) => {
    const { categoryId } = category;
    const { selectedCategory } = this.state;

    category && !subCategory && this.retainLastSelectedCategory(category);

    const objIndex = selectedCategory.findIndex(
      (obj) => obj.categoryId === categoryId
    );

    if (!subCategory) {
      if (objIndex === -1) {
        const list = [...selectedCategory, { categoryId }];
        this.setState({
          selectedCategory: list,
          countCategory: list.length,
        });
      } else {
        const filteredArray = selectedCategory.filter(
          (item) => item.categoryId !== selectedCategory[objIndex].categoryId
        );
        this.setState({
          selectedCategory: filteredArray,
          countCategory: filteredArray.length,
        });
      }
    }

    if (subCategory) {
      let updatedObj = {};
      let objIndexSubCategory = -1,
        subCategoryCount = 0,
        updatedSelectedCategory = [];

      if (selectedCategory[objIndex].subCategoryIdList) {
        objIndexSubCategory = selectedCategory[
          objIndex
        ].subCategoryIdList.findIndex(
          (obj) => obj === subCategory.subCategoryId
        );

        if (objIndexSubCategory === -1) {
          updatedObj = {
            ...selectedCategory[objIndex],
            subCategoryIdList: [
              ...new Set([
                ...selectedCategory[objIndex].subCategoryIdList,
                subCategory.subCategoryId,
              ]),
            ],
          };
        } else {
          const newArray = selectedCategory[objIndex].subCategoryIdList.filter(
            (item) =>
              item !==
              selectedCategory[objIndex].subCategoryIdList[objIndexSubCategory]
          );
          updatedObj = {
            ...selectedCategory[objIndex],
            subCategoryIdList: selectedCategory[
              objIndex
            ].subCategoryIdList.filter((x) => newArray.includes(x)),
          };
        }
      } else {
        updatedObj = {
          ...selectedCategory[objIndex],
          subCategoryIdList: [subCategory.subCategoryId],
        };
      }

      updatedSelectedCategory = [
        ...selectedCategory.slice(0, objIndex),
        updatedObj,
        ...selectedCategory.slice(objIndex + 1),
      ];

      if (updatedSelectedCategory.length > 0) {
        updatedSelectedCategory.forEach((element) => {
          if (element.subCategoryIdList) {
            subCategoryCount =
              subCategoryCount + element.subCategoryIdList.length;
          }
        });
      }

      this.setState({
        selectedCategory: updatedSelectedCategory,
        countSubCategory: subCategoryCount,
      });
    }
  };

  nextClicked = () => {
    const {
      selectedCategory,
      currentStep,
      categoryList,
      countCategory,
      countSubCategory,
    } = this.state;
    let updatedCategoryList = [];
    if (selectedCategory.length === 0) {
      this.setState(
        {
          errorMessage: "Please select atleast 1 ##category##".replace(
            /##category##/g,
            `${currentStep === 0 ? "category" : "sub category"}`,
            document.getElementById("body").classList.add("has-toast")
          ),
        },
        () =>
          setTimeout(() => {
            this.setState({ errorMessage: "" });
            document.getElementById("body").classList.remove("has-toast");
          }, 3000)
      );
    }

    if (countCategory > 0 && currentStep === 0) {
      categoryList.filter((list) => {
        selectedCategory.forEach((element) => {
          if (list.categoryId === element.categoryId) {
            updatedCategoryList.push(list);
          }
        });
      });

      this.setState({
        currentStep: 1,
        //countCategory: 0,
        subCategoryList: updatedCategoryList,
      });
    }

    if (countSubCategory > 0 && currentStep === 1) {
      sessionStorage.setItem(
        "selectedCategory",
        JSON.stringify(this.state.selectedCategory)
      );
      this.props.history.push("/productlist");
    }
  };

  backClicked = () => {
    this.setState({
      currentStep: 0,
      countSubCategory: 0,
      //subCategoryList: [],
      //selectedCategory: [],
    });
  };

  render() {
    const {
      categoryList,
      currentStep,
      errorMessage,
      countCategory,
      countSubCategory,
      subCategoryList,
      isCategoryLoading,
      lastSelectedCategory,
    } = this.state;
    const totalCategory = categoryList.length;

    let totalSubCategory = 0;
    if (subCategoryList.length > 0) {
      subCategoryList.forEach((element) => {
        totalSubCategory = totalSubCategory + element.subCategories.length;
      });
    }

    return (
      <div className="shop-by-category-container">
        <div className="shop-by-category-page-heading sticky-head">
          <div className="sticky-head-inner">
            {currentStep === 0 && (
              <SectionHeading
                classField="shop-by-category"
                tagH1
                heading={DisplayHeading.SHOP_BY_CATEGORY_TITLE}
                subHeading={DisplayHeading.SHOP_BY_CATEGORY_SUB_TITLE}
              />
            )}

            {currentStep === 1 && (
              <SectionHeading
                classField="shop-by-category"
                tagH1
                heading={DisplayHeading.SHOP_BY_CATEGORY_SNACKING}
                subHeading={DisplayHeading.SHOP_BY_CATEGORY_SELECT_ITEM}
              />
            )}

            <div className="shop-by-category-next">
              {currentStep === 1 && (
                <Button
                  className="btn-secondary"
                  aria-label="back"
                  disabled={currentStep === 0}
                  onClick={this.backClicked}
                >
                  {DisplayHeading.BACK}
                </Button>
              )}
              <Button
                className="btn-primary"
                disabled={
                  (currentStep === 0 && countCategory === 0) ||
                  (currentStep === 1 && countSubCategory === 0)
                }
                onClick={this.nextClicked}
                aria-label="Next"
              >
                {currentStep === 0 && countCategory === 0
                  ? "MAKE A SELECTION"
                  : DisplayHeading.NEXT}
              </Button>
              {currentStep === 0 && (
                <span>{`${countCategory} of ${totalCategory} selected`}</span>
              )}
              {currentStep === 1 && (
                <span>{`${countSubCategory} of ${totalSubCategory} selected`}</span>
              )}
            </div>
          </div>
        </div>
        {isCategoryLoading && (
          <Grid container justify="center" style={{ minHeight: "100px" }}>
            <Grid item>
              <CircularProgress />
            </Grid>
          </Grid>
        )}
        <div className="shop-by-category-container-inner">
          {errorMessage && <SnacksAlertMessage fullWidth msg={errorMessage} />}
          <div className="shop-by-category-category">
            {currentStep === 0 && (
              <CategoryTout
                categoryList={categoryList}
                currentStep={currentStep}
                selectCategory={this.selectCategory}
                lastSelectedCategory={lastSelectedCategory}
              />
            )}
            {currentStep === 1 && (
              <CategoryTout
                categoryList={subCategoryList}
                currentStep={currentStep}
                selectCategory={this.selectCategory}
              />
            )}
          </div>
        </div>
      </div>
    );
  }
}
